define('views/public/share',[
  'jquery',
  'backbone',
  'libs/bootstrap'
], function(
  $,
  Backbone
) {

  return Backbone.View.extend({
    el: '#content',
    
    initialize: function() {
      this.sharedHours = 1000067; // Starting value of Owners shared hours counter

      this.activate();

    },

    activate: function() {
      var self = this;
      
      // --------
      // Popovers
      // --------
      $(document).click(function (e) {
        if (($('.popover').has(e.target).length === 0) || $(e.target).is('.close')) {
          $('.show-qualifications-button').popover('hide');
        }
      });

      this.$el.find('.show-qualifications-button').popover({
        content: function() { return $('.qualifications').html(); },
        html: true,
        placement: 'bottom'
      }).on('click', function(event) {
        event.stopPropagation();
      });

      // -------------------
      // Hours Shared Counter
      // -------------------
      this.$counter = this.$el.find('.shared-hours-counter');

      function incrementHours() {
        self.sharedHours += 1;
        jQuery.map((self.sharedHours.toString()).split(''), function(num, index) {
          self.$counter.find('.num-' + (index + 1)).text(num);
        });
      }

      (function loopIncrementHours() {
        // Update hours every 0.25sec to 3sec
        var rand = Math.round(Math.random() * (3000 - 250)) + 250;
        setTimeout(function(){
          incrementHours();
          loopIncrementHours();
        }, rand);
      })();
    }

  });

});

