/*
  Loader Utils
  ------------

  setButtonSubmitting($saveButton)
  - Disables the button and replaces the label with a spinner.
  - Use when clicking a button performs an AJAX request.

  clearButtonSubmitting()

  showPageLoader()
  - Dims screen and shows a spinner.
  - Use when performing an AJAX request that, on success, will result with the
    user being forwarded to a different screen.

  hidePageLoader()

  showStatusMessage('Locating car')
  - Shows a text message with animated ellipsis in a small white rounded
    rectangle.
  - Use to show textual status of AJAX request.    
  - Currently only used on Connect Key screen.
  
  hideStatusMessage()

  showErrorDialog('There was an error saving your changes.')
  - Shows modal with caution icon, error message, and 'Okay' button.
  - Use when there is an error performing an AJAX request.

  hideErrorDialog()

  showProgressDialog('Locking')
  - Shows modal with animated progress bar.
  - Currently only used on Connect Key screen.

  hideProgressDialog()

  // WARNING: blockUI() is incomplete and untested.
  blockUI('.my-form');
  unblockUI();

*/
define('utils/loader',[
  'jquery',
  'underscore',
  'libs/bootstrap',
  'libs/handlebars',
  'text!templates/shared/error_dialog.handlebars',
  'text!templates/shared/progress_dialog.handlebars',
  'text!templates/shared/status_message.handlebars'
], function(
  $,
  _,
  bootstrap,
  handlebars,
  errorDialogHandlebars,
  progressDialogHandlebars,
  statusMessageHandlebars
) {

  // Hide before loading new page.
  window.onunload = function() {
    hideAll();
  };

  // Disables the button to prevent extra clicks and replaces the button label 
  // with a spinner.
  var setButtonSubmitting = function(button) {
    var $button = $(button);

    // Adding a disabled attr to an anchor in IE8 prevents
    // normal link behavior.
    if ($button.prop('tagName') === 'BUTTON') {
      $button.attr('disabled', 'disabled');
    }
    $button
      .data('label', $button.html())
      .css('width', $button.outerWidth())
      .css('height', $button.outerHeight())
      .html('<div class="spinner spinner-light-bg-small button-spinner"></div>')
      .addClass('btn-submitting');
  };


  var clearButtonSubmitting = function(button) {
    var $button = $(button);

    $button
      .html($button.data('label'))
      .removeAttr("disabled")
      .removeClass("btn-submitting");
  };


  var showStatusMessage = function(message, options) {
    var defaults = {
      'message': 'Loading',
      'showEllipsis': true 
    };
    options = _.defaults(options || {}, defaults);

    var template = Handlebars.compile(statusMessageHandlebars);
    var html     = template(options);

    var $message = $(html).appendTo($('body'));
    $message.fadeIn(500);
    
    return $message;
  };


  var hideStatusMessage = function(el) {
    var $message = (el instanceof jQuery)? el: $('.loader-message');
    $message.fadeOut(500, function() {
      $(this).remove();
    });
  };


  /*
    Note: How to use page loader when url forwarding
    -------------------------------------------------
    If you want to show a page loader before forwarding a user to a new URL
    you will need to add a small delay before setting window.location.href. 
    This will give time for the spinner to be injected into the dom.
   
    Example:
    loader.showPageLoader();
    setTimeout(function() {
      window.location.href = checkoutURL;
    }, 50);
  */
  var showPageLoader = function() {
    $('body').prepend('<div class="page-loader"><div class="spinner spinner-dark-bg-large"></div><div class="modal-backdrop fade in"></div></div>');
  };


  var hidePageLoader = function() {
    $('.page-loader').remove();
  };


  var showErrorDialog = function(message, options) {
    var defaults = {
      'message': 'Sorry. There was an unexpected error. Please try again.'
    };
    options = _.defaults(options || {}, defaults);

    var template = Handlebars.compile(errorDialogHandlebars);
    var html     = template(options);

    var $dialog = $(html).appendTo($('body'));
    $dialog.modal();

    $dialog.on('hidden.bs.modal', function (event) {
      $(this)
        .data('bs.modal', null)
        .remove();
    });

    return $dialog;
  };


  var hideErrorDialog = function(el) {
    var $dialog = (el instanceof jQuery)? el: $('.error-dialog');
    if ($dialog.length > 0) {
      $dialog.modal('hide');
    }
  };


  var showProgressDialog = function(message, options) {
    var defaults = {
      'classes': '',        // Custom CSS classes to attach to the dialog     
      'duration': 0,        // If duration > 0, the dialog will auto hide at that time. In MS.
      'message' : 'Loading'
    };
    options = _.defaults(options || {}, defaults);

    var template = Handlebars.compile(progressDialogHandlebars);
    var html     = template(options);

    var $dialog = $(html).appendTo($('body'));
    $dialog.modal();

    $dialog.on('hidden.bs.modal', function (event) {
      $(this)
        .data('bs.modal', null)
        .remove();
    });

    if (options.duration > 0) {
      window.setTimeout(function() {
        hideProgressDialog();
      }, options.duration);
    }

    return $dialog;
  };


  var hideProgressDialog = function(el) {
    var $dialog = (el instanceof jQuery)? el: $('.progress-dialog');
    if ($dialog.length > 0) {
      $dialog.modal('hide');
    }
  };


  var blockUI = function(element, options) {
    var defaults = {
      'spinnerSize': 'medium' // Valid options: 'small' and 'medium'
    };

    var $element;

    options = $.extend({}, this.blockUIDefaults, options);
    
    if (element instanceof jQuery) {
      $element = element;
    } else if (_.isElement(element)) {
      $element = $(element);
    } else {
      $element = $('body');
    }

    $element.wrap('<div class="ui-blocker"></div>');
    
    var $UIBlocker = $element.parent();
    $UIBlocker.prepend('<div class="ui-blocker-overlay"><div class="ui-blocker-spinner spinner-' + options.spinnerSize + '"></div></div>');
  };


  var unblockUI = function() {
    $('.ui-blocker-overlay')
      .unwrap()
      .remove();
  };


  var hideAll = function() {
    hideErrorDialog();    
    hidePageLoader();
    hideProgressDialog();
    hideStatusMessage();
  };


  return {
    clearButtonSubmitting: clearButtonSubmitting,
    hideErrorDialog: hideErrorDialog,
    hidePageLoader: hidePageLoader,
    hideProgressDialog: hideProgressDialog,
    hideStatusMessage: hideStatusMessage,
    setButtonSubmitting: setButtonSubmitting,
    showErrorDialog: showErrorDialog,
    showPageLoader: showPageLoader,
    showStatusMessage: showStatusMessage,
    showProgressDialog: showProgressDialog
  };
});

