define('models/user',[
  'underscore',
  'backbone',
  'libs/jstz',
  'config',
  'facebook'
], function (_, Backbone, jstz, config, facebookNotAModule) {

  return Backbone.Model.extend({
    // Expected data:
    // - iana_timezone
    // - utype
    // - userId
    // - authToken

    initialize: function () {
      this.initFacebookEvents();
      this.set('utype', config.globals.logged_in_user_utype);
    },

    initFacebookEvents: function () {
      // Initialize facebook SDK
      FB.init({
        appId: config.globals.facebook_app_id,
        status: true, // check login status
        cookie: true, // enable cookies to allow the server to access the session
        xfbml: true  // parse XFBML
      });

      var self = this;

      function handleFBAuthResponseChange(response) {
        if (response.status === 'connected') {
          // The user is logged in.
          self.set('userId', response.authResponse.userID);
          self.set('accessToken', response.authResponse.accessToken);
          self.trigger('user:fbConnected')
        }
      }

      // Register facebook events
      // auth.authResponseChange indicates that the user's access token has changed in some way
      FB.Event.subscribe('auth.authResponseChange', handleFBAuthResponseChange);
    },

    // Overriding the fetch method
    fetch: function () {
      this.set('iana_timezone', jstz.determine().name());
    },

    login: function (options) {
      // encapsulate business logic about become_user here within user model login
      // become_user state means we assume authentication success
      if (config.globals.is_become_user !== true) {
        this.trigger('login:start');
        if (this.has('accessToken') && this.has('userId')) {
          // this.has('accessToken') guarantees the user's client-side access token (short-lived) is current
          // as of last request to webapp - we don't have to force another FB.login right now
          this.loginCallback(this.get('userId'), this.get('accessToken'), options);
        }
        else {
          var self = this;
          FB.login(function (response) {
            if (response.status === 'connected') {
              self.loginCallback(response.authResponse.userID, response.authResponse.accessToken, options);
            }
            else if (response.status === 'not_authorized') {
              // The user is logged in to facebook but has not authorized our app.
              self.trigger('login:fbNotAuthorized');
            }
            else {
              self.trigger('login:error');
            }
          }, {scope: config.globals.facebook_permissions});
        }
      }
      else {
        this.authenticationSuccessCallback(options);
      }
    },

    logout: function () {
      window.location.href = '/logout';
    },

    loginCallback: function (facebookId, accessToken, options) {
      options = options || {};

      // this POST extends the FB access token to make it a long-lived token, server-side
      $.ajax({
        type: 'POST',
        url: '/ajax/login',
        dataType: 'json',
        data: {
          access_token: accessToken,
          facebook_id: facebookId
        },
        context: this,
        success: function (data) {
          this.set('is_owner', data['is_owner']);
          this.set('utype', data['utype']);
          this.authenticationSuccessCallback(options);
        },
        error: function (jqXHR, textStatus, errorThrown) {
          this.trigger('login:error');
          console.log("/ajax/login error: " + textStatus);
        }
      });

    },

    isLoggedIn: function() {
      return this.get('utype') === 'active';
    },

    authenticationSuccessCallback: function (options) {
      if (this.get('utype') === 'ghost') {
        this.trigger('login:noGetaroundAccount', options);
      }
      else if (this.get('utype') === 'suspended') {
        this.trigger('login:suspended');
      }
      else if (options.redirectURL) {
        this.trigger('login:success', options);
      }
      else if (this.get('is_owner')) {
        this.trigger('login:success', {redirectURL: '/dashboard'});
      }
      else {
        this.trigger('login:success', {redirectURL: '/search'});
      }
    },

    callFacebook: function(api, callback) {
      var self = this;
      if (this.has('accessToken')) {
        FB.api(api, function(response) {
          callback(response);
        });
      }
      else {
        this.once('user:fbConnected', function () {
          self.callFacebook(api, callback);
        });
      }
    }
  });
});

