// owner_accept.js
// ----------------
// public  init()
// public  show()
// public  hide()
// private acceptRental()
// ---------------
//
// This dialog is displayed when the user follows an 'Accept rental' link from
// an email. This link to the owner_got_request page has a url parameter
// action=accept that pops up the dialog on page load.

define('common/partials/dialogs/owner_accept',[
  'jquery',
  'common',
  'webapp',
  'text!templates/owner_accept.handlebars',
  'libs/bootstrap',
  'libs/handlebars'
], function( $, common, webapp, dialogHandlebars ){

  var $element      = $(),
      dialogTemplate,
      requestData;


  var init = function( ){

    dialogTemplate = Handlebars.compile( dialogHandlebars );

  };


  // =================
  // show
  // =================
  var show = function( data ){

    requestData = data;
    requestData.renter = ( typeof requestData.renter === "undefined" )? "the renter": requestData.renter;

    // If modal already exists from a previous show call: remove, compile new
    // html and add to DOM, and display modal
    $('#owner-accept-dialog').remove();
    $('body').append( dialogTemplate( requestData ));

    $element = $('#owner-accept-dialog');
    $element.modal();

    //
    // Attach UI events
    //
    $element.on( 'click', '.cancel-btn', hide );
    $element.on( 'click', '.confirm-btn', acceptRental );

  };

  // =================
  // hide
  // =================
  var hide = function(){
    $element.modal( 'hide' );
  };


  // =================
  // acceptRental
  // =================
  var acceptRental = function( ){

    webapp.approve( requestData.bundleId, $element.find('.confirm-btn') );

  };


  return {

    init: init,

    show: show,

    hide: hide

  };
});
