define('models/calendar_event',[
  'backbone',
  'utils/converters',
  'utils/time'
], function(
  Backbone,
  converters,
  time
){

  return CalendarEvent = Backbone.Model.extend({
    default: {
      'type': null,
      'start': null,
      'end': null,
      'renter_name': null
    },

    getDurationInMS: function() {
      var startMS = converters.ISODateToJSDate(this.get('start')).getTime();
      var endMS   = converters.ISODateToJSDate(this.get('end')).getTime();
      return endMS - startMS;
    },

    // FullCalendar jQuery plugin support
    // http://arshaw.com/fullcalendar/
    getFullCalendarEvent: function() {
      return {
        'title': this.get('type').toLowerCase(),
        'type': this.get('type'),
        'className': this.get('type').toLowerCase(),
        'start': time.roundISODateToQuarterHour(this.get('start')),
        'end': time.roundISODateToQuarterHour(this.get('end')),
        'allDay': false
      };
    }
  });

});

