define('views/car/parking_inputs',[
  'jquery',
  'backbone'
], function(
  $,
  Backbone
) {

  var ParkingInputsView = Backbone.View.extend({
    events: {
      'change [name=parking-category]': 'handleCategoryChange',
      'change [name=parking-reserved]': 'handleReservedChange',
      'change [name=parking-special-access]': 'handleSpecialAccessChange',
      'change [name=parking-limited-access]': 'handleLimitedAccessChange'
    },
    initialize: function() {
      // HTML rendered in haml.
      this.populateModelFromHTMLInputs();
      this.activate();
    },
    activate: function() {
      var self = this;

      this.listenTo(this.model, 'change:parking-category', function(model) {
        self.resetForm();
        
        var category = model.get('parking-category');
        if (category === 'garage') {
          self.$el.find('.parking-reserved-control-group').slideDown();
          self.$el.find('.category-label').text('garage');
        } else if (category === 'surface_lot') {
          self.$el.find('.parking-reserved-control-group').slideDown();
          self.$el.find('.category-label').text('parking lot');
        } else if (category === 'driveway') {
          self.$el.find('.parking-reserved-control-group').slideDown();
        } else if (category === 'valet') {
          self.$el.find('.parking-limited-access-control-group').slideDown();
        }

        model.unset('parking-reserved', {silent: true});
        model.unset('parking-special-access', {silent: true});
        model.unset('parking-limited-access', {silent: true});
      });

      this.listenTo(this.model, 'change:parking-reserved', function(model) {
        self.$el.find('.parking-special-access-control-group').slideDown();
      });

      this.listenTo(this.model, 'change:parking-special-access', function(model) {
        if (model.get('parking-category') === 'garage' ||
            model.get('parking-category') === 'surface_lot') {
          self.$el.find('.parking-limited-access-control-group').slideDown();
        }
      });

    },
    handleCategoryChange: function(event) {
      var category = $(event.target).val();
      this.model.set('parking-category', category);
    },
    handleReservedChange: function(event) {
      var isReserved = $(event.target).val();
      this.model.set('parking-reserved', isReserved);
    },
    handleSpecialAccessChange: function(event) {
      var hasSpecialAccess = $(event.target).val();
      this.model.set('parking-special-access', hasSpecialAccess);
    },
    handleLimitedAccessChange: function(event) {
      var hasLimitedAccess = $(event.target).val();
      this.model.set('parking-limited-access', hasLimitedAccess);
    },
    populateModelFromHTMLInputs: function() {
      this.model.set('parking-category', this.$el.find('[name=parking-category]').val());
      this.model.set('parking-reserved', this.$el.find('[name=parking-reserved]:checked').val());
      this.model.set('parking-special-access', this.$el.find('[name=parking-special-access]:checked').val());
      this.model.set('parking-limited-access', this.$el.find('[name=parking-limited-access]:checked').val());
    },
    // Hide all the form elements except for the top 'How is your car parked?' dropdown. Uncheck
    // any radio buttons that were checked.
    resetForm: function() {
      this.$el.find('.parking-inputs .control-group:not(.parking-category-control-group)').hide();
      this.$el.find('input:radio').prop('checked', false);

    }
  });

  return ParkingInputsView;
});

