define('trips/renter_made_request',[
  "jquery",
  "common",
  "libs/bootstrap",
  "common/partials/countdown",
  "common/partials/messaging",
  'utils/loader'
], function( $, common, bootstrap, countdown, messaging, loader ) {

  common.pageReady("trips-renter_made_request", function(){


    // ------------------
    // Init components
    // ------------------
    if ( GA_ARGS.seconds_till_expire > 0 ){
      countdown.init( '.countdown', GA_ARGS.seconds_till_expire );
    }
    messaging.init( '.messaging');


    // ------------------
    // Attach UI events
    // ------------------
    $(".cancelRequestButton").on( 'click', function( event ) {
      $("#confirmCancelRequestDialog").modal();
    });

    $(".confirmCancelRequestButton").on( 'click', function( event ){
      $button = $(this);
      loader.setButtonSubmitting($button);

      $.post(
        "/ajax/cancel",
        {"bundle_id": GA_ARGS.bundle_id},
        function() {
          window.location.reload();
        }
      );
    });

    // Layout fix.
    // The car profiles and messaging two column layout uses absolutely
    // positioning for the left column. If the left column is taller than the
    // right column it will flow outside of the parent div.
    function resizeUI(){
      var leftColHeight  = $('.cars-owners-profiles').height(),
          rightColHeight = $('.messaging').height();

      newHeight = (leftColHeight > rightColHeight) ? leftColHeight : rightColHeight;

      $('.profiles-and-messaging').css( "min-height", newHeight + "px" );
    }

    resizeUI();


  });

});
