define('views/car/car_location_map_card',[
  'jquery',
  'backbone',
  'text!templates/car/car_location_map_card.handlebars',
  'libs/handlebars',
  'utils/template_helpers'
], function(
  $,
  Backbone,
  carLocationMapCardHandlebars,
  HandlebarsModule,
  templateHelpers
){

  return CarLocationMapCardView = Backbone.View.extend({
  
    template: Handlebars.compile(carLocationMapCardHandlebars),

    initialize: function() {
      this.recentLocation = this.options.recentLocation;

      this.render();
    },

    render: function() {
      var staleClass = (this.recentLocation.minutesSinceLocated >= 5) ? 'stale': '';

      this.$el.html(this.template({
        car: this.model.toJSON(),
        recentLocation: this.recentLocation,
        staleClass: staleClass
      }));

      return this;
    }
  });

});

