define('views/car/owner_key_active_rental',[
  'jquery',
  'backbone',
  'common',
  'text!templates/car/owner_key_active_rental.handlebars',
  'utils/template_helpers'
], function(
  $,
  Backbone,
  common,
  OwnerKeyActiveRentalHandlebars,
  templateHelpers
){

  return OwnerKeyActiveRentalView = Backbone.View.extend({
    template: Handlebars.compile(OwnerKeyActiveRentalHandlebars),

    initialize: function() {
      this.model.set({
        'renter.primary_photo': common.fixImageUrl(this.model.get('renter.primary_photo'), 150, 150)
      });

      this.render();
    },

    render: function() {
      this.$el.html(this.template(this.model.toJSON()));
    }

  });

});
