define('views/car/rent_button',[
  'jquery',
  'backbone',
  'config',
  'utils/converters',
  'text!templates/car/rent_button.handlebars',
  'libs/bootstrap',
  'libs/handlebars'
], function(
  $,
  Backbone,
  config,
  converters,
  rentButtonHandlebars,
  bootstrap
){

  var RentButtonView = Backbone.View.extend({
    template: Handlebars.compile(rentButtonHandlebars),
    events: {
      'click .btn-request': 'handleRequestAndRentButtonClicks',
      'click .btn-instant': 'handleRequestAndRentButtonClicks'
    },
    initialize: function() {
      this.car = this.options.car;

      this.render();
      this.activate();
    },
    render: function() {
      var showRentButton            = false;
      var showDisabledRentButton    = false;
      var showRequestButton         = false;
      var showDisabledRequestButton = false;
      var showUninsurableMsg        = false;
      var showADRMsg                = false;
      var showUnavailableMsg        = false;
      var showMinimumDurationMsg    = false;
      var showManualTransmissionMsg = false;
      var showUnder25Msg            = false;
      var showUnder19Msg            = false;

      var status = this.model.get("carAvailabilityStatus");
      if (status === "available") {
        if (this.car.is_instant) {
          showRentButton = true;
        } else {
          showRequestButton = true;
        }
      } else if (status === "uninsurable") {
        showUninsurableMsg = true;
      } else if (status === "adr_rejected") {
        showADRMsg = true;
      } else if (status === "unavailable") {
        showUnavailableMsg = true;
      } else if (status === "duration_invalid") {
        showMinimumDurationMsg = true;
      } else if (status === "under_25") {
        showUnder25Msg = true;
      } else if (status === "under_19") {
        showUnder19Msg = true;
      } else {
        if (this.car.is_instant) {
          showDisabledRentButton = true;
        } else {
          showDisabledRequestButton = true;
        }
      }

      this.$el.html(this.template({
        'showRentButton': showRentButton,
        'showDisabledRentButton': showDisabledRentButton,
        'showRequestButton': showRequestButton,
        'showDisabledRequestButton': showDisabledRequestButton,
        'showUninsurableMsg': showUninsurableMsg,
        'showADRMsg': showADRMsg,
        'showUnavailableMsg': showUnavailableMsg,
        'showMinimumDurationMsg': showMinimumDurationMsg,
        'showManualTransmissionMsg': showManualTransmissionMsg,
        'showUnder25Msg': showUnder25Msg,
        'showUnder19Msg': showUnder19Msg,
        'minimumDuration': this.car.minimum_duration
      }));

      this.$el.find('.disabled-rent-button, .disabled-request-button').tooltip({
        placement: 'top'
      });

      return this;
    },
    activate: function() {
      var self = this;

      this.listenTo(this.model, 'change:carAvailabilityStatus', function(model, carAvailabilityStatus) {
        self.render();
      });
    },
    handleRequestAndRentButtonClicks: function(event) {
      if (this.doesCarRequireShowingRulesDialog()) {
        this.$el.trigger('SHOW_RULES_DIALOG');
      } else {
        this.$el.trigger('FORWARD_USER_TO_CHECKOUT');
      }
    },
    doesCarRequireShowingRulesDialog: function() {
      if (this.car.transmission === 'manual' ||
          this.car.fuel_type === 'electric' ||
          this.car.fuel_type === 'premium' ||
          this.car.fuel_type === 'diesel' ||
          this.car.pet_friendly) {
        return true;
      }
      return false;
    }
  });

  return RentButtonView;
});

