define('models/viewmodels/car_profile_ui_state',[
  'backbone',
  'models/viewmodels/search_cookie'
], function(Backbone, SearchCookie){


  var CarProfileUIState = Backbone.Model.extend({
    // Expected data:
    // - carAvailabilityStatus
    // - pickup_date
    // - return_date
    initialize: function() {
      this._readCookie();
      this._readURL();
    },
    _readCookie: function() {
      this.searchCookie = new SearchCookie();
      this.set(this.searchCookie.toJSON());
    },
    _readURL: function() {

    },
    updateCookie: function() {
      this.searchCookie.updateCookieValues(this.toJSON());
    }
  });

  return CarProfileUIState;
});

