define('views/car/car_edit_photos',[
  'jquery',
  'backbone',
  'libs/jquery.form',
  'utils/loader',
  'views/car/car_photo_uploader'
], function(
  $,
  Backbone,
  jQueryForm,
  loader,
  CarPhotoUploaderView
){

  return CarEditPhotosView = Backbone.View.extend({
  
    initialize: function() {
      this.render();
      this.activate();
    },

    render: function() {
      this.carPhotoUploaderView = new CarPhotoUploaderView({
        el: '.edit-car-photo-uploader'
      });
      
      return this;
    },

    activate: function() {
      var self = this;
      this.carPhotoUploaderView.on('photos:reorder', function() {
        self.submitForm();
      });
    },

    // When a user changes the order of the photos we need to submit the new
    // order before it is saved.
    submitForm: function() {
      this.$el.find('.photos-form').ajaxSubmit().data('jqxhr')
        .done(function(data, status, xhr) {
          if (data !== 'OK') {
            loader.showErrorDialog('There was an error savings your changes. Please reload the page to try again.');
          }
        })
        .fail(function(jqXHR, textStatus, errorThrown) {
          loader.showErrorDialog('There was an error savings your changes. Please reload the page to try again.');
        });      
    }
    
  });

});

