define('views/public/find_a_car',[
  'jquery',
  'backbone',
  'models/viewmodels/places_autocomplete',
  'utils/loader',
  'views/shared/places_autocomplete_input',  
  'libs/bootstrap'
], function(
  $,
  Backbone,
  PlacesAutocomplete,
  loader,
  PlacesAutocompleteInputView,
  bootstrap
) {

  return Backbone.View.extend({
    el: '#content',
    
    events: {
      'submit .find-a-car-form': 'handleFormSubmission'
    },

    initialize: function() {
      // Setup models and collections
      this.placesAutocomplete = new PlacesAutocomplete();

      this.render();
      this.activate();
    },

    render: function() {
      // Subviews
      this.placesAutocompleteView = new PlacesAutocompleteInputView({
        el: '#find-a-car-places-autocomplete-input',
        model: this.placesAutocomplete,
        showLocateIcon: false
      });
    },

    activate: function() {
      this.$el.find('.fb-login-button').popover();
    },

    handleFormSubmission: function(event) {
      loader.setButtonSubmitting(this.$el.find('.find-cars-button'));
    }
  });

});
