define('common/partials/rate_user',[
  'jquery',
  'utils/loader'
], function(
  $,
  loader
){

  var $element = $();
  var $rateButton;

  // =================
  // init
  // =================
  var init = function(element) {

    $element = $(element);
    $rateButton = $element.find('.rate-button');

    // When rating widget is on screen, we need to hide the normal message
    // compose form.
    $('.messaging .compose-message').hide();

    $element.on('focus', '.comment-textarea', function(event){
      $(this).height(80);
    });

    $element.on('keyup change', '.comment-textarea', _validateForm);
    $element.on('click', '.star', _validateForm );
    $element.on('click', '.rate-button', _submitForm);
  };

  var _submitForm = function(){
    var args = {
      "rental_id": GA_ARGS.rentalid,
      "rating":    $element.find('#rating').val(),
      "feedback":  $element.find('.comment-textarea').val()
    };
    
    loader.setButtonSubmitting($rateButton);

    $.post("/ajax/submit-rating", args, function(data) {
      if (data === "OK") {
        $('.rate-user').slideUp(1000);
        $('.messaging .compose-message').slideDown(1000);
      } else {
        loader.clearButtonSubmitting($rateButton);
      }
   });

  };

  var _validateForm = function(){

    if( $element.find('.comment-textarea').val() === "" || $element.find('#rating').val() === "" ){
       $element.find('.rate-button').attr( 'disabled', 'disabled' );
    } else {
      $element.find('.rate-button').removeAttr( 'disabled' );
    }

  };

  return {

    init: init

  };

});

