define('views/car/car_edit_pricing',[
  'jquery',
  'backbone',
  'config',
  'utils/loader',
  'views/car/car_price_control'
], function(
  $,
  Backbone,
  config,
  loader,
  CarPriceControlView
){

  return CarEditPricingView = Backbone.View.extend({
  
    initialize: function() {
      this.render();
      this.activate();
    },

    render: function() {
      this.carPriceControlView = new CarPriceControlView({
        el: '#edit-car-price-control',
        model: this.model
      });
      
      return this;
    },

    activate: function() {
      this.listenTo(this.model, 'change', function(model) {
        $.ajax({
            type: 'POST',
            url: config.ADJUST_CAR_RATES_URL,
            data: model.toJSON(),
            dataType: 'json'
          })
          .fail(function(jqXHR, textStatus, errorThrown) {
            loader.showErrorDialog('There was an error savings your changes. Please reload the page to try again.');
          });
      });
    }
    
  });

});

