define('views/public/list',[
  'jquery',
  'backbone',
  'config',
  'common/validators',  
  'models/viewmodels/places_autocomplete',
  'utils/maps',
  'utils/loader',
  'views/shared/make_model_inputs',  
  'views/shared/places_autocomplete_input',  
  'libs/jquery.form',
  'libs/jquery.validate'
], function(
  $,
  Backbone,
  config,
  validators,
  PlacesAutocomplete,
  maps,
  loader,
  MakeModelInputsView,
  PlacesAutocompleteInputView,
  jQueryForm,
  jQueryValidate
) {

  return Backbone.View.extend({
    el: '#content',
    
    events: {
      'change .source-input': 'handleSourceChange'
    },

    initialize: function() {
      // Setup models and collections
      this.placesAutocomplete = new PlacesAutocomplete();

      this.render();
      this.activate();
    },

    render: function() {
      // Subviews
      this.placesAutocompleteView = new PlacesAutocompleteInputView({
        el: '#list-places-autocomplete-input',
        model: this.placesAutocomplete,
        showLocateIcon: false
      });

      // MakeModelInputs content rendered in HAML partial.
      this.makeModelInputsView = new MakeModelInputsView();

      // Cache jQuery objects
      this.$form = this.$el.find('.list-lead-form');
    },

    activate: function() {
      var self = this;

      // Car form validation
      this.$form.validate({
        rules: {
          make: {
            required: true
          },
          year: {
            required: true,
            carYear: true
          },
          model: {
            required: true
          },
          mileage: {
            required: true,
            carMileage: true
          },
          location: {
            required: true
          },
          weekly_trips: {
            required: true
          },
          firstName: {
            required: true
          },
          lastName: {
            required: true
          },
          email: {
            required: true,
            email: true
          },
          phone: {
            required: true,
            phone: true
          },
          source: {
            required: true
          }          
        },
        submitHandler: function(form, event) {
          event.preventDefault();
          loader.setButtonSubmitting(self.$el.find('.start-listing-button'));

          var make        = self.$form.find('[name=make]').val();
          var model       = self.$form.find('[name=model]').val();
          var year        = self.$form.find('[name=year]').val();
          var mileage     = self.$form.find('[name=mileage]').val();
          
          // TODO: make sure location is valid
          var carAddress  = maps.getStructuredAddressFromGoogleMapsPlace(self.placesAutocomplete.get('location'));
          var weeklyTrips = self.$form.find('[name=weekly_trips]').val();
          var firstName   = self.$form.find('[name=firstName]').val();
          var lastName    = self.$form.find('[name=lastName]').val();
          var email       = self.$form.find('[name=email]').val();
          var phone       = self.$form.find('[name=phone]').val();
          var source      = self.$form.find('[name=source]').val();
          // TODO: account for 'Other' source input
          
          return $.ajax({
            url: config.GENERATE_LEAD_URL,
            type: 'POST',
            dataType: 'json',
            data: {
              'make': make,
              'model': model,
              'year': year,
              'mileage': mileage,
              'street_address': carAddress.street_address,
              'city': carAddress.city,
              'state': carAddress.state,
              'region': carAddress.region,
              'zipcode': carAddress.zipcode,
              'country': carAddress.country,
              'weekly_trips': weeklyTrips,
              'first_name': firstName,
              'last_name': lastName,
              'email': email,
              'phone': phone,
              'source': source
            }})
            .done(function (data, textStatus, jqXHR) {
              // Google Tag Manager event
              dataLayer.push({'event': 'listcarLeadSuccess'});
              // We only want to send 'LeadGenViaBannerSuccess' if we came from
              // the homepage with the list car banner
              if (window.document.referrer.indexOf("show-banner=true") >= 0) {
                dataLayer.push({'event': 'LeadGenViaBannerSuccess'});
              }

              self.toggleThankYourPage();
            })
            .fail(function(jqXHR, textStatus, errorThrown) {
              loader.showErrorDialog('There was an error submitting your information. Please try again.');
              
              loader.clearButtonSubmitting(self.$el.find('.start-listing-button'));
            });
        }
      });

      // Preload image
      new Image().src = '/img/public/list/bg_mt_tam.jpg';
    },

    handleSourceChange: function(event){
      if ($(event.target).val() === 'Other') {
        this.$form.find('.source-other-input').show().focus();
      } else {
        this.$form.find('.source-other-input').hide();
      }
    },

    toggleThankYourPage: function() {
      this.$el.find('.page-wrapper').toggleClass('show-list-lead-form-page show-list-thank-you-page');
      window.scroll(0,0);
    }


  });

});
