define('views/car/pickup_instructions_input',[
  'jquery',
  'backbone'
], function(
  $,
  Backbone
) {

  var PickupInstructionsInputView = Backbone.View.extend({
    initialize: function() {
      // HTML rendered in haml.
      this.activate();
    },
    activate: function() {
      var self = this;

      this.listenTo(this.model, 'change:parking-category', function(model) {
        self.resetMessages();
        if (model.get('parking-category') === 'street') {
          self.showMessage('street');
        } else if (model.get('parking-category') === 'garage') {
          self.$el.find('.category-label').text('garage');
        } else if (model.get('parking-category') === 'surface_lot') {
          self.$el.find('.category-label').text('parking lot');
        } else if (model.get('parking-category') === 'driveway') {
          self.$el.find('.category-label').text('driveway');
        } else if (model.get('parking-category') === 'valet') {
          self.showMessage('valet');
          self.$el.find('.category-label').text('valet');
        }

      });

      this.listenTo(this.model, 'change:parking-reserved', function(model) {
        if (model.get('parking-reserved') === 'true') {
          self.showMessage('reserved');
        } else {
          self.hideMessage('reserved');
        }
      });

      this.listenTo(this.model, 'change:parking-special-access', function(model) {
        if (model.get('parking-special-access') === 'true') {
          self.showMessage('access-how');
        } else {
          self.hideMessage('access-how');
        }
      });

      this.listenTo(this.model, 'change:parking-limited-access', function(model) {
        if (model.get('parking-limited-access') === 'true') {
          self.showMessage('access-when');
        } else {
          self.hideMessage('access-when');
        }
      });

    },
    showMessage: function(message) {
      var self = this;
      this.$el.find('.' + message  + '-msg').slideDown();
      self.toggleListHeading();
    },
    hideMessage: function(message) {
      var self = this;
      this.$el.find('.' + message  + '-msg').slideUp('slow', function() {
        self.toggleListHeading();
      });
      
    },
    toggleListHeading: function (){
      // If at least one pickup help tip is in the list we show the list heading 
      if (this.$el.find('.pickup-msg:visible').length > 0) {
        this.$el.find('.pickup-msg-lead').slideDown();
      } else {
        this.$el.find('.pickup-msg-lead').slideUp();
      }
    },
    resetMessages: function() {
      this.$el.find('.pickup-msg-lead, .pickup-msg').hide();
    }
  });

  return PickupInstructionsInputView;
});

