/* Common utilities */

define('common',[
  'jquery',
  'underscore',
  'libs/jquery.form',
  'utils/loader'
  ], function($, _, jQueryForm, loader) {

  if (!Array.prototype.forEach) {
    Array.prototype.forEach = function(fun /*, thisp*/) {
      var len = this.length >>> 0;
      if (typeof fun != "function")
        throw new TypeError();

      var thisp = arguments[1];
      for (var i = 0; i < len; i++)
      {
        if (i in this)
          fun.call(thisp, this[i], i, this);
      }
    };
  }

  if (!Array.prototype.indexOf)
  {
    Array.prototype.indexOf = function(elt /*, from*/)
    {
      var len = this.length >>> 0;

      var from = Number(arguments[1]) || 0;
      from = (from < 0)
        ? Math.ceil(from)
        : Math.floor(from);
      if (from < 0)
        from += len;

      for (; from < len; from++)
      {
        if (from in this &&
            this[from] === elt)
          return from;
      }
      return -1;
    };
  }

  if (!('map' in Array.prototype)) {
    Array.prototype.map= function(mapper, that /*opt*/) {
      var other= new Array(this.length);
      for (var i= 0, n= this.length; i<n; i++)
        if (i in this)
          other[i]= mapper.call(that, this[i], i, this);
      return other;
    };
  }

  function patternInArray(lookFor, arr) {
    for (var i = 0; i < arr.length; i++) {
      if (lookFor.match(arr[i])) {
        return true;
      }
    }
    return false;
  }

  // http://stackoverflow.com/questions/3514784/what-is-the-best-way-to-detect-a-handheld-device-in-jquery
  var isMobile = function( ){
    return (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent));
  };

  var scaleUpItemCount = function(params) {
    if ('item_count' in params) {
      params.item_count *= 5;
    }
    return params;
  };

  var loadMore = function(endpointURL, params, $element, clickSelector) {
    $.ajax({
      url: endpointURL,
      dataType: "html",
      data: params,
      success: function(responseText, status, jqxhr) {
        $element.html(responseText).slideDown();
        $element.find(clickSelector).on("click", function(event) {
          $element.find('.default').toggleClass('hide');
          $element.find('.loading').toggleClass('hide');
          loadMore(endpointURL, scaleUpItemCount(params), $element, clickSelector);
        });
      },
      error: function(jqxhr, textStatus, errorThrown) {
        console.log("Error thrown: " + errorThrown);
        $element.html("There was a problem retrieving history.");
      }
    });
  };

  var pageReadyOptions = {
    'matchCSSClass': false
  };

  return {

    isMobile: isMobile,

    loadMore: loadMore,

    beforePageReady: function(templatePattern, readyFunction, options) {
      var isTemplate = false;
      var templateName;
      options  = $.extend({}, pageReadyOptions, options);

      // By default we match IDs on the html tag against the templatePattern. If the matchCSSClass
      // is set to true we check CLASSes on the html tag.
      if (options.matchCSSClass) {
        templateName = $('html').attr('class');
      } else {
        templateName = $('html').attr('id');
      }

      if (_.isArray(templatePattern)) {
        isTemplate = patternInArray(templateName, templatePattern);
      }
      else {
        isTemplate = templateName.match(templatePattern);
      }
      if (isTemplate) {
        readyFunction.apply();
      }
    },

    pageReady: function(templatePattern, readyFunction, options) {
      $(function() {

        var isTemplate = false;
        var templateName;
        options  = $.extend({}, pageReadyOptions, options);

        // By default we match IDs on the html tag against the templatePattern. If the matchCSSClass 
        // is set to true we check CLASSes on the html tag.
        if (options.matchCSSClass) {
          templateName = $('html').attr('class');
        } else {
          templateName = $('html').attr('id');
        }

        if (_.isArray(templatePattern)) {
          isTemplate = patternInArray(templateName, templatePattern);
        }
        else {
          isTemplate = templateName.match(templatePattern);
        }
        if (isTemplate) {
          readyFunction.apply();
        }
      });
    },

    ajaxSubmitWrapper: function(button, form, args) {
      loader.setButtonSubmitting(button);
      args.originalSuccess = args.success;
      var common = this;
      args.success = function(responseText, statusText, xhr, form) {
        loader.clearButtonSubmitting(button);
        if (this.originalSuccess != undefined) {
          this.originalSuccess(responseText, statusText, xhr, form);
        }
      };
      $(form).ajaxSubmit(args);
    },

    getURLParameterByName: function(name) {
      /* This gets a URL parameter */
      name = name.replace(/[\[]/, "\\\[").replace(/[\]]/, "\\\]");
      var regexS = "[\\?&]" + name + "=([^&#]*)";
      var regex = new RegExp(regexS);
      var results = regex.exec(window.location.search);
      if(results == null)
        return "";
      else
        return decodeURIComponent(results[1].replace(/\+/g, " "));
    },

    fixImageUrl: function(imageUrl, width, height) {
      if (imageUrl.indexOf("graph.facebook.com") > 0) {
        var urlParts = imageUrl.split("?");
        imageUrl = urlParts[0] + "?width="+width+"&height="+height;
      }
      else {
        imageUrl = imageUrl + "/" + width + "x" + height;
      }
      return imageUrl;
    }
  };

});

