define('trips/owner_got_request',[
  'jquery',
  'common',
  'webapp',
  'common/partials/countdown',
  'common/partials/dialogs/owner_decline',
  'common/partials/dialogs/owner_accept',
  'common/partials/messaging'
],
function(
  $,
  common,
  webapp,
  countdown,
  ownerDecline,
  ownerAccept,
  messaging
) {

  common.pageReady("trips-owner_got_request", function(){

    // ------------------
    // Init components
    // ------------------
    ownerDecline.init();
    ownerAccept.init();

    if (GA_ARGS.seconds_till_expire > 0){
      countdown.init('.countdown', GA_ARGS.seconds_till_expire);
    }

    messaging.init('.messaging');


    // =======================================
    // = Display confirm dialog on page load =
    // =======================================
    var action = GA_ARGS["action"] || "";

    if (action == "accept") {
      ownerAccept.show({
        'bundleId': GA_ARGS.bundle_id,
        'renter': GA_ARGS.renter_first_name
      });

    } else if (action == "decline") {

      ownerDecline.show({
        'bundleId': GA_ARGS.bundle_id,
        'requestStartUnixTime': GA_ARGS.start,
        'requestEndUnixTime': GA_ARGS.end,
        'renter': GA_ARGS.renter_first_name,
        'renterPhoto': GA_ARGS.renter_photo
      });

    }


    // ------------------
    // Attach UI events
    // ------------------
    $(".acceptRequestButton").click(function() {
      webapp.approve(GA_ARGS.bundle_id, $(this));
    });

    $(".declineRequestButton").click(function() {
      ownerDecline.show({
        'bundleId': GA_ARGS.bundle_id,
        'requestStartUnixTime': GA_ARGS.start,
        'requestEndUnixTime': GA_ARGS.end,
        'renter': GA_ARGS.renter_first_name,
        'renterPhoto': GA_ARGS.renter_photo
      });

    });

  });
});
