define('views/car/parking_map_card',[
  'jquery',
  'backbone',
  'text!templates/car/parking_map_card.handlebars',
  'libs/handlebars'
], function(
  $,
  Backbone,
  parkingMapCardHandlebars,
  HandlebarsModule
){

  return ParkingMapCardView = Backbone.View.extend({
  
    template: Handlebars.compile(parkingMapCardHandlebars),

    initialize: function() {
      this.render();
    },

    render: function() {
      this.$el.html(this.template({
        car: this.model.toJSON(),
        parkingLabel: this.model.getParkingLabel(),
        showParkingTip: (this.model.get('parking.location_type') === "STREET")
      }));

      return this;
    }
    
  });

});
