// Signup page view
// This is an empty page that loads the Signup Dialog.
define('views/login/signup',[
  'jquery',
  'backbone',
  'views/login/signup_dialog'
], function(
  $,
  Backbone,
  SignupDialogView
) {

  return SignupView = Backbone.View.extend({
    initialize: function() {
      this.user = this.options.user;

      this.render();
    },

    render: function() {
      this.signupDialogView = new SignupDialogView({
        'isDialogCloseable': false,
        'user': this.user
      });
    }
  });

});
