define('views/public/rent',[
  'jquery',
  'backbone',
  'common',
  'config',
  'libs/bootstrap',
  'views/public/banner'
], function(
  $,
  Backbone,
  common,
  config,
  bootstrap,
  BannerView
) {

  return Backbone.View.extend({
    el: '#content',

    initialize: function() {
      // HTML is rendered in HAML
      if (config.globals.show_homepage_banner) {
        this.bannerView = new BannerView();
      }

      this.activate();
    },

    activate: function() {
      this.$el.find('.fb-login-button').popover();
      if (config.globals.show_homepage_banner) {
        this.bannerView.show();
      }
    }
  });

});
