define('views/public/banner',[
  'jquery',
  'backbone',
  'config',
  'text!templates/public/list_car_banner.handlebars',
  'libs/handlebars'


], function($, Backbone, config, listCarBannerHandlebars){

  var BannerView = Backbone.View.extend({
    template: Handlebars.compile(listCarBannerHandlebars),
    events: {
    },
    initialize: function () {
      this.addedToDOM = false;
      this.$el = $(this.template());
      this.$nav = $('#nav');
    },
    render: function () {
      this.$el.prependTo(this.$nav);
      var self = this;
      this.$el.find('.banner-close-button').on('click', function(event){
        self.hide();
        self.disable();
      });
      this.setPositionOffScreen();
    },
    show: function() {
      if (this.isDisabled()) {
        return;
      }
      var self = this;
      if (!this.addedToDOM) {
        this.render();
      }

      setTimeout(function(){
        self.$nav.addClass('banner-transition banner-visible');
        self.$nav.css({ top: 0 });
      }, 500);
    },
    hide: function() {
      this.setPositionOffScreen();

      var self = this;
      self.$nav.removeClass('banner-visible');

      setTimeout(function(){
        self.$nav.removeClass('banner-transition');
        self.$nav.css({ top: 0 });
        self.$el.remove();

      }, 500);
    },
    isDisabled: function() {
      // Using local storage instead of a cookie since this is purely client
      // side and would only increase http request overhead
      return !((Modernizr.localstorage) &&
             (localStorage.getItem('banner-list-car-disabled') === null));
    },
    disable: function() {
      if (Modernizr.localstorage) {
        localStorage.setItem('banner-list-car-disabled', true);
      }
    },
    setPositionOffScreen: function() {
      this.$nav.css({ top: '-' + this.$el.outerHeight() + 'px' });
    }
  });

  return BannerView;
});

