define('views/public/promotion_landing',[
  'jquery',
  'backbone',
  'libs/bootstrap',
  'text!templates/public/promotion_landing/failure_dialog.handlebars',
  'text!templates/public/promotion_landing/success_dialog.handlebars',
], function ($, Backbone, bootstrapNotAModule, promoLandingFailureDialogHandlebars, promoLandingSuccessDialogHandlebars) {

  var PromoDialog = Backbone.View.extend({
    show: function () {
      this.$el.modal({
        backdrop: 'static',
        keyboard: false
      });

    }
  });
  var SuccessDialog = PromoDialog.extend({
    template: Handlebars.compile(promoLandingSuccessDialogHandlebars),
    initialize: function () {
      this.code = this.options.code;
      this.discount_up_to = this.options.discount_up_to;
      this.percentage = this.options.percentage;

      this.render();
    },
    render: function () {
      var self = this;
      var $body = $('body');

      $body.append(self.template({
        code: this.code,
        discount_up_to: this.discount_up_to,
        percentage: this.percentage
      }));
      self.$el = $body.find('.promo-dialog');

    }
  });
  var FailureDialog = PromoDialog.extend({
    template: Handlebars.compile(promoLandingFailureDialogHandlebars),
    initialize: function () {
      this.reason = this.options.reason;
      this.username = this.options.username;

      this.render();
    },
    render: function () {
      var self = this;
      var $body = $('body');

      $body.append(self.template({
        reason: this.reason,
        username: this.username
      }));
      self.$el = $body.find('.promo-dialog');

    }
  });

  var PromotionLandingView = Backbone.View.extend({
    initialize: function () {
      this.user = this.options.user;
      this.activate();
    },

    activate: function () {
      var successFn = function (options) {
        $('.signup-dialog .modal-dialog').hide();

        var data = {
          'promo_code': options.promoCode,
          'referring_user': options.referringUser
        };

        $.ajax({
          type: 'POST',
          url: '/ajax/promo_campaign',
          dataType: 'json',
          data: data,
          context: this,
          success: function (data) {
            if (data['eligible']) {
              new SuccessDialog(data).show();
            } else {
              new FailureDialog(data).show();
            }
          },
          error: function (jqXHR, textStatus, errorThrown) {
            this.trigger('login:error');
            console.log("/ajax/login error: " + textStatus);
          }
        });

      };
      this.user.on('login:success', successFn);
    }
  });

  return {
    PromoDialog: PromoDialog,
    SuccessDialog: SuccessDialog,
    FailureDialog: FailureDialog,
    PromotionLandingView: PromotionLandingView
  }
});

