define('views/car/security_toggle',[
  'jquery',
  'backbone'
], function(
  $,
  Backbone
){

  return SecurityToggleView = Backbone.View.extend({

    updateHiddenCheckbox: function() {
      var isSecurityOnChecked = (this.$el.find('.security-checkbox-on').is(':checked'));
      this.$el.find('.security-checkbox-off').prop('disabled', isSecurityOnChecked);  
    }

  });

});
