
define('common/pricing',[
  'common',
  'utils/formatters'
], function(common, formatters) {

  function format_length(num) {
    var digits;
    if (num % 1 === 0) {
      digits = 0;
    } else {
      digits = 2;
    }
    return Number(num).toFixed(digits);
  }

  return{

    // Example return obj:
    // { length: "1.75 hours", price: 10.50 }
    getSubtotal: function(car, length) {

      var isHourlyPriceSet = (!isNaN(car.price) && car.price > 0);
      var isDailyPriceSet  = (!isNaN(car.day_price) && car.day_price > 0);
      var isWeeklyPriceSet = (!isNaN(car.week_price) && car.week_price > 0);
      var weeks            = (isWeeklyPriceSet) ? Math.floor(Math.floor(length)/ 168) : 0;
      var days             = (isDailyPriceSet ) ? Math.floor(Math.floor(length) / 24 - weeks * 7) : 0;
      var hours            = parseFloat(length) - days * 24 - weeks * 24 * 7;
      var length_string    = null;
      var rentalPrice      = 0;

      // daily maximum
      if (isDailyPriceSet && (car.day_price < car.price * hours)) {
        days += 1;
        hours = 0;
        if (days === 1 && weeks === 0)
          length_string = "Daily max";
      }

      // weekly maximum
      if (isWeeklyPriceSet && (car.week_price < (car.day_price * days) + (car.price * hours))) {
        weeks += 1;
        days = hours = 0;
        if (weeks === 1) {
          length_string = "Weekly max";
        }
      }

      var shorter    = weeks !== 0 && days !== 0 && hours !== 0;
      var weeks_unit = weeks === 0 ? "" : shorter ? weeks + "w" : weeks == 1 ? "1 week" : weeks + " weeks";
      var days_unit  =  days === 0  ? "" : shorter ? days  + "d" : days == 1 ? "1 day" : days + " days";
      var hours_unit = hours === 0 ? "" : shorter ? format_length(hours) + "h" : hours == 1 ? "1 hour" : format_length(hours) + " hours";

      if (!length_string) length_string = weeks_unit + " " + days_unit + " " + hours_unit;

      rentalPrice = (isHourlyPriceSet) ? rentalPrice + (hours * car.price) : rentalPrice;
      rentalPrice = (isDailyPriceSet) ? rentalPrice + (days * car.day_price) : rentalPrice;
      rentalPrice = (isWeeklyPriceSet) ? rentalPrice + (weeks * car.week_price) : rentalPrice;
      rentalPrice = formatters.formatNumber(rentalPrice, 2);

      return {length: length_string, price: rentalPrice};
    }

  };

});

