define('public/drive',[
  'jquery',
  'common',
  'utils/loader',
  'libs/jquery.form'
], function($, common, loader, jQueryForm) {
  
  common.pageReady("public-drive-index", function() {

    $(".drive-form").validate({
      rules: {
        email: {
          required: true,
          email: true
        }
      },
      submitHandler: function(form) {
        loader.setButtonSubmitting( $(form).find('.submit-email-form') );

        $(form).ajaxSubmit({
          success: function(response) {
            $('.check-email-message').show();
            $('.submit-email-form').hide();
            $('.default-message').hide();
            loader.clearButtonSubmitting($('.submit-email-form'));
          },
          error: function(xhr, statusText, text) {
            loader.clearButtonSubmitting($('.submit-email-form'));
            var errorMessage = xhr.responseText;
            var alreadyUser = "is already a Getaround user";
            var alreadySubscribed = "is already subscribed";
            if (errorMessage.search(alreadyUser) > 0) {
              $('.already-user-message').show();
              $('.default-message').hide();
              $('.submit-email-form').hide();

            }
            else if (errorMessage.search(alreadySubscribed) > 0) {
              $('.already-subscribed-message').show();
              $('.default-message').hide();
              $('.submit-email-form').hide();
            }
            else {
              // generic case, displayed to user as a "Network error"
              $('.error-message').show();
              $('.default-message').hide();
              $('.submit-email-form').hide();
            }
          }
        })
      }
    });
  });
});

