define('views/global/login_button',[
  'jquery',
  'backbone',
  'libs/bootstrap'
], function(
  $,
  Backbone,
  bootstrapNotAModule
) {

  return LoginButtonView = Backbone.View.extend({
    initialize: function() {
      this.user = this.options.user;
      this.activate();
    },

    activate: function() {
      var self = this;
      $('body').on('click', '.login-button', function(event) {
        self.user.login({
          'redirectURL': $(this).attr('data-redirect-url'),
          'referringUser': $(this).attr('data-referring-user'),
          'promoCode': $(this).attr('data-promo-code')
        });
        event.preventDefault();
        return false;
      });

    }
  });

});

