define('public/press/request',[
  'jquery',
  'common',
  'libs/jquery.validate',
  'libs/jquery.form',
  'utils/loader'
], function( $, common, jQueryValidate, jQueryForm, loader){

  common.pageReady( "public-press-request", function() {

    $('#request-speaker-form input:first').focus();

    // ====================================================
    // = Validation =
    // ====================================================
    $.validator.setDefaults({
      debug: true,
      success: "valid"
    });


    $("#request-speaker-form").validate({
      rules: {
        'event-name': {
          required: true
        },
        'event-website': {
          required: true
        },
        'event-date': {
          required: true
        },
        'contact-name': {
          required: true
        },
        'contact-phone': {
          required: true
        },
        'contact-email': {
          required: true,
          email: true
        }
      },
      submitHandler: function(form) {

        loader.setButtonSubmitting( $('#request-speaker-form-submit-button') );

        $(form).ajaxSubmit({
          success: function(responseText, statusText, xhr, form) {
            $(".form-container").fadeOut("slow", function() {
              $(".thank-you-message")
                .removeClass("hidden")
                .fadeIn("slow");
            });
            loader.clearButtonSubmitting( $('#request-speaker-form-submit-button') );
          },
          error: function(responseText, statusText, xhr, form) {
            loader.clearButtonSubmitting( $('#request-speaker-form-submit-button') );
          }
        });
      }
    });

    $('#request-speaker-form-submit-button').on( 'click', function( e ) {
      $("#request-speaker-form").submit();
      return false;
    });

  });
})
;
