
GOOGLE_MAPS_URL = '//maps.googleapis.com/maps/api/js?libraries=places,geometry&sensor=false&callback=__initialize_ga_maps';

define('utils/maps',[
  'jquery',
  'config'
], function(
  $,
  config
){

  var isMapsLoaded = false;
  var isMapsLoading = false;
  var callbacks = [];

  var markerTypes = {
    INSTANT: 'instant',
    INSTANT_MULTI: 'instantMulti',
    INSTANT_MULTI_HIGHLIGHT: 'instantMultiHighlight',
    INSTANT_HIGHLIGHT: 'instantHighlight',
    REQUEST: 'request',
    REQUEST_MULTI: 'requestMulti',
    REQUEST_MULTI_HIGHLIGHT: 'requestMultiHighlight',
    REQUEST_HIGHLIGHT: 'requestHighlight',
    CARKIT: 'carkit',
    SMALL_INSTANT: 'smallInstant',
    SMALL_INSTANT_HIGHLIGHT: 'smallInstantHighlight',
    SMALL_REQUEST: 'smallRequest',
    SMALL_REQUEST_HIGHLIGHT: 'smallRequestHighlight',
    SMALL_CARKIT: 'smallCarkit',
    USER: 'user',
    CAR: 'car',
    PARKING: 'parking'
  };

  var setMarkerIcon = function(marker, type) {
    switch (type) {
      case markerTypes.INSTANT:                 setPinIcon(marker, config.INSTANT_PIN); break;
      case markerTypes.INSTANT_HIGHLIGHT:       setPinIcon(marker, config.INSTANT_PIN_HIGHLIGHT); break;
      case markerTypes.INSTANT_MULTI:           setPinIcon(marker, config.INSTANT_PIN_MULTI); break;
      case markerTypes.INSTANT_MULTI_HIGHLIGHT: setPinIcon(marker, config.INSTANT_PIN_MULTI_HIGHLIGHT); break;
      case markerTypes.REQUEST:                 setPinIcon(marker, config.CLASSIC_PIN); break;
      case markerTypes.REQUEST_HIGHLIGHT:       setPinIcon(marker, config.CLASSIC_PIN_HIGHLIGHT); break;
      case markerTypes.REQUEST_MULTI:           setPinIcon(marker, config.CLASSIC_PIN_MULTI); break;
      case markerTypes.REQUEST_MULTI_HIGHLIGHT: setPinIcon(marker, config.CLASSIC_PIN_MULTI_HIGHLIGHT); break;
      case markerTypes.CARKIT:                  setPinIcon(marker, config.CARKIT_PIN);  break;
      case markerTypes.SMALL_INSTANT:           setMeasleIcon(marker, config.SMALL_INSTANT_PIN); break;
      case markerTypes.SMALL_INSTANT_HIGHLIGHT: setMeasleIcon(marker, config.SMALL_INSTANT_PIN_HIGHLIGHT); break;
      case markerTypes.SMALL_REQUEST:           setMeasleIcon(marker, config.SMALL_CLASSIC_PIN); break;
      case markerTypes.SMALL_REQUEST_HIGHLIGHT: setMeasleIcon(marker, config.SMALL_CLASSIC_PIN_HIGHLIGHT); break;
      case markerTypes.SMALL_CARKIT:            setMeasleIcon(marker, config.SMALL_CARKIT_PIN); break;
      case markerTypes.USER:                    setUserIcon(marker); break;
      case markerTypes.CAR:                     setCarIcon(marker); break;
      case markerTypes.PARKING:                 setParkingIcon(marker); break;
      default:
        console.warn('Invalid or unspecified marker type: ' + type);
        return;
    }
  };

  /*
    addMarker
    ---------
    Supported marker types:
    - instant
    - request
    - carkit
    - smallInstant
    - smallRequest
    - smallCarkit
    - user
    - car
    - parking
    
    latLng param must be a Google Maps LatLng
    
    To draw a parking radius, use addParkingRadius(...)
  */
  
  var addMarker = function(type, latLng, map) {

    var options = {
      position: latLng,
      map: map
    };

    var marker = new google.maps.Marker(options);
    setMarkerIcon(marker, type);
    return marker;
  };

  var getMarkerType = function(isInstant, isSmall, isHighlight, isMulti) {
    var type;
    if      ( isInstant && !isSmall &&  isMulti &&  isHighlight) type = markerTypes.INSTANT_MULTI_HIGHLIGHT;
    else if ( isInstant && !isSmall &&  isMulti && !isHighlight) type = markerTypes.INSTANT_MULTI;
    else if ( isInstant && !isSmall && !isMulti &&  isHighlight) type = markerTypes.INSTANT_HIGHLIGHT;
    else if ( isInstant && !isSmall && !isMulti && !isHighlight) type = markerTypes.INSTANT;
    else if ( isInstant &&  isSmall &&              isHighlight) type = markerTypes.SMALL_INSTANT_HIGHLIGHT;
    else if ( isInstant &&  isSmall &&             !isHighlight) type = markerTypes.SMALL_INSTANT;
    else if (!isInstant && !isSmall &&  isMulti &&  isHighlight) type = markerTypes.REQUEST_MULTI_HIGHLIGHT;
    else if (!isInstant && !isSmall &&  isMulti && !isHighlight) type = markerTypes.REQUEST_MULTI;
    else if (!isInstant && !isSmall && !isMulti &&  isHighlight) type = markerTypes.REQUEST_HIGHLIGHT;
    else if (!isInstant && !isSmall && !isMulti && !isHighlight) type = markerTypes.REQUEST;
    else if (!isInstant &&  isSmall &&              isHighlight) type = markerTypes.SMALL_REQUEST_HIGHLIGHT;
    else if (!isInstant &&  isSmall &&             !isHighlight) type = markerTypes.SMALL_REQUEST;

    return type;
  };


  var addParkingRadius = function(latLng, map) {
    var parkingCircle = new google.maps.Circle({
      center: latLng,
      radius: 213,
      fillOpacity: 0.2,
      fillColor: '#333333',
      strokeOpacity: 1.0,
      strokeColor: '#333333',
      strokeWeight: 1.0
    });
    parkingCircle.setMap(map);  
    return parkingCircle;
  };

  var setPinIcon = function(marker, path) {
    return marker.setIcon({
      scaledSize: new google.maps.Size(24, 35),
      url: path
    });
  };

  var setMeasleIcon = function(marker, path) {
    return marker.setIcon({
      anchor: new google.maps.Point(12, 12),
      scaledSize: new google.maps.Size(12, 12),
      url: path
    });
  };



  var setInstantIcon = function(marker) {
    return setPinIcon(marker, config.INSTANT_PIN);
  };


  var setInstantHighlightIcon = function(marker) {
    return setPinIcon(marker, config.INSTANT_PIN_HIGHLIGHT);
  };


  var setRequestIcon = function(marker) {
    return setPinIcon(marker, config.CLASSIC_PIN);
  };


  var setRequestHighlightIcon = function(marker) {
    return setPinIcon(marker, config.CLASSIC_PIN_HIGHLIGHT);
  };


  var setCarkitIcon = function(marker) {
    return setPinIcon(marker, config.CARKIT_PIN);
  };


  var setSmallInstantIcon = function(marker) {
    return setMeasleIcon(marker, config.INSTANT_PIN);
  };


  var setSmallRequestIcon = function(marker) {
    return setMeasleIcon(marker, config.CLASSIC_PIN);
  };


  var setSmallCarkitIcon = function(marker) {
    return setMeasleIcon(marker, config.CARKIT_PIN);
  };


  var setUserIcon = function(marker) {
    return marker.setIcon({
      path: google.maps.SymbolPath.CIRCLE,
      fillOpacity: 0.8,
      fillColor: '#3498db',
      strokeOpacity: 1.0,
      strokeColor: '#1478bb',
      strokeWeight: 1.0,
      scale: 10 //pixels
    });   
  };


  var setCarIcon = function(marker) {
    return marker.setIcon({
      anchor: new google.maps.Point(12, 12),
      scaledSize: new google.maps.Size(25, 25),
      url: config.CAR_PIN
    });   
  };


  var setParkingIcon = function(marker) {
    return marker.setIcon({
      anchor: new google.maps.Point(12, 12),
      scaledSize: new google.maps.Size(25, 25),
      url: config.PARKING_PIN
    });   
  };


  // Convert String lat and String lng TO Google Maps LatLng Object
  var createGoogleLatLng = function(lat, lng) {
    return new google.maps.LatLng(lat, lng);
  };


  // Position and zoom map to fit all markers in array
  var fitMarkers = function(markers, map) {
    var bounds = new google.maps.LatLngBounds();
    for (var i = 0, markerCount = markers.length; i < markerCount; i++) {
      bounds.extend(markers[i].getPosition());
    }
    map.fitBounds(bounds);
  };

  // Returned object has the fields:
  // - street_address
  // - city
  // - state
  // - region
  // - zipcode
  // - country.
  //
  // The car model address property group fits this structure.
  //
  // WARNING: Only tested with US addresses.
  var getStructuredAddressFromGoogleMapsPlace = function(place) {
    var address = {};
    for (var i = 0; i < place.address_components.length; i++) {
      var addressType = place.address_components[i].types[0];
      switch(addressType) {
        case 'street_number':
          address.street_address = place.address_components[i].short_name;
          break;
        case 'route': // Street name
          address.street_address += " " + place.address_components[i].short_name;
          break;
        case 'neighborhood':
          break;
        case 'locality':  // City
          address.city = place.address_components[i].long_name;
          break;
        case 'administrative_area_level_2': // County
          break;
        case 'administrative_area_level_1': // State
          address.state = address.region = place.address_components[i].short_name;
          break;
        case 'country':
          address.country = place.address_components[i].short_name;
          break;
        case 'postal_code':
          address.zipcode = place.address_components[i].short_name;
          break;
      }
    }
    return address;
  };

  return {

    addMarker:                               addMarker,
    addParkingRadius:                        addParkingRadius,
    markerTypes:                             markerTypes,
    getMarkerTypeStatic:                     getMarkerType,
    setMarkerIcon:                           setMarkerIcon,
    setInstantIcon:                          setInstantIcon,
    setRequestIcon:                          setRequestIcon,
    setRequestHighlightIcon:                 setRequestHighlightIcon,
    setCarkitIcon:                           setCarkitIcon,
    setSmallInstantIcon:                     setSmallInstantIcon,
    setSmallRequestIcon:                     setSmallRequestIcon,
    setSmallCarkitIcon:                      setSmallCarkitIcon,
    setUserIcon:                             setUserIcon,
    setCarIcon:                              setCarIcon,
    setParkingIcon:                          setParkingIcon,
    createGoogleLatLng:                      createGoogleLatLng,
    fitMarkers:                              fitMarkers,
    getStructuredAddressFromGoogleMapsPlace: getStructuredAddressFromGoogleMapsPlace,

    onScriptLoad: function(mapsFunction, hideControls, initializeMap)  {
      if (!isMapsLoaded && !isMapsLoading) {
        if (initializeMap == undefined) {
          initializeMap = true;
        }
        var script = document.createElement('script');
        script.type = 'text/javascript';
        script.src = GOOGLE_MAPS_URL + '&key=' + GA_ARGS.maps_api_key;
        document.body.appendChild(script);
        mapsModule = this;
        window.__initialize_ga_maps = function() {
          if (initializeMap) {
            mapsModule.initialize(hideControls);
          }
          this.__initialize_ga_maps = undefined;
          while (callbacks.length) {
            callbacks.shift().apply();
          }
          isMapsLoaded = true;
          isMapsLoading = false;
        };
        if (mapsFunction) {
          callbacks.push(mapsFunction);
        }
        isMapsLoading = true;
      }
      else if (mapsFunction && !isMapsLoading) {
        mapsFunction.apply();
      }
      else if (mapsFunction) {
        callbacks.push(mapsFunction);
      }
    },
    initialize: function(hideControls) {
      // =================
      // = Map Variables =
      // =================
      var carMarkers     = [];
      var carInfoWindows = [];
      var carnames       = [];

      var debug_rects    = [];

      var lat  = window.lat  || 37.7774052;
      var lng  = window.lng  || -122.4099154;
      var SW   = window.SW   || [37.7685854,-122.4259228];
      var NE   = window.NE   || [37.7862239,-122.3939080];
      var zoom = window.zoom || 0;
      var cars = window.cars || [];

      var timeouts   = window.timeouts  || {};
      var updateMap  = window.updateMap || false;   // update map when viewport changes
      var showInfo   = window.showInfo  || false;   // show infoWindow on marker click

      var default_car_image = window.default_car_image || "";


      // =================
      // = Map Functions =
      // =================
      // DEPRECATED: Being phased out for the new addMarker() above
      function addMarkerOld(latlng, drag, focus, setLocationCallback){
        var image  = new google.maps.MarkerImage(
          config.INSTANT_PIN,
          null,
          null,
          null,
          new google.maps.Size(28, 40)
        );

        var marker = new google.maps.Marker({
          position: latlng,
          draggable: drag,
          map: map,
          icon: image,
          optimized: false
        });
        google.maps.event.addListener(marker, 'dragstart', function(MouseEvent) {
          marker.setFlat(true);
        });
        if (typeof(setLocationCallback) == "function") {
          google.maps.event.addListener(marker, 'dragend', function(MouseEvent) {
            marker.setFlat(false);
            var loc = marker.getPosition();
            setLocationCallback(loc);
          });
        } else {
          google.maps.event.addListener(marker, 'dragend', function(MouseEvent) {
            marker.setFlat(false);
          });
        }

        carMarkers.push(marker);
        if (focus){
          map.setZoom(15);
          resizeMap();
          map.panTo(latlng);
        }

        if (showInfo) {
          google.maps.event.addListener(marker, 'click', function() {
            carInfoWindows.forEach(function(infowindow) {
              infowindow.close();
            });
            index = jQuery.inArray(marker, carMarkers);
            carInfoWindows[index].open(map, marker);
            $(window).focus();
          });
        }
      }

      function addMarkerLL(lat_comma_lng, drag, focus, setLocationCallback){
        var latlng_array = lat_comma_lng.split(",");
        var car_latlng = new google.maps.LatLng(latlng_array[0], latlng_array[1]);
        addMarkerOld(car_latlng, drag, focus, setLocationCallback);
      }

      function addMarkerGeocode(address, drag, focus, setLocationCallback, callback){
        if (geocoder) {
          geocoder.geocode( { 'address': address}, function(results, status) {
            if (status == google.maps.GeocoderStatus.OK) {
              var loc = results[0].geometry.location;
              // setLocationCallback(loc)
              addMarkerOld(loc, drag, focus, setLocationCallback);
              if (typeof(callback) == "function"){
                var parts = parseAddressComponents(results[0].address_components);
                callback(parts['city'], parts['state'], parts ['country'], parts['street_address'], parts['postal_code'], results[0].geometry.location);
              }
            }
          });
        }
      }

      function reverseGeoCode(loc, callback) {
        geocoder.geocode({"location":loc}, function(results, status) {
          if (!results || status != google.maps.GeocoderStatus.OK) {
          }
          else if (typeof(callback) == "function") {
            var parts = parseAddressComponents(results[0].address_components);
            callback(parts['city'], parts['state'], parts['country'], parts['street_address'], parts['postal_code'], results[0].geometry.location);
          }
        });
      }

      /* For debugging */
      function logAllResults(results) {
        for (var i in results) {
          var parts = parseAddressComponents(results[i].address_components);
          var full = [parts['street_address'], parts['city'], parts['state'], parts['country'], parts['postal_code']].join(", ");
        }
      }

      function parseAddressComponents(address_components) {
        var city = "";
        var state = "";
        var country = "";
        var street_number = "";
        var route = "";
        var postal_code = "";
        var chunks = address_components;
        var resultLength = chunks.length;
        // loop in reverse order to prefer locality over neighborhood for city assignment
        for (var i = resultLength-1; i >= 0; i--) {
          if ( (chunks[i]["types"].indexOf("neighborhood") != -1 ||
                chunks[i]["types"].indexOf("sublocality") != -1 ||
                chunks[i]["types"].indexOf("locality") != -1) && city == "") {
            city = chunks[i]["long_name"];
          } else if ( chunks[i]["types"].indexOf("administrative_area_level_1") != -1 && state == "" ) {
            state = chunks[i]["long_name"];
          } else if ( chunks[i]["types"].indexOf("country") != -1 && country == "" ) {
            country = (chunks[i]["short_name"]).toLowerCase ();
          } else if ( chunks[i]["types"].indexOf("street_number") != -1 && street_number == "" ) {
            street_number = chunks[i]["long_name"];
          } else if ( chunks[i]["types"].indexOf("route") != -1 && route == "") {
            route = chunks[i]["long_name"];
          } else if ( chunks[i]["types"].indexOf("postal_code") != -1 && postal_code == "") {
            postal_code = chunks[i]["long_name"];
          }
        }
        var street_address = street_number;
        if (route) {
          street_address += (" " + route);
        }
        return {"city":city, "state":state, "country":country, "street_address":street_address, "postal_code":postal_code};
      }

      function getLocation(index){
        if (index < carMarkers.length){
          var location = carMarkers[index].getPosition();
          return location.lat() + "," + location.lng();
        } else {
          return "None";
        }
      }

      function panTo(index) {
        if (index < carMarkers.length){
          var location = carMarkers[index].getPosition();
          map.panTo(location);
        } else {
          return "None";
        }
      }

      function panBy(x, y) {
        map.panBy(x, y);
      }

      function clearMap(completely){
        carMarkers.forEach(function(marker) {
          marker.setMap(null);
          if (completely) {
            marker = null;
          }
        });
        carMarkers.length = 0;
        if (completely) {
          carInfoWindows.forEach(function(w) {
            w.setMap(null);
            w = null;
          });
          carInfoWindows.length = 0;
          carnames.length = 0;
        }
      }

      function resizeMap(){
        google.maps.event.trigger(map, 'resize');
      }

      function fitBounds(sw, ne) {
        if (!sw || !ne) return;
        resizeMap();
        var SW_latlng = new google.maps.LatLng(sw[0], sw[1]);
        var NE_latlng = new google.maps.LatLng(ne[0], ne[1]);
        var viewport = new google.maps.LatLngBounds(SW_latlng, NE_latlng);
        map.fitBounds(viewport);
        map.setZoom(map.getZoom()+1);
      }

      
      // ==================
      // = Initialize Map =
      // ==================
      var init_latlng = new google.maps.LatLng(lat, lng);
      
      var myOptions = {
        zoom: zoom,
        scrollwheel: true,
        center: init_latlng,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
        navigationControl: !hideControls,
        navigationControlOptions: { style: google.maps.NavigationControlStyle.SMALL },
        mapTypeControl: !hideControls,
        mapTypeControlOptions: {style: google.maps.MapTypeControlStyle.DROPDOWN_MENU},
        scaleControl: false,
        streetViewControl: !hideControls
      };

      var map;
      try {
        map = new google.maps.Map(document.getElementById("map_canvas"), myOptions);
      } catch(e) {}

      var geocoder = new google.maps.Geocoder();


      // ========================
      // = Keep the map updated =
      // ========================
      if (updateMap) {

        var delay_updateMap = function() {
          return function() {
            delete timeouts["updateMap"];

            var bounds = map.getBounds();
            var sw = bounds.getSouthWest();
            var ne = bounds.getNorthEast();
            window.ajax_state["SW"] = [sw.lat(), sw.lng()];
            window.ajax_state["NE"] = [ne.lat(), ne.lng()];
            window.ajax_state["page"] = 1;
            window.ajax_state["src"] = "viewport";
            window.ajaxLoad({"flush_cars": false});
          };
        };

        var updateMap_bind = function() {
          if (window.ajax_state["view"] === "map") {
            if("updateMap" in timeouts) {
              clearTimeout(timeouts["updateMap"]);
            }
            timeouts["updateMap"] = setTimeout(delay_updateMap(), 1000);
          }
        };

        // google.maps.event.addListener(map, 'bounds_changed', function() {
        google.maps.event.addListener(map, 'dragend', function() {
          updateMap_bind();
        });

        google.maps.event.addListener(map, 'zoom_changed', function() {
          updateMap_bind();
        });

      }

      // ======================================
      // = Export functions into global scope =
      // ======================================

      window.addMarkerLL      = addMarkerLL;
      window.addMarkerGeocode = addMarkerGeocode;
      window.getLocation      = getLocation;
      window.panTo            = panTo;
      window.panBy            = panBy;
      window.clearMap         = clearMap;
      window.resizeMap        = resizeMap;
      window.fitBounds        = fitBounds;
      window.reverseGeoCode   = reverseGeoCode;
    }
  }

});

