define('braintree_box',[
  'jquery',
  'libs/jquery.validate',
  'libs/braintree',
  'common',
  'config',
  'utils/loader'
], function(
  $,
  jQueryValidate,
  braintreeJS,
  common,
  config,
  loader
) {

  common.pageReady(["user-edit", "checkout-payment"], function() {

    var braintree = Braintree.create(config.globals.bt_js_key);
    var setupInternational = function() {
      $("#card-country").find("option[value=UK]").val("GB"); // Make our country codes compatible with braintree.
      var $cardStateBox = $("#card-state");
      var $cardZipBox = $("#card-zip");
      var internationalBoxHTML = $('<input id="card-state-international" name="billing_region"' +
                                   'secure="true" do_not_track=1 class="required input-fullwidth"/>');
      $(document).on('change', '#card-country', function() {
        var $labelForState = $("label[for='card-state']");
        var $labelForZip = $("label[for='card_zip']");
        if ($(this).val() == "US") {
          $labelForState.after($cardStateBox);
          $("#card-state-international").remove();
          $labelForState.text("State");
          $labelForZip.text("Zip code");
          $cardZipBox.addClass("digits").attr("minlength", 5).attr("maxlength", 5);
        }
        else {
          $("#card-state").remove();
          $labelForState.after(internationalBoxHTML);
          $labelForState.text("Region");
          $labelForZip.text("Postal code");
          $cardZipBox.removeClass("digits").removeAttr("minlength").removeAttr("maxlength");
        }
      });
      $("#braintree-box #card-country").trigger("change");
    };
    setupInternational();
    // ==============================
    // = Toggling the add card form =
    // ==============================  
    $(document).on('click', '#braintree-box #add-another-card', function() {
      $("#cancel-addcard").show();
      $("#add-another-card").hide();
      $("#addcard").show();
      $("#card-no").focus();
    });
    
    $(document).on('click', '#cancel-addcard', function() {
      $("#cancel-addcard").hide();
      $("#add-another-card").show();
      $("#addcard").hide();
    });
    
    // ===============
    // = Delete card =
    // ===============
    $(document).on('click', '#braintree-box .delete', function(event){
      // NOTE: this extra case handling is probably not necessary - the UI doesn't even allow
      // deleting multiple cards, and the logic for the last card check doesn't work for some reason
      if ( $("#braintree-box .delete.processing").length > 0 ){
        alert("You can only delete one credit card at a time.");
      } else {
        var del = confirm( $(this).attr("confirm") );
        if (del) {
          var token = $(this).attr("token");
          $(this).html("");
          $(this).addClass("processing");
          // TODO(BS): store jquery wrapped #braintree-box in a variable to save on wrapping cost
          $("#braintree-box .delete.processing").html("Delete");
          $("#braintree-box .delete.processing").removeClass("processing");
          $.ajax({
             type: "POST",
             data: {"token": token},
             url: "/ajax/credit-card/delete",
             dataType: "json",
             success: function (defaultToken) {
               $("#" + token).remove();
               $("td.make-default").removeClass("default");
               $("#" + defaultToken).children("td.make-default").addClass("default");
             },
             error: function (data) {
               $("#bt-error-hook").addClass("alert").html(data.responseText);
             }
          });
        }
      }
    });
    
    // =======================
    // = Change default card =
    // =======================
    $(document).on('click', '#braintree-box .make-default', function(event){
      var token = $(this).attr("token");
      $("#braintree-box .make-default").removeClass("default");
      // TODO(BS): fix this processing class - it should display a loading indicator
      $(this).addClass("processing");
      
      $.ajax({
        type: "POST",
        data: {"token": token},
        url: "/ajax/credit-card/set-default",
        dataType: "json",
        success: function (defaultToken) {
          $("#braintree-box .make-default").removeClass("processing");
          $("#" + defaultToken).children("#braintree-box .make-default").addClass("default");
        },
        error: function (data) {
          $("#bt-error-hook").addClass("alert").html("Payment Error: " + data.responseText);
        }
      });
    });

    // =========================================
    // = Encrypt to hidden so user doesn't see =
    // =========================================
    var encryptToHiddenForm = function (form) {
      var hiddenIdentifier = "hidden-";
      var updateIds = function (formToUpdate) {
        // preserves uniqueness of ids in DOM
        $formToUpdate = $(formToUpdate);
        $formToUpdate.find("*").each(function (index, element) {
          if ($(element).attr("id")) {
            $(element).attr("id", hiddenIdentifier + $(element).attr("id"));
          }
        });
        $formToUpdate.attr("id", hiddenIdentifier + $formToUpdate.attr("id"));
      };

      var shouldEncrypt = function (element) {
        var encryptedFields = [
          'card-no',
          'card-expiry-month',
          'card-expiry-year',
          'card-cvc'
        ];
        var elementId = $(element).attr("id");
        return ($.inArray(elementId, encryptedFields) >= 0);
      };

      var encryptFormValues = function ($btForm) {
        $btForm.find('input, select').each(function (index, element) {
          var elementId = $(element).attr("id");
          var elementValue = $(element).val();

          if (shouldEncrypt(element)) {
            $("#" + hiddenIdentifier + elementId).val(braintree.encrypt(elementValue));
          } else {
            $("#" + hiddenIdentifier + elementId).val(elementValue);
          }
        });
      };

      var convertSelectsToTextInputs = function ($form) {
        $form.find('select').each(function (index, element) {
          // append to what we're iterating is ok because type is not a select
          var $element = $(element);
          // getting value from unencrypted form; jQuery.clone() doesn't copy some dynamic values
          $('<input>')
              .attr('type', 'text')
              .attr('name', $element.attr('name'))
              .attr('id', $element.attr('id'))
              .attr('secure', 'true')
              .appendTo($form);
        });
        $form.find('select').remove();
      };

      var encryptedForm = function (form) {
        var newForm = $(form).clone();
        convertSelectsToTextInputs(newForm); // because encrypted values can't be set on selects
        updateIds(newForm);
        var newDivWithUpdatedIds = $("#encrypted-wrapper");
        newDivWithUpdatedIds.append(newForm);
        encryptFormValues($(form));
        return newDivWithUpdatedIds.find("form");
      };

      return encryptedForm(form);
    };

    // ============
    // = Add card =
    // ============
    $(document).on('click', '#braintree-box #addcard-button', function() {
      $("#addcard").submit();
    });
    
    $("#braintree-box #addcard").validate({
      errorElement: "span",
      onfocusout: false,
      onkeyup: false,
      onclick: false,
      focusInvalid: false,
      focusCleanup: true,
      wrapper: "p",
      groups: {
        "expiration": "expiration_month expiration_year"
      },
      messages: {
        "billing_country_code_alpha2": "Required.",
        "expiration_month": "These fields are required.",
        "expiration_year": "These fields are required.",
        "cvv": "Required."
      },
      errorPlacement: function(error, element) {
        if (jQuery.inArray( $(element).attr("id"), ["card-expiry-month", "card-expiry-year"]) != -1 ) {
          error.insertAfter($("#card-expiry-year"));
        } else {
          error.insertAfter(element);
        }
      },
      submitHandler: function(form) {
        // remove any whitespace and dashes in credit card number
        var $cardNo = $("#card-no"); // for efficiency
        $cardNo.val($cardNo.val().replace(/[\s-]/g,""));
        var addCardButton = $("#addcard-button");
        loader.setButtonSubmitting(addCardButton);

        $("#cancel-addcard").hide();
        var formToSubmit = encryptToHiddenForm(form);
        var formData = formToSubmit.serialize();
        var formUrl = formToSubmit.attr("action");
        $.ajax({
          type: "POST",
          data: formData,
          url: formUrl,
          dataType: "json",
          success: function(data) {
            // maybe add to client-side state here, tracking the cards, so a full page reload is not necessary?
            location.reload();
          },
          error: function(data) {
            $("#bt-error-hook").addClass("alert").html(data.responseText);
            loader.clearButtonSubmitting(addCardButton);
          }
        });

      }
    });
    
    // =======================
    // = Add card and submit =
    // =======================
    function add_card_and_submit(callback) {
      if ($("#braintree-box #addcard").is(":visible") &&
          $("#braintree-box #card-no").val() != "") {
        $("#braintree-box #addcard-button.active").trigger("click");
      } else {
        callback();
      }
    }
    window.add_card_and_submit = add_card_and_submit;
    $(".addCardAndSave").click(function() {
      add_card_and_submit(
        function() {$('#signupform').submit();});
    });

    // ========
    // = Misc =
    // ========
    $(".remove-default").change(function() {
      $(this).children("[value='']").remove();
    });
    
  });
});

