// manage_calendar.js
// ----------------
// public init()
// public show()
// public hide()
// public setStartDay()
// public setEndDay()
// private enableInputs()
// private disableInputs()
// private updateDayInputs();
// private blockDays()
// ---------------
// If the owner declines a rental and states that their 'car is in use', 'car is being serviced',
// 'they cannot do a key handoff', or mark 'other' as the reason then we show them this follow-up
// 'Manage your calendar dialog.' In this dialog the owner can block off a day or span of days in
// which they would like to not receive future rental requests.
//
// This dialog is used on three different pages:
// - dashboard/activity
// - dashbaord/dashboard
// - trips/rental
//


define('common/partials/dialogs/manage_calendar',[
  'jquery',
  'common',
  'config',
  'utils/converters',
  'utils/loader',
  'libs/jquery_ui',
  'text!templates/manage_calendar_dialog.handlebars',
  'libs/moment.min',
  'libs/bootstrap',
  'libs/handlebars'
], function(
  $,
  common,
  config,
  converters,
  loader,
  jQueryUI,
  dialogHandlebars
){

  var $element      = $(),
      dialogTemplate,
      startDay,
      endDay,
      carId,
      dayInputWithFocus = "none";


  // =================
  // init
  // =================
  var init = function() {

    dialogTemplate = Handlebars.compile( dialogHandlebars );

  };


  // =================
  // show
  // =================
  var show = function( data ) {

    // If modal already exists from a previous show() call: remove, compile new
    // html, and display modal.
    $('#manage-calendar-dialog').remove();
    $('body').append( dialogTemplate() );

    $element = $('#manage-calendar-dialog');
    $element.modal();

    var $calendar             = $element.find( '.manage-calendar-widget' ),
        $startDayInput        = $element.find( '.start-day' ),
        $endDayInput          = $element.find( '.end-day' ),
        $activeInputIndicator = $element.find( '.active-input-indicator' ); // caret pointing from
                                                                            // calendar to txt input

    // Difference between date and day variables is that the Day variables drop the time.
    startDate = new Date( data.startUnixTime * 1000 );
    endDate = new Date( data.endUnixTime * 1000 );

    startDay = new Date( startDate.getYear() + 1900 , startDate.getMonth(), startDate.getDate() );
    endDay = new Date( endDate.getYear() + 1900, endDate.getMonth(), endDate.getDate() );

    carId = data.carId;

    updateDayInputs();

    //
    // Attach UI events and activate calendar.
    //
    $element.on( 'change', '.block-days', function( event ){
      if ( $( this ).is( ':checked' ) ) {
        enableInputs();
      } else {
        disableInputs();
      }
    });

    $element.on( 'click', '.skip-btn', function( event ){
      loader.setButtonSubmitting( this );
      window.location = window.location.href.split("?")[0];
    });

    $element.on( 'click', '.done-btn', function( event ){
      loader.setButtonSubmitting( this );
      blockDays();
    });

    // For mobile devices we hide the calendar widget and text inputs and show native date inputs.
    if ( common.isMobile() ) {
      $element.find( '.start-day, .end-day' ).hide();
      $element.find( '.start-day-dateinput, .end-day-dateinput' )
        .show()
        .on( 'change blur', function( event ){
          var $dateinput       = $(this),
              dateinputMoment  = moment( $(this).val() ),
              isStartDateinput = $dateinput.hasClass('start-day-dateinput');

          if ( dateinputMoment.isValid() ){
            $dateinput.removeClass( 'error' );
            $element.find( '.done-btn' ).removeAttr( 'disabled' );

            if ( isStartDateinput ){
              startDay = dateinputMoment.toDate();
            } else {
              endDay = dateinputMoment.toDate();
            }

          } else {
            $dateinput.addClass( 'error' );
            $element.find( '.done-btn' ).attr( 'disabled', 'disabled' );
          }
        });

    } else {

      // If not a mobile device...

      // Validate user input dates
      $startDayInput
        .on( 'focus', function( event ) {
          dayInputWithFocus = "start";
          $activeInputIndicator.addClass( 'from-input-active' );
        })
        .on( 'blur', function( event ) {
          $activeInputIndicator.removeClass( 'from-input-active' );
        })
        .on( 'keyup', function( event ){
          var startMoment = moment( $startDayInput.val() ),
              currentYear = new Date().getFullYear();

          if ( startMoment.isValid() ){
            // If the user does not specify a year, the date object defaults
            // to 2001. If this happens, update the year to the current.
            if( startMoment.year() < currentYear ){
              startMoment.year( currentYear );
            }
            $startDayInput.removeClass( 'error' );
            startDay = startMoment.toDate();
            $calendar.datepicker( 'setDate', startDay );

            // If both date inputs are valid, make sure Done button is enabled
            if( moment( $endDayInput.val() ).isValid() ){
              $element.find( '.done-btn' ).removeAttr( 'disabled' );
            }
          } else {
            $startDayInput.addClass( 'error' );
            $element.find( '.done-btn' ).attr( 'disabled', 'disabled' );
          }
        });

      $endDayInput
        .on( 'focus', function( event ) {
          dayInputWithFocus = "end";
          $activeInputIndicator.addClass( 'to-input-active' );
        })
        .on( 'blur', function( event ) {
          $activeInputIndicator.removeClass( 'to-input-active' );
        })
        .on( 'keyup', function( event ){
          var endMoment = moment( $endDayInput.val() ),
              currentYear = new Date().getFullYear();

          if ( endMoment.isValid() ){
            // If the user does not specify a year, the date object defaults
            // to 2001. If this happens, update the year to the current.
            if( endMoment.year() < currentYear ){
              endMoment.year( currentYear );
            }
            $endDayInput.removeClass( 'error' );
            endDay = endMoment.toDate();
            $calendar.datepicker( 'setDate', endDay );

            // If both date inputs are valid, make sure Done button is enabled
            if( moment( $startDayInput.val() ).isValid() ){
              $element.find( '.done-btn' ).removeAttr( 'disabled' );
            }
          } else {
            $endDayInput.addClass( 'error' );
            $element.find( '.done-btn' ).attr( 'disabled', 'disabled' );
          }
        });

      //
      // Calendar
      //
      $calendar.datepicker({
        defaultDate: startDay,
        numberOfMonths: 2,
        minDate: 0,
        maxDate: '+3M',
        beforeShowDay : function ( day ) {
          // This function is called before each day is rendered on the calendar widget

          // Comparing date objects in JS has its quirks. Using getTime to avoid.
          var dayUTC  = day.getTime(),
              startUTC = startDay.getTime(),
              endUTC   = endDay.getTime(),
              cssClass = "";

          // Add custom classes to display date range
          if ( dayUTC === startUTC && dayUTC === endUTC ) {
            cssClass = "rangeStartAndEnd";
          } else if ( dayUTC === startUTC ) {
            cssClass = "rangeStart";
          } else if ( dayUTC === endUTC ) {
            cssClass = "rangeEnd";
          } else if ( dayUTC > startUTC && dayUTC < endUTC ){
            cssClass = "rangeMid";
          }

          return [ true, cssClass ]; // Return optional third arg for tooltip
        },
        onSelect: function( dateText, element ) {
          var dateSelected = $calendar.datepicker("getDate");

          if ( dayInputWithFocus === "end" ){
            setEndDay( dateSelected );
            $startDayInput.focus();
          } else {
            setStartDay( dateSelected );
            $endDayInput.focus();
          }
        }
      });
    }
  };


  // =================
  // hide
  // =================
  var hide = function( ) {
    $element.modal( 'hide' );
  };


  // =================
  // setStartDay
  // =================
  var setStartDay = function( day ) {
    startDay = day;
    if ( endDay < startDay ){
      endDay = startDay;
    }
    updateDayInputs();
  };

  // =================
  // setEndDay
  // =================
  var setEndDay = function( day ) {
    endDay = day;
    if ( startDay > endDay ){
      startDay = endDay;
    }
    updateDayInputs();
  };


  // =================
  // enableInputs
  // =================
  var enableInputs = function() {
    if ( common.isMobile() ) {
      $element
        .find('.day-inputs').addClass( 'on' ).end()
        .find( '.start-day-dateinput, .end-day-dateinput' ).removeAttr( 'disabled' ).end()
        .find('.start-day-dateinput').focus().end()
        .find('.done-btn-wrapper').animate( { width: 'toggle', opacity: 1 }, 'slow' );
    } else {
      $element
        .find('.day-inputs').addClass( 'on' ).end()
        .find('.start-day, .end-day').removeAttr( 'disabled').end()
        .find('.manage-calendar-widget').slideDown( 'slow' ).end()
        .find('.start-day').focus().end()
        .find('.done-btn-wrapper').animate( { width: 'toggle', opacity: 1 }, 'slow' );
    }
  };


  // =================
  // disableInputs
  // =================
  var disableInputs = function() {
    if ( common.isMobile() ) {
      $element
        .find( '.start-day-dateinput, .end-day-dateinput' ).attr( 'disabled', 'disabled' ).end()
        .find('.done-btn-wrapper').animate( { width: 'toggle', opacity: 0 }, 'slow' );
    } else {
      $element
        .find('.day-inputs').removeClass( 'on' ).end()
        .find('.start-day, .end-day').attr( 'disabled', 'disabled' ).end()
        .find('.manage-calendar-widget').slideUp( 'slow' ).end()
        .find('.done-btn-wrapper').animate( { width: 'toggle', opacity: 0 }, 'slow' );
    }
  };


  // =================
  // updateDayInputs
  // =================
  // Take the current startDay and endDay values and update the start and end day inputs.
  var updateDayInputs = function() {
    if ( common.isMobile() ) {
      var $startDateinput = $element.find( '.start-day-dateinput' ),
          $endDateinput   = $element.find( '.end-day-dateinput' );

      $startDateinput
        .removeAttr( 'disabled')
        .val( $.datepicker.formatDate( 'yy-mm-dd', startDay ) )
        .attr( 'disabled', 'disabled' );

      $endDateinput
        .removeAttr( 'disabled')
        .val( $.datepicker.formatDate( 'yy-mm-dd', endDay ) )
        .attr( 'disabled', 'disabled' );

    } else {

      var $startDayInput = $element.find( '.start-day' ),
          $endDayInput   = $element.find( '.end-day' );

      // If input is disabled, we need to temporarily enable to set the value.
      if ( $startDayInput.attr( 'disabled') === "disabled" ){
        $startDayInput
          .removeAttr( 'disabled')
          .val( $.datepicker.formatDate( 'M d', startDay ) )
          .attr( 'disabled', 'disabled' );
      } else {
        $startDayInput.val( $.datepicker.formatDate( 'M d', startDay ) );
      }

      if ( $endDayInput.attr( 'disabled') === "disabled" ){
        $endDayInput
          .removeAttr( 'disabled')
          .val( $.datepicker.formatDate( 'M d', endDay ) )
          .attr( 'disabled', 'disabled' );
      } else {
        $endDayInput.val( $.datepicker.formatDate( 'M d', endDay ) );
      }

    }

  };


  // =================
  // blockDays
  // =================
  var blockDays = function( ){
    $.ajax({
      url: config.BLOCK_AVAILABILITY_URL,
      type: "POST",
      dataType: "json",
      data: {
        "start_time": converters.JSDateToISODateTimezoneNaive(startDay),
        "end_time": converters.JSDateToISODateTimezoneNaive(endDay.setDate(endDay.getDate() + 1)),
        "carid": carId,
        "widget": "decline-dialog"
      },
      success: function(data, textStatus, jqXHR) {
        if (data["result"] == "success") {
          // Reload current page without url parameters
          window.location = window.location.href.split("?")[0];
        }
        else {
          console.log("failure server returned " + data["message"]);
        }
      },
      error: function(jqXHR, textStatus, errorThrown) {
        console.log("HTTP call failed " + textStatus + " ERROR: " + errorThrown);
      }
    });
  };


  return {

    init: init,

    show: show,

    hide: hide,

    setStartDay: setStartDay,

    setEndDay: setEndDay

  };
});
