// Top level Backbone view for Search.

define('views/car/car_profile',[
  'jquery',
  'backbone',
  'common',
  'config',
  'libs/bootstrap',
  'utils/converters',
  'utils/maps',  
  'models/viewmodels/pickup_and_return',
  'views/car/rent_button',
  'views/car/rules_dialog',
  'views/car/trip_subtotal',
  'views/shared/notification',
  'views/shared/pickup_and_return_inputs',
  'common/partials/availability_matrix'
], function(
  $,
  Backbone,
  common,
  config,
  bootstrap,
  converters,
  maps,
  PickupAndReturn,
  RentButtonView,
  RulesDialogView,
  TripSubtotalView,
  NotificationView,
  PickupAndReturnInputsView,
  availabilityMatrix
) {

  var CarProfileView = Backbone.View.extend({
    el: '#content',
    initialize: function() {
      // Setup models and collections
      this.user              = this.options.user;
      this.car               = config.globals.car;
      this.pickupAndReturn   = new PickupAndReturn();
      
      this.pickupAndReturn.set({
        'pickup_date': this.model.get('pickup_date'),
        'return_date': this.model.get('return_date'),
        'car_iana_timezone': config.globals.car.timezone,
        'user_iana_timezone': this.user.get('iana_timezone')
      });

      // Render and turn on UI
      this.render();
      this.activate();
    },
    render: function() {
      // Subviews
      if (!config.globals.isOwner) {
        this.pickupAndReturnInputsView = new PickupAndReturnInputsView({
          el: '#car-profile-pickup-and-return-inputs',
          model: this.pickupAndReturn
        });
        this.tripSubtotalView = new TripSubtotalView({
          el: '#car-profile-trip-subtotal',
          model: this.model,
          car: this.car
        });
        this.rentButtonView = new RentButtonView({
          el: '#car-profile-rent-button',
          model: this.model,
          car: this.car
        });
        this.rulesDialogView = new RulesDialogView({
          el: '#car-profile-rules-dialog',
          model: this.model,
          car: this.car
        });
      }

      // Attaches UI events to notification. HTML is rendered in haml.
      this.notificationView = new NotificationView();
      
      return this;
    },
    activate: function() {
      var self = this;

      this.listenTo(this.pickupAndReturn, 'change:pickup_date change:return_date', function(model) {
        this.model.set({
          'pickup_date': model.get('pickup_date'),
          'return_date': model.get('return_date')
        });
      });

      this.listenTo(this.model, 'change', function(model) {
        var changedAttributes = _.keys(model.changedAttributes());
        
        if (_.intersection(changedAttributes, ['pickup_date', 'return_date']).length > 0) {
          self.checkCarAvailability();
        }

        model.updateCookie();
      });

      this.$el.on('SHOW_RULES_DIALOG', function() {
        self.rulesDialogView.show();
      });

      this.$el.on('FORWARD_USER_TO_CHECKOUT', function() {
        self.forwardUserToCheckout();
      });
      
      this.checkCarAvailability();

      // --------------------------------------------------------
      // Code copied from our old car/profile.js
      // --------------------------------------------------------

      // Car photos
      $("#otherImages .img").on( 'click', function( event ) {
        var $this = $(this);
        if (!$this.hasClass("selected")) {
          $("#otherImages .img, #otherImages .img-wrapper").removeClass("selected");
          $this.addClass("selected");
          $this.closest('.img-wrapper').addClass("selected");
          $("#mainImage").attr('src', $this.attr("large_url"));
          $("#otherImages").css("background-position", $this.attr("selected_bg_pos"));
        }
      });
      
      // Map
      if (config.globals.is_listed){
        maps.onScriptLoad(function() {
          // lat and lng won't be set until google's map api loads
          if (window.lat && window.lng) {

            // Put the car on the map
            addMarkerLL(window.lat + "," + window.lng, false, true, false);
            resizeMap();
            panTo(0);
            setTimeout("resizeMap(); panTo(0);", 200);
            setTimeout("resizeMap(); panTo(0);", 1000);
          }
        });
      };

      // Availability matrix
      // Rename a few car object keys so that the object is ready to be consumed by availability matrix
      var car = {
        'carname':    config.globals.car.car_name,
        'ymm':        config.globals.car.car_title,
        'price':      config.globals.car.price,
        'dayprice':   config.globals.car.dayprice,
        'weekprice':  config.globals.car.weekprice,
        'has_carkit': config.globals.car.has_carkit, // note the dash and hyphen, be careful with this property
        'instant':    config.globals.car.is_instant,
        'photo':      config.globals.car.photo,
        'timezone':   config.globals.car.timezone,
        'utc':        parseFloat( config.globals.car.utc_offset ),
        'dst':        parseFloat( config.globals.car.dst_offset ),
        'start':      config.globals.car.start,
        'length':     config.globals.car.duration,
        'transmission': config.globals.car.transmission
      };

      availabilityMatrix.init( $('#availability-matrix'), {
        'car': car,
        'availability': config.globals.car.availability
      });

      // Ratings
      var $loadMoreElement = $('#car-profile-ratings');

      var handleRatingsLoadMore = function(event) {
        $loadMoreElement.find('.default').toggleClass('hide');
        $loadMoreElement.find('.loading').toggleClass('hide');
        common.loadMore("/ajax/car-ratings", {"item_count": 1000, "car_key": config.globals.car_key}, $loadMoreElement, '.ratings-load-more');
      };

      $loadMoreElement.find('.ratings-load-more').on("click", handleRatingsLoadMore);
      // subtitle section has an in-page link to reviews anchor
      $('#ratings-subtitle-link').on("click", handleRatingsLoadMore);

      // Facebook join button tooltip
      this.$el.find('.fb-login-button').popover();
    },
    checkCarAvailability: function() {
      // Returns 'adr_rejected', 'unavailable', 'uninsurable', 'duration_invalid', 'stealth', or 
      // 'available'. Unless there is an unexpected error, in which case it returns 'error'.
      var self = this;
      if (this.model.has('pickup_date') && this.model.has('return_date')) {
        var availabilityStatus;

        $.ajax({
          type: 'GET',
          url: config.CHECK_AVAILABILITY_URL,
          data: {
            'start': converters.JSDateToISODateTimezoneNaive(this.model.get('pickup_date')),
            'end': converters.JSDateToISODateTimezoneNaive(this.model.get('return_date')),
            'car_name': this.car.car_name
          },
          dataType: 'json',
          success: function(data) {
            self.model.set('carAvailabilityStatus', data.update);
          },
          error: function(data) {
            self.model.set('carAvailabilityStatus', "error");
          },
          complete: function(data, status) {
            self.trigger('afterFetch');
          }
        });
      } else {
        self.model.unset('carAvailabilityStatus');
      }
    },
    forwardUserToCheckout: function() {
      var params = {
        'start': converters.JSDateToISODateTimezoneNaive(this.model.get('pickup_date')),
        'end': converters.JSDateToISODateTimezoneNaive(this.model.get('return_date')),
        'carid': this.car.carid
      };
      var checkoutUrl = "/checkout/signin?" + $.param(params);

      this.rulesDialogView.hide();
      this.user.login({'redirectURL': checkoutUrl});
    }
  });

  return CarProfileView;
});

