define('models/calendar',[
  'backbone',
  'config',
  'models/calendar_event',
  'utils/converters'
], function(
  Backbone,
  config,
  CalendarEvent,
  converters
){

  return Calendar = Backbone.Collection.extend({
    model: CalendarEvent,

    // Temp client-side fix for events passed from the server that start and end
    // at the same time.
    removeCorruptEvents: function () {
      _.chain(this.models).clone().each(function(model){
        if (model.getDurationInMS() <= 0) {
          model.destroy();
        }
      });
    },

    // Get events that start, end, or run through a day
    getEventsByDay: function(day) {
      var dayStartMS = day.getTime();
      var dayEndMS   = dayStartMS + (24 * 60 * 60 * 1000) - 1;

      var dayEvents = [];
      var eventStartMS;
      var eventEndMS;
      var startDate;
      var endDate;

      return this.filter(function(model) {
        startDate    = converters.ISODateToJSDate(model.get('start'));
        endDate      = converters.ISODateToJSDate(model.get('end'));
        eventStartMS = startDate.getTime();
        eventEndMS   = endDate.getTime();
        if ((eventStartMS >= dayStartMS && eventStartMS <= dayEndMS) ||
            (eventEndMS > dayStartMS && eventEndMS <= dayEndMS) || 
            (eventStartMS < dayStartMS && eventEndMS > dayEndMS)) {
          return true;              

        } else {
          return false; 
        }
      });
    },

    // FullCalendar jQuery plugin support
    // http://arshaw.com/fullcalendar/
    getFullCalendarEventsArray: function() {
      var eventsArray = [];
      this.each(function(model) {
        eventsArray.push(model.getFullCalendarEvent());
      });
      return eventsArray;
    },

    block: function(carID, startISO, endISO, widget) {
      return this._blockOrUnblock(carID, startISO, endISO, widget, 'block');
    },

    unblock: function(carID, startISO, endISO, widget) {
      return this._blockOrUnblock(carID, startISO, endISO, widget, 'unblock');
    },

    _blockOrUnblock: function(carID, startISO, endISO, widget, blockOrUnblock) {
      var self     = this;
      var url      = (blockOrUnblock === 'block') ? config.BLOCK_AVAILABILITY_URL: config.UNBLOCK_AVAILABILITY_URL;

      return $.ajax({
        url: url,
        type: 'POST',
        dataType: 'json',
        data: {
          'carid': carID,
          'start_time': startISO,
          'end_time': endISO,
          'widget': widget
        }})
        .done(function (data, textStatus, jqXHR) {
          if (data.result === 'success') {
            if (blockOrUnblock === 'block') {
              self.add({
                'type': 'UNAVAILABLE',
                'start': startISO,
                'end': endISO,
              });             
            } else if (blockOrUnblock === 'unblock') {
              self._trimBlocks(startISO, endISO);
            }
          } else {
            console.log('failure server returned ' + data.message);
          }
        })
        .fail(function(jqXHR, textStatus, errorThrown) {
          console.log('HTTP call failed ' + textStatus + ' ERROR: ' + errorThrown);
        });
    },

    // If any blocked periods overlap the recently unblocked time we trim them.
    _trimBlocks: function(startISO, endISO) {
      var self        = this;
      var trimStartMS = converters.ISODateToJSDate(startISO).getTime();
      var trimEndMS   = converters.ISODateToJSDate(endISO).getTime();
      var eventStartMS;
      var eventEndMS;

      _.chain(this.models).clone().each(function(model){
        if (model.get('type') === 'UNAVAILABLE') {          
          eventStartMS = converters.ISODateToJSDate(model.get('start')).getTime();
          eventEndMS   = converters.ISODateToJSDate(model.get('end')).getTime();

          if (trimStartMS <= eventStartMS && trimEndMS >= eventEndMS) {
            // Remove event
            model.destroy();
          } else if (trimStartMS <= eventStartMS &&
                    (trimEndMS > eventStartMS && trimEndMS <= eventEndMS)) {
            // Trim start of event
            model.set('start', endISO);
          } else if ((trimStartMS >= eventStartMS && trimStartMS < eventEndMS) &&
                      trimEndMS >= eventEndMS) {
            // Trim end of event
            model.set('end', startISO);
          } else if ((trimStartMS > eventStartMS && trimStartMS < eventEndMS) && 
                    (trimEndMS > eventStartMS && trimEndMS < eventEndMS)) {
            // Split event into two
            self.add({
              'type': 'UNAVAILABLE',
              'start': endISO,
              'end': model.get('end'),
            });      

            model.set('end', startISO);
          }
        }
      });      
    }

  });

});

