define('dashboard/progress_widget',[
  'jquery',
  'common',
  'common/validators',
  'libs/jquery.form',
  'utils/loader',
  'views/car/car_photo_uploader',  
], function(
  $,
  common,
  validators,
  form,
  loader,
  CarPhotoUploaderView
) {

    common.pageReady("dashboard-dashboard", function() {
      // ====================================================
      // = Initialize =
      // ====================================================
      this.carPhotoUploaderView = new CarPhotoUploaderView({
        el: '#dashboard-car-photo-uploader'
      });

      var carProgress = GA_ARGS["car_progress"];
      var currentCarProgress;
      var TOTAL_PANELS = 4;

      var carProgressArray = [];
      var i;
      for (i in carProgress){
        var panel = $('#car-' + carProgress[i][0]);
        var form = $('#' + carProgress[i][0] + '-form');
        carProgressArray[i] = {index: i,
          name: carProgress[i][0],
          panel: panel,
          form: form,
          complete: carProgress[i][1]};
      }

      currentCarProgress = carProgressArray[0];

      var carProgressDictionary = {};
      carProgressDictionary["description"] = 0;
      carProgressDictionary["photos"] = 1;
      carProgressDictionary["features"] = 2;
      carProgressDictionary["vin"] = 3;

      advancePanel();

      initializePanelForm( "description", {});

      initializePanelForm( "photos", {photo_msg: {uploadedPhotoCount: true}});

      initializePanelForm( "features", {features_fuel: {required: true}});

      initializePanelForm(
        "vin", {
          vin: {vinUS: true},
          license_plate: {
            asciiOnly: true,
            minlength: 2,
            noSpaces: true,
            required: true
          }
        }
      );

      $("#social-form-submit").click(function () {
        $('#progress-widget').slideUp('slow');
      });

      if (getNumComplete() == TOTAL_PANELS){
        $('#progress-widget').hide();
      }
      else {
        $('#progress-widget').slideDown("slow");
      }

      // ====================================================
      // = Helper methods =
      // ====================================================

      function getNumComplete(){
        var numPanelsComplete = 0;
        for (var i in carProgressArray) {
          if (carProgressArray[i].complete) {
            numPanelsComplete++;
          }
        }
        return numPanelsComplete;
      }

      function updateCarProgressComplete(panelName, complete){
        var index = carProgressDictionary[panelName];
        carProgressArray[index].complete = complete;
        carProgress[index][1] = complete;
      }

      function getNextCarProgress(){
        for (var i in carProgressArray) {
          if (!carProgressArray[i].complete) {
            return carProgressArray[i];
          }
        }
      }

      function advancePanel(){
        if (getNumComplete() != TOTAL_PANELS){
          var nextCarProgress = getNextCarProgress();
          currentCarProgress.panel.addClass("fade");
          nextCarProgress.panel.removeClass("fade");

          // Calculate and set width of progress bar.
          // If nothing is completed, we will set width to 7%.
          var percentComplete = (nextCarProgress.index * 100/TOTAL_PANELS);
          var newWidth = 7 + percentComplete;
          newWidth = (newWidth > 100)? 100: newWidth;

          $(".progress-meter-fill").width( "" + newWidth + "%");
          if (nextCarProgress.name == "description"){
            $(".progress-meter-percentage").text( "10%");
          } else{
            $(".progress-meter-percentage").text( "" + percentComplete + "%");
          }

        }
        else{
          currentCarProgress.panel.addClass("fade");
          $('#social').removeClass("fade");

          $(".progress-meter-fill").width("100%");
          $(".progress-meter-percentage").text("100%");
        }
      }

      // ====================================================
      // = Validation =
      // ====================================================

      function initializePanelForm(panelName, validationRules) {
        var index = carProgressDictionary[panelName];
        var panelForm = carProgressArray[index].form;

        var errorHandler = function(error, element) {
          if (element.is(":radio")){
            $("#fuel-validation-msg").append( error );
          }
          else{
            error.insertAfter(element);
          }
        };

        var submitHandler = function(form) {
          loader.setButtonSubmitting($('#' + panelName + '-form-submit'));
          var formData = panelForm.serialize();
          var formAction = panelForm.attr( "action" );
          $.ajax({
            type: "POST",
            data: formData,
            url: formAction,
            dataType: "json"
          });
          currentCarProgress = carProgressArray[carProgressDictionary[panelName]];
          updateCarProgressComplete(panelName, true);
          advancePanel();
          return false;
        };

        validators.validate(panelForm, validationRules, errorHandler, submitHandler);
      }

    });
  });
