// This monthly calendar uses the FullCalendar jQuery plugin:
// http://arshaw.com/fullcalendar/
define('views/car/monthly_calendar',[
  'jquery',
  'backbone',
  'config',
  'libs/fullcalendar.patched',
  'libs/handlebars',
  'libs/moment.min',
  'models/calendar',
  'utils/converters',
  'views/car/monthly_calendar_day_popup'
], function(
  $,
  Backbone,
  config,
  fullCalendar,
  handlebars,
  moment,
  Calendar,
  converters,
  MonthlyCalendarDayPopupView
){

  return MonthlyCalendarView = Backbone.View.extend({

    initialize: function() {
      this.currentDate            = new Date();
      this.currentMonth           = this.currentDate.getMonth();
      this.currentYear            = this.currentDate.getFullYear();
      this.firstDayOfCurrentMonth = new Date(this.currentYear, this.currentMonth, 1);

      // Cache jQuery objects
      this.$calendar = this.$el.find('.full-calendar');

      this.activate();
    },

    activate: function() {
      var self = this;

      this.listenTo(this.collection, 'add remove change', this.renderEvents);
      
      this.$calendar.fullCalendar({
        header: {
          left: 'prev,next today',
          center: '',
          right: 'title'
        },
        editable: false,
        selectable: false,
        
        // We set the css property 'pointer-events: none' on the event pills.
        // Clicks on the pill will bubble through... in most cases.
        //
        // IE 10 and below do not support the pointer-events property. We 
        // need to handle clicks on events and pass them through to the parent
        // day table cell.
        eventClick: function(event, jsEvent, view) {
          target = this;
          var $hiddenEls = $();

          while (target.tagName !== "TD"){
            $(target).hide();
            $hiddenEls = $hiddenEls.add(target);
            target = document.elementFromPoint(jsEvent.clientX, jsEvent.clientY);
          }
          var date = converters.ISODateToJSDate($(target).data('date'));
          $(target).click();
          $hiddenEls.show();
        },
        dayClick: function(day, isAllDay, event, view) {
          var dayPopupView = new MonthlyCalendarDayPopupView({
            collection: self.collection,
            carID: config.globals.car_id,
            day: day,
            selectedDayTableCell: $(this)
          });
          dayPopupView.show();
        },
        eventSources: [
          {
            events: this.collection.getFullCalendarEventsArray(),
            ignoreTimezone: true
          }
        ],
        viewDisplay: function( calendar ){
          // Fires when the user clicks Prev Month, Next Month, or Today buttons
          
          self.$el.find('.spinner-wrapper').remove();

          var dateIncrement = new Date(calendar.visStart);

          // If current month is displaying, disable the Previous Month button
          if( calendar.start.getTime() > self.firstDayOfCurrentMonth.getTime() ){
            self.$el.find('.fc-button-prev').removeClass( 'fc-state-disabled' );
          } else {
            self.$el.find('.fc-button-prev').addClass( 'fc-state-disabled' );
          }
          
          var ninetyDaysFuture = new Date(); // We increment by ninety days later.
          ninetyDaysFuture.setDate(self.currentDate.getDate() + 90);

          if (calendar.end.getTime() > ninetyDaysFuture) {
            self.$el.find('.fc-button-next').addClass( 'fc-state-disabled' );
          } else {
            self.$el.find('.fc-button-next').removeClass( 'fc-state-disabled' );
          }
        }
      });
    }, 

    renderEvents: function() {
      this.$calendar.fullCalendar('removeEvents');
      this.$calendar.fullCalendar('addEventSource', {
        events: this.collection.getFullCalendarEventsArray(),
        ignoreTimezone: true
      });
    }

  });
});

