define('views/search/result_list_item',[
  'jquery',
  'backbone',
  'config',
  'common/pricing',
  'utils/template_helpers',
  'text!templates/search/result_list_item.handlebars',
  'libs/handlebars'
], function(
  $,
  Backbone,
  config,
  pricing,
  templateHelpers,
  resultListItemHandlebars
  ){

  return ResultListItemView = Backbone.View.extend({
    template: Handlebars.compile(resultListItemHandlebars),
    events: {
      "click .car": "handleCarClick",
      "mouseenter .car": "handleCarEnter",
      "mouseleave .car": "handleCarLeave"
    },
    initialize: function() {
      this.rentalDuration = this.options.rentalDuration;
      this.searchUIState  = this.options.searchUIState;

      this.render();
    },
    render: function() {
      var displayHourlyPrice;
      var displayDailyPrice;
      var isRentalDurationSet;
      var priceForRentalDuration;

      if (this.rentalDuration > 0) {
        var carRates = {
          "price"      : parseFloat(this.model.get('price_hourly')),
          "day_price"  : parseFloat(this.model.get('price_daily')),
          "week_price" : parseFloat(this.model.get('price_weekly'))
        };
        priceForRentalDuration = pricing.getSubtotal(carRates, this.rentalDuration).price;
        displayHourlyPrice     = false;
        displayDailyPrice      = false;
        isRentalDurationSet    = true;
      } else {
        displayHourlyPrice  = (this.model.get('price_hourly') > 0);
        displayDailyPrice   = (this.model.get('price_daily') > 0);
        isRentalDurationSet = false;
      }

      this.$el.html(this.template({
        'car': this.model.toJSON(),
        'display_hourly_price': displayHourlyPrice,
        'display_daily_price': displayDailyPrice,
        'is_rental_duration_set': isRentalDurationSet,
        'price_for_rental_duration': priceForRentalDuration
      }));
      return this;
    },
    handleCarEnter: function(event) {
      if (this.searchUIState.get('isMapVisible')) {
        this.searchUIState.set('hoveredCarId', this.model.get('car_id'));
      }
    },
    handleCarLeave: function(event) {
      if (this.searchUIState.get('isMapVisible')) {
        this.searchUIState.unset('hoveredCarId');
      }
    },
    handleCarClick: function(event) {
      if (this.searchUIState.get('isMapVisible')) {
        this.searchUIState.set('selectedCarId', [this.model.get('car_id')]);
      } else {
        window.location.href = '/' + this.model.get('car_name') + '?carid=' +this.model.get('car_id');
      }
    }
  });

});
