define('views/search/results_map',[
  'jquery',
  'backbone',
  'config',
  'utils/maps',
  'views/search/map_card',
  'libs/infobox'
], function(
  $,
  Backbone,
  config,
  maps,
  MapCardView,
  InfoBoxModule
){

  var DEFAULT_ZOOM_LEVEL = 14;

  var ResultsMapView = Backbone.View.extend({
    initialize: function() {
      // Note: HTML is rendered in search.html
      this.activate();
    },
    activate: function(){
      var self = this;
      this.markers = {};
      this.positionMarker = {};
      this.mapCardView = new MapCardView({
        searchQuery: this.model
      });

      this.listenTo(this.model, 'change:searchLat change:searchLng', this.handleLocationChange);
      this.listenTo(this.model, 'change:mapCenter change:mapZoom', this.handleMapCenterChange);
      this.listenTo(this.model, 'change:mapZoom', this.handleMapZoomChange);
      this.listenTo(this.model, 'change:pickup_date change:return_date', this.handleDateChange);
      this.listenTo(this.model, 'change:selectedCarId', this.handleSelectedCarIdChange);
      this.listenTo(this.model, 'change:hoveredCarId', this.handleMarkerActiveChanged);
      this.listenTo(this.collection, 'reset', this.handleSearchResultsReset);

      this.listenTo(this.collection, 'reset', this.addCarsToMap);
      this.listenTo(this.collection, 'add', this.handleAddToCollection);
      this.listenTo(this.collection, 'remove', this.handleRemoveFromCollection);

      this.defaultMapOptions = {
        scrollwheel: true,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
        minZoom: 3,
        panControl: false,
        streetViewControl: false
      };
      var usersLocation = null;
      if (this.model.get('mapCenter')) {
        this.defaultMapOptions['center'] = this.latLngFromUrlValue(
          this.model.get('mapCenter')
        );
      }
      if (this.model.get('searchLat') && this.model.get('searchLng')) {
        usersLocation = new google.maps.LatLng(
          this.model.get('searchLat'), this.model.get('searchLng')
        );
        if (this.defaultMapOptions['center'] === undefined) {
          this.defaultMapOptions['center'] = usersLocation;
        }
      }
      if (this.model.get('mapZoom')) {
        this.defaultMapOptions['zoom'] = this.model.get('mapZoom');
      }

      this.map = new google.maps.Map(this.el, this.defaultMapOptions);

      if (usersLocation !== null) {
        this.setUserLocationMarker(usersLocation);
      }

      google.maps.event.addListenerOnce(self.map, 'idle', function() {
        self.updateSearchQuery();
      });
      google.maps.event.addListener(self.map, 'dragend', function() {
        self.updateSearchQuery();
      });
      google.maps.event.addListener(self.map, 'zoom_changed', function() {
        // zoom_changed event is firing on zoom changes and incorrectly firing on drag actions. If
        // the zoom didn't change but it was just a drag, we don't want to hide the map card.
        if (self.map.getZoom() !== self.model.get('mapZoom')) {
          self.model.unset('selectedCarId');
        }
        self.updateSearchQuery();
      });
      google.maps.event.addListener(self.map, 'click', function() {
        self.model.unset('selectedCarId');
      });

      InfoBoxModule.init();
      this.infoBox = new InfoBoxModule.InfoBox({
        content: self.mapCardView.el,
        disableAutoPan: false,
        maxWidth: 0,
        pixelOffset: new google.maps.Size(-166, -55),
        zIndex: null,
        closeBoxURL: "",
        infoBoxClearance: new google.maps.Size(20, 20),
        isHidden: false,
        pane: "floatPane",
        enableEventPropagation: false
      });

      // If cars already exist in collection, add to map.
      if (this.collection.length > 0) {
        this.addCarsToMap(this.collection);
      }

    },
    handleMapZoomChange: function (arg1, currentZoomLevel) {
      var prevZoomLevel = arg1.previous('mapZoom');

      if (prevZoomLevel >= config.ZOOM_THRESHOLD_MEASLES &&  currentZoomLevel < config.ZOOM_THRESHOLD_MEASLES) {
        this.updateMarkerIcons();
      } else if ( prevZoomLevel < config.ZOOM_THRESHOLD_MEASLES &&  currentZoomLevel >= config.ZOOM_THRESHOLD_MEASLES) {
        this.updateMarkerIcons();
      }
    },
    updateMarkerIcons: function () {
      var position;
      var carID;
      var marker;
      var type;
      var mapPinIsSmall = this.map.getZoom() < config.ZOOM_THRESHOLD_MEASLES;

      for (position in this.positionMarker) {
        // For-in loop goes over all properties including inherited properties
        // let's use only our own properties.
        if (this.positionMarker.hasOwnProperty(position)) {
          carID = this.positionMarker[position][0];
          marker = this.markers['car-' + carID];

          this.updateMarker(marker, marker.isInstant, mapPinIsSmall, marker.isMultiPin);
        }
      }
    },
    handleLocationChange: function() {
      var searchLat = this.model.get('searchLat');
      var searchLng = this.model.get('searchLng');
      var newUserLocation = new google.maps.LatLng(searchLat, searchLng);
      if (this.map.getCenter() === undefined) {
        this.map.setCenter(newUserLocation);

      }
      if (this.map.getZoom() === undefined) {
        this.map.setZoom(DEFAULT_ZOOM_LEVEL);
      }
      this.setUserLocationMarker(newUserLocation);
    },
    handleDateChange: function(searchQuery, date) {
      this.refreshMapCard();
    },
    handleSelectedCarIdChange: function(searchUIState, selectedCarId) {
      if (!selectedCarId || selectedCarId.length <= 0) {
        //If we don't have a selected car id hide the card and allow zoom scrolling
        this.hideMapCard();
        this.map.setOptions({scrollwheel: true});
      } else {
        this.map.setOptions({scrollwheel: false});
        this.showMapCard(selectedCarId);
      }
    },
    handleSearchResultsReset: function(searchResults) {
      var selectedCarId = this.model.get('selectedCarId');
      var cars          = searchResults.toJSON();

      // If selected car is not in the search results any more, hide the map card
      if (selectedCarId && selectedCarId.length > 0) {
        if (!_.findWhere(cars, {car_id: selectedCarId[0]})){
          this.model.unset('selectedCarId');
        }
      }
    },
    handleMarkerActiveChanged: function (searchUIState, hoveredCarId) {

      //Get the previous hoveredCarId and reset
      var prev = searchUIState.previous('hoveredCarId');
      var marker;
      if (prev) {
        marker = this.markers["car-" + prev];
        if (marker) {
          marker.setIcon(marker.defaultIcon);
        }
      }

      marker = this.markers["car-" + hoveredCarId];
      if (marker) {

        // Check if we have already cached the highlight, if not make a new marker
        if (marker.highlightIcon === undefined) {
          var type = maps.getMarkerTypeStatic(marker.isInstant, marker.isSmall, true, marker.isMultiPin);
          maps.setMarkerIcon(marker, type);
          marker.highlightIcon = marker.icon;
        } else {
          marker.setIcon(marker.highlightIcon);
        }
      }
    },
    updateMarker: function(marker, isInstant, isSmall, isMulti) {
      if ( marker.isSmall === isSmall &&
          marker.isMultiPin === isMulti &&
          marker.isInstant === isInstant ) {
        //This is the same pin, no need to update
        return;
      }

      var type = maps.getMarkerTypeStatic(isInstant, isSmall, false, isMulti);

      maps.setMarkerIcon(marker, type);
      marker.defaultIcon = marker.icon;
      marker.isMultiPin = isMulti;
      marker.isSmall = isSmall;
      delete marker.highlightIcon;

    },
    updateSearchQuery: function() {
      var viewport  = this.map.getBounds();
      var mapZoom   = this.map.getZoom();
      var mapCenter = this.map.getCenter();
      if (viewport !== undefined && mapZoom !== undefined &&
          mapCenter !== undefined) {
        // Sometimes on startup the map does not return valid bounds and or
        // center. Since this generally only happens at startup we just don't
        // set the values on the model.
        this.model.set({
          'viewport': viewport,
          'mapZoom': mapZoom,
          'mapCenter': mapCenter.toUrlValue()
        });
      }
    },
    handleAddToCollection: function(car) {
      this.addCarToMap(car, this);
    },
    handleRemoveFromCollection: function(car) {
      var carID = car.get('car_id');

      var carsAtLocation = this.positionMarker[this.getPositionHash(car)];
      var carsAtLocationLength = carsAtLocation.length;
      var carMarkerHash = this.getCarMarkerHash(carID);
      var i;
      var marker;

      if (carsAtLocationLength > 1) {
        for (i = carsAtLocationLength - 1; i >= 0; --i) {
          if (carsAtLocation[i] === carID) {
            carsAtLocation.splice(i, 1);
            delete(this.markers[carMarkerHash]);
            // carsAtLocationLength has one less now
            // since we just deleted one, don't need to waste a cycle to update though
            if (carsAtLocationLength === 2) {
              marker = this.markers['car-' + carsAtLocation[0]];
              this.updateMarker(marker, marker.isInstant, this.map.getZoom() < config.ZOOM_THRESHOLD_MEASLES, false);
            }
          }
        }
      } else if (carsAtLocationLength === 1) {
        this.markers[carMarkerHash].setMap(null);
        delete(this.markers[carMarkerHash]);
        delete(this.positionMarker[this.getPositionHash(car)]);
      }
    },
    getCarMarkerHash: function (carID) {
      return 'car-' + carID;
    },
    getPositionHash: function(car) {
      var lat = Math.round(car.get('latitude') * 10000) / 10000;
      var lgn = Math.round(car.get('longitude') * 10000) / 10000;
      return lat + "," + lgn;
    },
    addSinglePinMarker: function (car, type) {
      var self = this;
      var positionHash = self.getPositionHash(car);
      var newMarker =  maps.addMarker(type,
                                      new google.maps.LatLng(car.get('latitude'),
                                                             car.get('longitude')),
                                      this.map);

      google.maps.event.addListener(newMarker, 'click', function () {
        self.model.set('selectedCarId', self.positionMarker[positionHash]);
      });

      return newMarker;
    },
    addCarToMap: function(car, self) {
      var mapPinIsSmall = this.map.getZoom() < config.ZOOM_THRESHOLD_MEASLES;
      var carIsInstant = car.get('instant_rental');
      var type;
      var carID = car.get('car_id');
      var positionHash = this.getPositionHash(car);
      var hasPinAtLocation = false;
      var marker;

      //Check if we already have a marker at this position
      if (self.positionMarker[positionHash] === undefined) {
        self.positionMarker[positionHash] = [carID];
      } else {
        hasPinAtLocation = true;
        self.positionMarker[positionHash].push(carID);
        marker = self.markers['car-' + self.positionMarker[positionHash][0]];
        carIsInstant = carIsInstant || marker.isInstant;
      }

      type = maps.getMarkerTypeStatic(carIsInstant, mapPinIsSmall, false, hasPinAtLocation);


      // Check if we already have a pin at this location
      if (hasPinAtLocation) {
        if (marker && !marker.isMultiPin) {
          // If the marker icon is currently not a multi-pin, make it one
          this.updateMarker(marker, carIsInstant, mapPinIsSmall, true);
        }
      } else if (typeof self.markers['car-' + carID] === "undefined") {
        marker = this.addSinglePinMarker(car, type);
      }

      if (marker !== undefined) {
        // Add a default icon which is the same as the current icon
        // so that if we change the icon we can change it back
        marker.defaultIcon = marker.icon;
        // Set some state properties on the marker, to help us later
        marker.isInstant   = carIsInstant;
        marker.isMultiPin  = hasPinAtLocation;
        marker.isSmall     = mapPinIsSmall;

        this.markers['car-' + carID] = marker;
      }
    },
    addCarsToMap: function(cars) {

      var self = this;
      this.clearCache();

      cars.each(function(car) {
        self.addCarToMap(car, self);
      });
    },
    handleMapCenterChange: function(searchUIState) {
      if (searchUIState.get('mapCenter')) {
        this.map.setCenter(this.latLngFromUrlValue(searchUIState.get('mapCenter')));
      }
      if (!isNaN(searchUIState.get('mapZoom'))) {
         this.map.setZoom(searchUIState.get('mapZoom'));
      }
      this.model.set({'viewport': this.map.getBounds()});
    },
    setUserLocationMarker: function(usersLocation) {
      if (typeof this._usersLocationMarker === "undefined") {
        this._usersLocationMarker = maps.addMarker('user', usersLocation, this.map);
      }
      else {
        this._usersLocationMarker.setPosition(usersLocation);
      }
    },
    showMapCard: function (selectedCarIds) {
      var marker = this.markers['car-' + selectedCarIds[0]];
      var selectedCars = this.collection.select(function(car) {
        return _.contains(selectedCarIds, car.get('car_id'));
      });
      this.mapCardView.updateCar(selectedCars);

      // Position map card above marker. Markers (pins and measles) can be different sizes.
      this.infoBox.setOptions({
        pixelOffset: this.mapCardView.getPixelOffset(marker)
      });

      this.hideMapCard();
      this.infoBox.open(this.map, marker);
    },
    hideMapCard: function() {
      this.model.unset('selectedCar');
      this.infoBox.close();
    },
    refreshMapCard: function() {
      if (!_.isUndefined(this.infoBox)) {
        if (this.infoBox.getVisible()) {
          this.mapCardView.render();
        }
      }
    },
    clearCache: function() {
      var marker;

      for(marker in this.markers) {
        // For-in loop goes over all properties including inherited properties
        // let's use only our own properties.
        if (this.markers.hasOwnProperty(marker)){
          this.markers[marker].setMap(null);
        }
      }
      this.markers = {};
      this.positionMarker = {};
    },
    latLngFromUrlValue: function(urlValue) {
      if (typeof urlValue === "string") {
        var values = urlValue.split(",");
        if (values.length > 2) {
          return new google.maps.LatLngBounds(
            new google.maps.LatLng(Number(values[0]), Number(values[1])),
            new google.maps.LatLng(Number(values[2]), Number(values[3]))
          );
        }
        else {
          return new google.maps.LatLng(Number(values[0]), Number(values[1]));
        }
      }
      else {
        return undefined;
      }
    }
  });

  return ResultsMapView;
});
