define("webapp", ["jquery", "utils/loader"], function($, loader) {

  var webapp = {
    // TODO: Move code out of here and wipe out this file.
    // Create a Request Backbone model with an approve() method. On success, let the view
    // decide what to do, for example forward the user to request/[requestid]
    approve: function(bundleId, button) {
      if (button) {
        loader.setButtonSubmitting(button);
      }
      $.post("/ajax/approve", {"bundle_id": bundleId}, function(response) {
        if (response["Incomplete"]) {
          window.location = response["redirect_url"];
        } else if ( response["Failed!"] ) {
          alert("Approving the rental failed for the following reason: " + response['message']);
          if (button) {
            loader.clearButtonSubmitting(button);
          }
        } else {
          window.location = "/request/" + bundleId;
        }
      }, "json");
    }
  };
  return webapp;
});
