define('views/search/map_card',[
  'jquery',
  'backbone',
  'common/pricing',
  'utils/time',
  'utils/template_helpers',
  'text!templates/search/map_card.handlebars',
  'text!templates/search/map_multi_card.handlebars',
  'libs/handlebars'
], function(
  $,
  Backbone,
  pricing,
  time,
  templateHelpers,
  mapCardHandlebars,
  mapMultiCardHandlbars,
  HandlebarsModule
){

  var MapCardView = Backbone.View.extend({
    singleTemplate: Handlebars.compile(mapCardHandlebars),
    multiTemplate: Handlebars.compile(mapMultiCardHandlbars),


    initialize: function() {
      this.searchQuery    = this.options.searchQuery;
    },

    render: function() {
      var displayHourlyPrice;
      var displayDailyPrice;
      var isRentalDurationSet;
      var priceForRentalDuration;

      var pickupDate     = this.searchQuery.get('pickup_date');
      var returnDate     = this.searchQuery.get('return_date');
      var rentalDuration = time.calcDurationInHours(pickupDate, returnDate);
      var newHtml;
      var currentCar;
      var carData = [];

      for (var i = 0; i < this.model.length; i ++) {
        currentCar = this.model[i];
        if (rentalDuration > 0) {
          var carRates = {
            "price"      : parseFloat(currentCar.get('price_hourly')),
            "day_price"  : parseFloat(currentCar.get('price_daily')),
            "week_price" : parseFloat(currentCar.get('price_weekly'))
          };

          priceForRentalDuration   = pricing.getSubtotal(carRates, rentalDuration).price;
          displayHourlyPrice       = false;
          displayDailyPrice        = false;
          this.isRentalDurationSet = true;
        } else {
          displayHourlyPrice       = (currentCar.get('price_hourly') > 0);
          displayDailyPrice        = (currentCar.get('price_daily') > 0);
          this.isRentalDurationSet = false;
        }

        carData.push({
          'car': currentCar.toJSON(),
          'display_hourly_price': displayHourlyPrice,
          'display_daily_price': displayDailyPrice,
          'is_rental_duration_set': this.isRentalDurationSet,
          'price_for_rental_duration': priceForRentalDuration
        });

      }

      if (this.model.length > 1) {
        newHtml = this.multiTemplate({cars: carData});
      } else {
        newHtml = this.singleTemplate(carData[0]);
      }

      this.$el.html(newHtml);

      return this;
    },
    
    updateCar: function(car) {
      this.model = car;
      this.render();
    },

    getPixelOffset: function (marker) {
      var priceRowHeight = (this.isRentalDurationSet) ? 15: 0;

      if (this.model.length === 1) {
        return new google.maps.Size(-168, -270 - marker.icon.size.height + priceRowHeight);
      } else {
        var mapCardPadding       = 27;
        var carRowHeight         = 101;
        var infoBoxMysteryHeight = 13;
        // The max height of the map card is 3.5 times the height of a car row compnent.
        var rowMultiplier        = Math.min(this.model.length, 3.5); 
        return new google.maps.Size(-185, (rowMultiplier * -carRowHeight) - mapCardPadding - marker.icon.size.height - infoBoxMysteryHeight);
      }
    }
  });

  return MapCardView;
});

