define('views/shared/pickup_and_return_inputs',[
  'jquery',
  'underscore',
  'backbone',
  'config',
  'libs/handlebars',
  'libs/jquery.timepicker',
  'libs/jquery_ui',
  'libs/jstz',
  'text!templates/shared/pickup_and_return_inputs.handlebars',
  'utils/converters',
  'utils/time'
], function(
  $,
  _,
  Backbone,
  config,
  handlebars,
  jqueryTimepicker,
  jqueryDatepicker,
  jstz,
  pickupAndReturnHandlebars,
  converters,
  time
){

   // checkInputsOnTimeChange() will be called every X seconds
  var CHECK_INPUTS_POLLING_FREQUENCY_IN_SECONDS = 60;

  var PickupAndReturnInputsView = Backbone.View.extend({
    
    template: Handlebars.compile(pickupAndReturnHandlebars),
    
    defaults: {
      'startLabel': 'Pickup',
      'endLabel': 'Return',
      'isStartTimeInputSetOnInit': true,
      'isEndTimeInputSetOnInit': true
    },

    events: {
      "keyup .date-input": "handleDateInputKeyUp",
      "change .pickup-time-input": "handlePickupTimeChange",
      "change .return-time-input": "handleReturnTimeChange"
    },
    
    initialize: function() {
      this.options = _.defaults(this.options || {}, this.defaults);
      
      if (this.$el.length) {
        // If the element doesn't exist don't bother rendering.
        this.render();
        this.activate();
      }
    },
    
    render: function() {
      this.$el.html(this.template({
        'startLabel': this.options.startLabel,
        'endLabel': this.options.endLabel
      }));

      // Cache jquery objects
      this.$pickupDateInput = this.$el.find('.pickup-date-input');
      this.$pickupTimeInput = this.$el.find('.pickup-time-input');
      this.$returnDateInput = this.$el.find('.return-date-input');
      this.$returnTimeInput = this.$el.find('.return-time-input');
      this.$timezoneMessage = this.$el.find('.timezone-message');

      return this;
    },
    
    activate: function() {
      var self = this;

      this.listenTo(this.model, 'change:car_iana_timezone', this.checkTimezones);

      // Trigger on load to get UI setup
      this.checkTimezones();
      this.checkInputsOnTimeChange();

      // -----------
      // Date inputs
      // -----------

      // For Ops in Manila we need to allow days in the past to work around a timezone bug.
      // TODO(isho) Fix this the right way.
      var minDate = (config.globals.is_become_user) ? '-1': '0';

      this.$el.find('.date-input').datepicker({
        beforeShow: function() {
          self.addClearButtonToDatePicker(this);
        },
        dateFormat: 'mm/dd/y',
        minDate: minDate,
        maxDate: '+3M',
        onChangeMonthYear: function() {
          self.addClearButtonToDatePicker(this);
        },
        showAnim: '',
        showButtonPanel: true
      });
      this.$pickupDateInput.datepicker('option', {
        'onSelect': function(dateText, datePicker) { self.handlePickupDateOnSelect(); },
        'onClose': function(dateText, datePicker) { self.handlePickupDateOnClose(); }
      });
      this.$returnDateInput.datepicker('option', {
        'onSelect': function(dateText, datePicker) { self.handleReturnDateOnSelect(); },
        'onClose': function(dateText, datePicker) { self.handleReturnDateOnClose(); }
      });
      // -----------
      // Time inputs
      // -----------
      this.$el.find('.time-input').timepicker({
        'closeOnWindowScroll': false,
        'forceRoundTime': true,
        'maxTime': '11:45pm',
        'step': config.RENTAL_TIME_INTERVAL_IN_MINUTES,
        'scrollDefaultNow': true
      });

      // ---------------------------------
      // If pickup date and return date were passed in, set them on pickers and validate.

      if (this.model.get('pickup_date')) {
        this.$pickupDateInput.datepicker('setDate', this.model.get('pickup_date'));
        if (this.options.isStartTimeInputSetOnInit) {
          this.$pickupTimeInput.timepicker('setTime', this.model.get('pickup_date'));  
        }
      }

      if (this.model.get('return_date')) {
        this.$returnDateInput.datepicker('setDate', this.model.get('return_date'));
        if (this.options.isEndTimeInputSetOnInit) {
          this.$returnTimeInput.timepicker('setTime', this.model.get('return_date'));
        }
      }

      if (this.model.get('pickup_date') || this.model.get('return_date')) {
        setTimeout(function() {
          self.clearInvalidDatesAndTimes();
          self.disableInvalidOptions();
          self.validate();
        }, 100);
      }
    },
    
    handleDateInputKeyUp: function(event) {
      this.addClearButtonToDatePicker(event.target);
    },
    
    handlePickupDateOnSelect: function(dateText, datePicker) {
      this.disableInvalidPickupTimes();
      this.validate('pickupDate');
      this.$pickupTimeInput.focus();
      if (this.isPickupDateSyntaxValid() && !this.isReturnDateSyntaxValid()) {
        this.$returnDateInput.datepicker('option', 'defaultDate', this.$pickupDateInput.datepicker('getDate'));
      }
    },

    handlePickupDateOnClose: function(dateText, datePicker) {
      this.disableInvalidPickupTimes();
      this.validate('pickupDate');
      if (this.isPickupDateSyntaxValid() && !this.isReturnDateSyntaxValid()) {
        this.$returnDateInput.datepicker('option', 'defaultDate', this.$pickupDateInput.datepicker('getDate'));
      }
    },

    handlePickupTimeChange: function(event) {
      this.validate('pickupTime');
      this.$returnDateInput.focus();
    },

    handleReturnDateOnSelect: function(dateText, datePicker) {
      this.disableInvalidReturnTimes();
      this.validate('returnDate');
      this.$returnTimeInput.focus();
    },

    handleReturnDateOnClose: function(dateText, datePicker) {
      this.disableInvalidReturnTimes();
      this.validate('returnDate');
    },

    handleReturnTimeChange: function(event) {
      this.validate('returnTime');
      this.$returnTimeInput.blur();
    },

    // Replaces jQuery Datepicker button panel with a 'Clear dates' button
    addClearButtonToDatePicker: function(input) {
      var self = this;
      setTimeout(function() {
        var buttonPane = $(input)
          .datepicker("widget")
          .find(".ui-datepicker-buttonpane");

        var $clearButton = $("<a>", {
          text: "Clear dates",
          'class': "clear-button",
          click: function() {
            self.clearInputs();
            self.validate();
          }
        });
        buttonPane.html($clearButton);
      }, 1);
    },

    // We use timezone from the first car in the search results and compare it to the user's.
    // If the user's local timezone is different, display a message.
    checkTimezones: function() {
      var userTimezone = this.model.get('user_iana_timezone');
      var carTimezone  = this.model.get('car_iana_timezone');

      if (carTimezone) {
        if (carTimezone === userTimezone) {
          this.hideTimezoneMessage();
        } else {
          var timezoneLabel = converters.IANATimezoneToShortTimezone(carTimezone);
          this.showTimezoneMessage(timezoneLabel);
        }
      } else {
        this.hideTimezoneMessage();
      }
    },

    showTimezoneMessage: function(timezoneLabel) {
      this.$timezoneMessage.find('.timezone-abbreviation').text(timezoneLabel);

      if (!this.$timezoneMessage.is(':visible')) {
        this.$timezoneMessage.show();
        $.event.trigger('appLayoutChange');
      }
    },

    hideTimezoneMessage: function() {
      if (this.$timezoneMessage.is(':visible')) {
        this.$timezoneMessage.hide();
        $.event.trigger('appLayoutChange');
      }
    },
    
    // Runs every X seconds. X being the constant CHECK_INPUTS_POLLING_FREQUENCY_IN_SECONDS
    checkInputsOnTimeChange: function() {
      this.clearInvalidDatesAndTimes();
      this.disableInvalidOptions();
      this.checkInputsTimeout = setTimeout(this.checkInputsOnTimeChange.bind(this), CHECK_INPUTS_POLLING_FREQUENCY_IN_SECONDS * 1000);
    },

    // If the user has selected times that have now fallen behind the current time, clear them.
    clearInvalidDatesAndTimes: function() {
      var now   = new Date();
      var nowMS = now.getTime();

      if (this.isPickupDateSyntaxValid() && this.isPickupTimeSyntaxValid()) {
        var pickupDate = this.$pickupDateInput.datepicker("getDate");
        var pickupTime = this.$pickupTimeInput.timepicker("getTime");
        pickupDate.setHours(pickupTime.getHours());
        pickupDate.setMinutes(pickupTime.getMinutes());
        var pickupMS = pickupDate.getTime();
        // If they have chosen 6:15pm, we clear it if the time goes to 6:16pm. We don't clear it
        // at 6:15:01. We wait a minute, thus the +60000 MS.
        if (pickupMS < nowMS - 60000) {
          this.clearInputs();
          this.validate();
        }
      }
    },

    disableInvalidOptions: function() {
      this.disableInvalidDates();
      this.disableInvalidPickupTimes();
      this.disableInvalidReturnTimes();
    },

    disableInvalidDates: function() {
      if (!config.globals.is_become_user) {
        var minutesBeforeMidnight = time.getMinutesBeforeMidnight();

        if (!this.$pickupDateInput.is(":focus")) {
          if (minutesBeforeMidnight < config.RENTAL_TIME_INTERVAL_IN_MINUTES) {
            this.$pickupDateInput.datepicker('option', 'minDate', '+1');
          }
        }
        if (!this.$returnDateInput.is(":focus")) {
          if (minutesBeforeMidnight < config.RENTAL_TIME_INTERVAL_IN_MINUTES + config.MINIMUM_RENTAL_IN_MINUTES) {
            this.$returnDateInput.datepicker('option', 'minDate', '+1');
          }
        }
      }
    },

    // If current day is chosen as the pickup date then disable pickup times in the past.
    disableInvalidPickupTimes: function() {
      if (!config.globals.is_become_user) {
        if (this.isPickupDateSyntaxValid() && !this.$pickupTimeInput.is(":focus")) {

          var now       = new Date();
          var pickupDay = this.$pickupDateInput.datepicker("getDate");

          if (time.areDatesOnSameDay(pickupDay, now)) {
            var hours           = now.getHours();
            var mins            = now.getMinutes();
            var intervals       = Math.ceil(mins / config.RENTAL_TIME_INTERVAL_IN_MINUTES);
            var intervalsAnHour = (60 / config.RENTAL_TIME_INTERVAL_IN_MINUTES);
            hours = (intervals === intervalsAnHour)? hours + 1: hours;
            mins  = (intervals * config.RENTAL_TIME_INTERVAL_IN_MINUTES) % 60;
            this.$pickupTimeInput.timepicker('option', 'minTime', hours + ":" + mins);
          } else {
            this.$pickupTimeInput.timepicker('option', 'minTime', null);
          }
        }
      }
    },

    // If current day is chosen as the return date then disable return times that occur in less
    // than one hour and fifteen minutes
    disableInvalidReturnTimes: function() {
      if (!config.globals.is_become_user) {
        if (this.isReturnDateSyntaxValid() && !this.$returnTimeInput.is(":focus")) {
          var now       = new Date();
          var returnDay = this.$returnDateInput.datepicker("getDate");

          if (time.areDatesOnSameDay(returnDay, now)) {
            var minRentalTime   = new Date(now.getTime() + (config.MINIMUM_RENTAL_IN_MINUTES * 60000));
            var hours           = minRentalTime.getHours();
            var mins            = minRentalTime.getMinutes();
            var intervals       = Math.ceil(mins / config.RENTAL_TIME_INTERVAL_IN_MINUTES);
            var intervalsAnHour = (60 / config.RENTAL_TIME_INTERVAL_IN_MINUTES);
            hours = (intervals === intervalsAnHour)? hours + 1: hours;
            mins  = (intervals * config.RENTAL_TIME_INTERVAL_IN_MINUTES) % 60;
            this.$returnTimeInput.timepicker('option', 'minTime', hours + ":" + mins);
          } else {
            this.$returnTimeInput.timepicker('option', 'minTime', null);
          }
        }
      }
    },

    validate: function(changedInput) {
      var valid = true;
      var pickupDate;
      var returnDate;

      if(this.isSyntaxValid() === true) {
        pickupDate = this.$pickupDateInput.datepicker("getDate");
        returnDate = this.$returnDateInput.datepicker("getDate");

        var pickupTime = this.$pickupTimeInput.timepicker("getTime");
        var returnTime = this.$returnTimeInput.timepicker("getTime");

        pickupDate.setHours(pickupTime.getHours());
        pickupDate.setMinutes(pickupTime.getMinutes());
        returnDate.setHours(returnTime.getHours());
        returnDate.setMinutes(returnTime.getMinutes());
 
        var pickupMS = pickupDate.getTime();
        var returnMS = returnDate.getTime();

        // On pickup date or time change, if pickup datetime is after or less than one hour before
        // the return datetime then clear return date and time inputs.
        if ((changedInput === "pickupDate") || (changedInput === "pickupTime")) {
          if (returnMS - pickupMS < (config.MINIMUM_RENTAL_IN_MINUTES * 60000)){
            this.clearReturnInputs();
            valid = false;
          }
        }
        // On return date or time change, if pickup datetime is after or less than one hour before
        // the return datetime then clear pickup date and time inputs.
        if ((changedInput === "returnDate") || (changedInput === "returnTime")) {
          if (returnMS - pickupMS < (config.MINIMUM_RENTAL_IN_MINUTES * 60000)) {
            this.clearPickupInputs();
            valid = false;
          }
        }
      } else {
        valid = false;
      }

      this.model.set('isValid', valid);

      if (valid && _.isDate(pickupDate) && _.isDate(returnDate)){
        this.model.set({
          'pickup_date': pickupDate,
          'return_date': returnDate
        });
      } else {
        this.model.unset('pickup_date');
        this.model.unset('return_date');
      }
    },

    isSyntaxValid: function() {
      if (this.isPickupDateSyntaxValid() &&
          this.isPickupTimeSyntaxValid() &&
          this.isReturnDateSyntaxValid() &&
          this.isReturnTimeSyntaxValid()) {
        return true;
      } else {
        return false;
      }
    },

    // Check if pickup date is not empty and valid
    isPickupDateSyntaxValid: function() {
      this.$pickupDateInput.removeClass('error');
      if (this.$pickupDateInput.val() === "") {
        return false;
      } else {
        try {
          $.datepicker.parseDate('mm/dd/y', this.$pickupDateInput.val());
        } catch (e) {
          this.$pickupDateInput.addClass('error');
          return false;
        }
      }
      return true;
    },

    // Check if return date is not empty and valid
    isReturnDateSyntaxValid: function() {
      this.$returnDateInput.removeClass('error');
      if (this.$returnDateInput.val() === "") {
        return false;
      } else {
        try {
          $.datepicker.parseDate('mm/dd/y', this.$returnDateInput.val());
        } catch (e) {
          this.$returnDateInput.addClass('error');
          return false;
        }
      }
      return true;
    },

    // Check if pickup time is not empty and valid
    isPickupTimeSyntaxValid: function() {
      this.$pickupTimeInput.removeClass('error');
      if (this.$pickupTimeInput.val() === ""){
        return false;
      } else if (this.$pickupTimeInput.timepicker('getSecondsFromMidnight') === null) {
        this.$pickupTimeInput.addClass('error');
        return false;
      }
      return true;
    },
    
    // Check if return time is not empty and valid
    isReturnTimeSyntaxValid: function() {
      this.$returnTimeInput.removeClass('error');
      if (this.$returnTimeInput.val() === ""){
        return false;
      } else if (this.$returnTimeInput.timepicker('getSecondsFromMidnight') === null) {
        this.$returnTimeInput.addClass('error');
        return false;
      }
      return true;
    },

    clearPickupInputs: function() {
      this.$pickupDateInput
          .datepicker("hide")
          .datepicker("setDate", null);
      this.$pickupTimeInput.timepicker('setTime', null);
      this.$pickupTimeInput.timepicker('option', 'minTime', null);
    },

    clearReturnInputs: function() {
      this.$returnDateInput
          .datepicker("hide")
          .datepicker("setDate", null);
      this.$returnTimeInput.timepicker('setTime', null);
      this.$returnTimeInput.timepicker('option', 'minTime', null);
    },

    clearInputs: function() {
      this.clearPickupInputs();
      this.clearReturnInputs();
    },

    remove: function() {
      // Custom cleanup code
      clearTimeout(this.checkInputsTimeout);
      
      // Call the base class remove method 
      Backbone.View.prototype.remove.apply(this, arguments);
    }

  });

  return PickupAndReturnInputsView;
});

