define('common/error',[
    'jquery',
    'common',
    'libs/jquery.form'
], function($, common, jQueryForm) {

    common.pageReady("errors-500", function() {
        $('#error-report').focus();

        $('#error-report-button').click(function() {

            function formSuccess() {
                $('.submit-issue-form').slideUp('slow');
                $('.issue-submitted-msg').slideDown('slow');
            }
            var options = {
                success: formSuccess // post-submit callback
            };

            $('#error-report-form').ajaxSubmit(options);
        });
    });
});

