define('views/car/trip_subtotal',[
  'jquery',
  'backbone',
  'common/pricing',
  'utils/formatters',
  'text!templates/car/trip_subtotal.handlebars',
  'libs/handlebars'
], function($, Backbone, pricing, formatters, tripSubtotalHandlebars){

  var TripSubtotalView = Backbone.View.extend({
    template: Handlebars.compile(tripSubtotalHandlebars),
    initialize: function() {
      this.car = this.options.car;

      this.render();
      this.activate();
    },
    render: function() {
      if (typeof this.model.get('pickup_date') !== 'undefined' &&
          typeof this.model.get('return_date') !== 'undefined') {
        var pickupDate        = this.model.get('pickup_date');
        var returnDate        = this.model.get('return_date');
        var pickupMS          = pickupDate.getTime();
        var returnMS          = returnDate.getTime();
        var tripLengthInHours = (returnMS - pickupMS) / 3600000;
        var carRates = {
          "week_price" : parseFloat(this.car.weekprice),
          "day_price"  : parseFloat(this.car.dayprice ),
          "price"      : parseFloat(this.car.price )
        };
        var subtotal  = pricing.getSubtotal(carRates, tripLengthInHours);

        this.$el.html(this.template({
          'tripLength': subtotal['length'],
          'tripPrice': '$' + formatters.formatNumber(subtotal.price, 2)
        })).show();
      } else {
        this.$el.hide();
      }
      return this;
    },
    activate: function() {
      this.listenTo(this.model, 'change:pickup_date change:return_date', this.render);
    }
  });

  return TripSubtotalView;
});
