define('views/car/car_price_control',[
  'jquery',
  'backbone',
  'config',
  'libs/jquery_ui',
  'text!templates/car/car_price_control.html',
  'text!templates/car/car_price_rates.handlebars',
  'utils/formatters',
  'utils/template_helpers',
  'libs/bootstrap',
  'libs/jquery.ui.touch-punch',
  'libs/handlebars'
], function(
  $,
  Backbone,
  config,
  jQueryUI,
  carPriceControlHTML,
  carPriceRatesTemplate,
  formatters,
  templateHelpers,
  bootstrap,
  jqueryUITouchPunch
){

  var CarPriceControlView = Backbone.View.extend({
    events: {
      'change .disable-weekly-rate': 'handleDisableWeeklyRateChange'
    },

    initialize: function() {
      // Compile template that is used to display part of this view
      this.carPriceRatesHandlebars = Handlebars.compile(carPriceRatesTemplate);

      this.render();
      this.activate();
    },

    render: function() {
      // In this view  the rendering is split into 2 steps:
      //   1) Render the slider, checkbox, and help text.
      //   2) Render the prices. 
      //
      // We need to update the prices independently from the rest of the component. Ex. When the
      // user is dragging the slider we want to update the prices but not re-render the slider.
      
      // Rendering - Step 1
      this.$el.html(carPriceControlHTML);
      
      // Rendering - Step 2
      this.renderPrices(this.model.toJSON(), false);
      
      // Cache jQuery objects
      this.$slider             = this.$el.find('.car-price-slider');
      this.$weeklyRateCheckbox = this.$el.find('.disable-weekly-rate-checkbox');

      return this;
    },

    renderPrices: function(rates, disabledWeekly) {
      // build the context for the handlebars template
      var contextObj = rates;
      contextObj['disabledWeekly'] = disabledWeekly;
      this.$el.find('.prices').html(this.carPriceRatesHandlebars(contextObj));
    },

    activate: function() {
      var self = this;

      // Turn on slider
      this.$slider.slider({
        min: config.MIN_HOURLY_RATE,
        max: config.MAX_HOURLY_RATE,
        range: 'min',
        step: 0.5,
        value: this.model.get('hourly_rate'),
        
        slide: function(event, ui) {
          // Hourly rates greater than $10 are rounded to nearest $1 increment
          var rates = self.generateRates(ui.value);
          var disabledWeekly = self.$weeklyRateCheckbox.prop('checked');
          self.renderPrices(rates, disabledWeekly);
        },

        change: function(event, ui) {
          var rates                = self.generateRates(ui.value);
          var isWeeklyRateDisabled = self.$weeklyRateCheckbox.is(':checked');
          rates.weekly_rate        = (isWeeklyRateDisabled) ? 0 : rates.weekly_rate;
          self.model.set(rates);
        },
      });
      
      // Update Weekly rate checkbox
      if (parseInt(this.model.get('weekly_rate'), 10) === 0) {
        this.$weeklyRateCheckbox.prop('checked', true);
        this.disableWeeklyRate();
      } else {
        this.$weeklyRateCheckbox.prop('checked', false);
        this.enableWeeklyRate();
      }
      
      this.$el.find('.disable-weekly-rate').tooltip({
        placement: 'top'
      });

    },
    
    handleDisableWeeklyRateChange: function(event) {
      if (this.$weeklyRateCheckbox.is(':checked')) {
        this.disableWeeklyRate();
      } else {
        this.enableWeeklyRate();
      }
    },

    disableWeeklyRate: function() {
      this.$el.find('.weekly-price').addClass('disabled');
      this.model.set('weekly_rate', 0);
    },

    enableWeeklyRate: function() {
      this.$el.find('.weekly-price').removeClass('disabled');
      var rates = this.generateRates(this.$slider.slider('value'));
      this.model.set('weekly_rate', rates.weekly_rate);
    },

    generateRates: function(hourlyValue) {
      // Hourly rates greater than $10 are rounded to nearest $1 increment
      hourlyValue = Number(hourlyValue);
      hourlyValue = (hourlyValue > 10) ? Math.round(hourlyValue): hourlyValue;

      return {
        'hourly_rate': hourlyValue,
        'daily_rate': hourlyValue * config.globals.hourly_daily_ratio,
        'weekly_rate': hourlyValue * config.globals.hourly_daily_ratio * config.globals.daily_weekly_ratio
      };
    }
  });

  return CarPriceControlView;
});

