// This file contains validators that extend the jQuery Validation Plugin.
//
// More info:
// http://bassistance.de/jquery-plugins/jquery-plugin-validation/
// http://docs.jquery.com/Plugins/Validation
//
// Built in validation methods
// ---------------------------
// required
// remote: Requests a resource to check the element for validity.
// minlength
// maxlength
// rangelength: Makes the element require a given value range.
// min: Makes the element require a given minimum.
// max:  Makes the element require a given maximum.
// range: Makes the element require a given value range.
// email:  Makes the element require a valid email
// url: Makes the element require a valid url
// date: Makes the element require a date.
// dateISO: Makes the element require an ISO date.
// number: Makes the element require a decimal number.
// digits: Makes the element require digits only.
// creditcard: Makes the element require a credit card number.
// equalTo: Requires the element to be the same as another one
//
// Additional validator methods taken from
// http://ajax.aspnetcdn.com/ajax/jquery.validate/1.11.1/additional-methods.js 
// ---------------------------------------
// zipcodeUS
//
// Custom validators
// -------------------------
// carMileage
// carNameAvailable
// carNameLength
// carNameCharacters
// carYear
// leapDay
// noSpaces
// phone
// textareaMaxLength
// uploadedPhotoCount
// vinUS
// zipcodeMatchesGeocoded



define('common/validators',[
  'jquery',
  'libs/jquery.validate'
], function($, validate){

  $.validator.setDefaults({
    debug: true,
    success: "valid",
    ignore: ""
  });

  // ---------------------------------------------------------------------------
  // Additional validator methods taken from
  // http://ajax.aspnetcdn.com/ajax/jquery.validate/1.11.1/additional-methods.js 
  // ---------------------------------------------------------------------------

  jQuery.validator.addMethod("zipcodeUS", function(value, element) {
    return this.optional(element) || /\d{5}-\d{4}$|^\d{5}$/.test(value);
  }, "Please specify a valid US Zip code.");

  // ---------------------------------------------------------------------------
  // Custom validators
  // ---------------------------------------------------------------------------

  $.validator.addMethod("asciiOnly", function(value, element){
    var re = /^[\x00-\x7F]*$/;
      return re.test(value);
  }, "Only letters, numbers, and standard characters are allowed.");

  $.validator.addMethod("carMileage", function(value, element){
    mileage = $(element).val();
    return (mileage != "150000+");
  }, "Your car does not qualify for insurance reasons.");

  // Car name is available
  $.validator.addMethod("carNameAvailable", function(value, element) {
    return $(element).attr("validity") == "available";
  }, "Car name is taken");

  // Car name has valid characters
  $.validator.addMethod("carNameCharacters", function(value, element) {
      var re = /^[a-zA-Z0-9_\-\.]*$/;
      return re.test(value);
  }, "Car name can only contain letters, numbers, and . _ -");

  // Car name is at least 3 characters long
  $.validator.addMethod("carNameLength", function(value, element){
      return value.length >= 3 && value.length <= 25;
  }, "Car name must be at least 3 characters, and no more than 25 characters long.");

  // Car year
  $.validator.addMethod("carYear", function(value, element){
    year = parseInt($(element).val(), 10);
    return (year >= 1995);
  }, "Sorry, your car does not qualify for insurance reasons.");

  // Leap day
  $.validator.addMethod("leapDay", function(value, element){
    if ($("#dob-month").val() == "February" && $("#dob-day").val() == "29") {
      var year = parseInt(value, 10);
      if (year % 400 === 0) {
        return true;
      } else if (year % 100 === 0) {
        return false;
      } else if (year % 4 === 0) {
        return true;
      } else {
        return false;
      }
    } else {
      return true;
    }
  }, "February 29th only occurs in leap years.");

 
  $.validator.addMethod("noSpaces", function(value, element){
    return value.indexOf(" ") < 0;
  }, "No spaces please.");
 
  // US or international Phone number
  $.validator.addMethod("phone", function (phone, element){
    phone = phone.replace(/\D/g,'');
    return phone.length >= 10;
  }, "Please specify a valid phone number");

  // Textarea max length
  $.validator.addMethod("textareaMaxLength", function(value, element){
    return ($(element).val().length < 10000);
  }, "Too many characters.");

  // At least one photo in the car progress widget
  $.validator.addMethod("uploadedPhotoCount", function(value, element) {
    if ($("#image_upload_area .image").length > 0) {
      return true;
    }
    else {
      return false;
    }
  }, "Upload a photo to continue.");

  // vinUS
  $.validator.addMethod("vinUS", function(value, element){
    if (value.length > 0) {
      return (/^\w{17}$/.test(value));
    }
    else {
      return true;
    }
  }, "Please specify a valid Vehicle Identification Number.");

  //
  $.validator.addMethod("zipcodeMatchesGeocoded", function(value, element) {
    return $("#geocoded_zipcode").val() == value;
  }, "Please enter a valid address. Or drag the pin on the map below.");


  // ====================================================
  // = Validate =
  // ====================================================

  return{
    validate: function(form, rules, errorPlacement, submitHandler){

      if (errorPlacement){
        form.validate({
          rules: rules,
          errorPlacement: errorPlacement,
          submitHandler: submitHandler
        });
      }
      else{
        form.validate({
          rules: rules,
          submitHandler: submitHandler
        });
      }
    }
  };

});
