define('views/car/list_car',[
  'jquery',
  'backbone',
  'config',
  'common/validators',
  'libs/jquery.validate',
  'libs/jquery.form',
  'models/viewmodels/car_price',
  'utils/loader',
  'views/car/set_location_map',
  'views/car/car_price_control',
  'views/car/parking_inputs',
  'views/car/pickup_instructions_input',
  'views/shared/make_model_inputs'
], function(
  $,
  Backbone,
  config,
  validators,
  jQueryValidate,
  jQueryForm,
  CarPrice,
  loader,
  SetLocationMapView,
  CarPriceControlView,
  ParkingInputsView,
  PickupInstructionsInputView,
  MakeModelInputsView
) {

  var ListCarView = Backbone.View.extend({
    el: '#content',
    
    initialize: function() {
      // Setup models and collections
      this.carPrice = new CarPrice({
        'hourly_rate': config.globals.default_hourly_rate,
        'daily_rate': config.globals.default_daily_rate,
        'weekly_rate': config.globals.default_weekly_rate
      });

      // Render and turn on UI
      this.render();
      this.activate();
    },
    
    render: function() {
      // The following Backbone Views attach UI events to already existing html. They do not render
      // any content.
      this.setLocationMapView = new SetLocationMapView({
        el: '.set-location-map'
      });

      this.parkingInputsView = new ParkingInputsView({
        el: '#list-car-parking-inputs-view',
        model: this.model
      });

      this.pickupInstructionsInputView = new PickupInstructionsInputView({
        el: '#list-car-pickup-instructions-input-view',
        model: this.model
      });

      this.carPriceControlView = new CarPriceControlView({
        el: '#list-car-price-control',
        model: this.carPrice
      });

      this.makeModelInputsView = new MakeModelInputsView();

      return this;
    },
    
    activate: function() {
      var self = this;
      
      this.listenTo(this.carPrice, 'change', function(model) {
        // Update hidden inputs
        self.$el.find('.list-car-hourly-rate').val(model.get('hourly_rate'));
        self.$el.find('.list-car-daily-rate').val(model.get('daily_rate'));
        self.$el.find('.list-car-weekly-rate').val(model.get('weekly_rate'));
      });

      // --------------------------------------------------------
      // Code copied from our old listcar.js
      // --------------------------------------------------------

      $('#car_name').focus();
    
      // --------------------------
      // Check if car name is taken
      // --------------------------
      
      var car_name = $('#car_name');
      car_name.keyup(function () {

        if (carNameLength(this.value, this) && carNameRegex(this.value, this)) {
          if (this.value != this.lastValue) {
            if (this.timer) clearTimeout(this.timer);
            car_name.removeClass("processing available taken reserved");
            car_name.addClass('processing');

            this.timer = setTimeout(function () {
              $.ajax({
                url: "/ajax/car_name_lookup",
                data: {"car_name": car_name.val()},
                dataType: 'json',
                success: function (data) {
                  car_name.removeClass("processing available taken reserved");
                  car_name.addClass(data.response);
                  car_name.attr("validity", data.response);
                  car_name.valid();
                }
              });
            }, 200);
            this.lastValue = this.value;
          }
        }
        else {
          car_name.valid();
        }
      });

      // ----------
      // Validation
      // ----------

      $.validator.setDefaults({
        debug: true,
        success: "valid"
      });


      function carNameRegex(value, element) {
        var re = /^[a-zA-Z0-9_\-\.]*$/;
        return re.test(value);
      }
      function carNameLength(value, element){
          return value.length >= 3 && value.length <= 25;
        }

       $("#listcar-form").validate({
        rules: {
          make: {
            required: true
          },
          year: {
            required: true,
            carYear: true
          },
          model: {
            required: true
          },
          mileage: {
            required: true,
            carMileage: true
          },
          transmission: {
            required: true
          },
          color: {
            required: true,
            asciiOnly: true
          },
          license_state: {
            required: true
          },
          license_plate: {
            asciiOnly: true,
            minlength: 2,
            noSpaces: true
          },
          vin: {
            vinUS: true
          },
          street_address: {
            required: true,
            asciiOnly: true
          },
          zipcode: {
            required: true,
            zipcodeUS: true
          },
          pickup_description: {
            required: true,
            textareaMaxLength: true
          },
          car_name: {
            required: true,
            carNameCharacters: true,
            carNameLength: true,
            carNameAvailable: true
          },
          phone_number: {
            required: true,
            phone: true
          },
          weekly_trips: {
            required: true
          }
        },
        errorPlacement: function(error, element) {
          if (element.is(":radio")){
            $("#transmission").append( error );
          }
          else{
            error.insertAfter(element);
          }
        },
        submitHandler: function(form) {
          $("#zipcode").rules("add", {
            zipcodeMatchesGeocoded: true
          });
          if (!$(form).valid()) {
            $("#zipcode").rules("remove", "zipcodeMatchesGeocoded");
            return false;
          }
          else {
            $("#zipcode").rules("remove", "zipcodeMatchesGeocoded");
          }

          loader.setButtonSubmitting($('#listcar-button'));
          
          $(form).ajaxSubmit({
            success: function(responseText, statusText, xhr, form) {
              if (responseText == "OK") {
                window.dataLayer.push({'event': 'listcarSuccess'});
                window.setTimeout(function() {
                  window.location = form.attr("next");
                }, 200);
              }
              else if (responseText.indexOf("redirect_url=") === 0) {
                window.dataLayer.push({'event': 'listcarSuccess'});
                window.setTimeout(function() {
                  window.location = responseText.split("=")[1];
                }, 200);
              }
              else {
                $(".container-fluid.page").first().prepend(
                  "<div class='alert alert-block'><p class='message'>Server validation failed! "
                    + responseText +"</p></div>"
                );
                loader.clearButtonSubmitting($('#listcar-button'));
              }
            },
            error: function(responseText, statusText, xhr, form) {
              $(".container-fluid.page").first().prepend(
                "<div class='alert alert-block'><p class='message'>Server validation failed! "
                  + responseText.status + " " + statusText +"</p></div>"
              );
              loader.clearButtonSubmitting($('#listcar-button'));
              $("#zipcode").rules("remove", "zipcodeMatchesGeocoded");
            }
          });
        }
      });

      $('#listcar-button').click(function() {
        $("#listcar-form").submit();
        return false;
      });
    }
  });

  return ListCarView;

});

