define('models/viewmodels/search_cookie',[
  'jquery',
  'backbone',
  'config',
  'utils/converters',
  'libs/jquery.cookie'
], function($, Backbone, config, converters){

  var SearchCookie = Backbone.Model.extend({
    // A list of all the attributes that we store in the cookie.
    // For details on what each of these values is used for, see comments in search_ui_state.js
    STORED_KEYS: ['bodyType',
                  'instant',
                  'locationText',
                  'mapCenter',
                  'mapZoom',
                  'max_hourly_price',
                  'max_daily_price',
                  'max_weekly_price',
                  'pickup_date',
                  'return_date',
                  'searchLat',
                  'searchLng',
                  'transmission'],

    initialize: function() {
      $.cookie.json = true;
      this.fetch();
    },
    fetch: function() {
      var newCookieData = $.cookie(config.SEARCH_COOKIE_NAME);
      if (newCookieData) { // If cookieData is not undefined or null
        var self = this;
        this._operateOnAllKeys(function(key) {
          newCookieData.hasOwnProperty(key);
          var value = newCookieData[key];
          if (value !== undefined) {
            if (self._castingFunctions.hasOwnProperty(key)) {
              value = self._castingFunctions[key](value);
            }
            self.set(key, value);
          }
        });
      }
    },
    save: function() {
      // This saves to a cookie rather than a database.
      // eventually we should trigger the same events that backbone models do when they save().
      $.cookie(
        config.SEARCH_COOKIE_NAME,
        this.toJSON()
      );
    },
    updateCookieValues: function(jsonObject) {
      var self = this;
      this._operateOnAllKeys(function(key) {
        self.set(key, jsonObject[key]);
      });
      this.save();
    },
    _operateOnAllKeys: function(operation) {
      // Operate on all keys that are present. (not null or undefined.)
      for (var i=0; i < this.STORED_KEYS.length; i++) {
        var key = this.STORED_KEYS[i];
        operation(key);
      }
    },
    _operateOnAllKeysWithValues: function(operation) {
      var self = this;
      this._operateOnAllKeys(function(key) {
        if (self.has(key)) {
          operation(key, self.get(key));
        }
      });
    },
    _castingFunctions: {
      'instant': converters.castToBoolean,
      'mapZoom': converters.castToNumber,
      'max_hourly_price': converters.castToNumber,
      'max_daily_price': converters.castToNumber,
      'max_weekly_price': converters.castToNumber,
      'pickup_date': converters.castToDate,
      'return_date': converters.castToDate,
      'searchLat': converters.castToNumber,
      'searchLng': converters.castToNumber
    }
  });
  return SearchCookie;
});
