define('views/car/lock_unlock_buttons',[
  'jquery',
  'backbone',
  'libs/bootstrap',
  'views/car/lock_unlock_confirm_dialog',
  'utils/loader'
], function(
  $,
  Backbone,
  bootstrap,
  LockUnlockConfirmDialogView,
  loader
){

  return LockUnlockButtonsView = Backbone.View.extend({
    events: {
      'click .lock-btn': 'handleLockButtonClick',
      'click .unlock-btn': 'handleUnlockButtonClick'
    },

    initialize: function() {
      this.hasActiveRental          = this.options.hasActiveRental;
      this.isOwner                  = this.options.isOwner;
      this.isUserAllowedToAccessCar = this.options.isUserAllowedToAccessCar;
      this.rentalToken              = this.options.rentalToken;

      this.render();
      this.activate();
    },

    render: function() {
      // Buttons rendered in HAML

      this.lockUnlockConfirmDialogView = new LockUnlockConfirmDialogView({
        el: '#lock-unlock-confirm-dialog'
      });

    },

    activate: function() {
      var self = this;

      $(document).on('LOCK', function() {
        self.toggleLock('lock');
      });

      $(document).on('UNLOCK', function() {
        self.toggleLock('unlock');
      });
      
      if (!this.isUserAllowedToAccessCar) {
        this.$el.find('.tooltip-hover-helper').popover({
          placement: 'top',
          content: 'You can lock and unlock the car during your rental.',
          trigger: 'hover'
        });
      }

    },

    handleLockButtonClick: function(event) {
      if (this.hasActiveRental && this.isOwner) {
        this.lockUnlockConfirmDialogView.show('lock');
      } else {
        this.toggleLock('lock');
      }
    },

    handleUnlockButtonClick: function(event) {
      if (this.hasActiveRental && this.isOwner) {
        this.lockUnlockConfirmDialogView.show('unlock');
      } else {
        this.toggleLock('unlock');
      }
    },

    toggleLock: function(lockOrUnlock) {
      var loadingMessage = (lockOrUnlock === 'lock') ? "Locking": "Unlocking";

      var $progressDialog = loader.showProgressDialog(loadingMessage, {'classes': 'lock-progress-dialog'});
      $.ajax({
        type: 'POST',
        url: '/key/' + lockOrUnlock + '/' + this.model.get('car_name'),
        data: {token: this.rentalToken},
        success: function(data) {
          // On successful lock or unlock we switch out the contents of the progress dialog with a
          // green checkmark for a bit over a second before hiding the modal.
          var $modalDialog             = $progressDialog.find('.modal-dialog');
          var $modalBody               = $modalDialog.find('.modal-body');
          var modalBodyVerticalPadding = $modalBody.outerHeight() - $modalBody.height();
          $modalBody.css('height', ($modalDialog.height() - modalBodyVerticalPadding) + 'px');
          $progressDialog.addClass('success');

          window.setTimeout(function() {
            loader.hideProgressDialog($progressDialog);
          }, 1300);
        },
        error: function(data) {
          loader.showErrorDialog('There was an error ' + lockOrUnlock + 'ing the car. Please try again.');
        }
      });
    }

  });

});
