define('views/login/signup_dialog',[
  'jquery',
  'underscore',
  'backbone',
  'config',
  'libs/jquery.validate',
  'libs/jquery.form',
  'text!templates/login/signup_dialog.handlebars',
  'utils/loader',
  'utils/template_helpers',
  'libs/handlebars',
  'facebook'
], function(
  $,
  _,
  Backbone,
  config,
  jQueryValidate,
  jQueryForm,
  signupDialogHandlebars,
  loader,
  templateHelpers
) {

  return SignupDialogView = Backbone.View.extend({
    template: Handlebars.compile(signupDialogHandlebars),

    initialize: function() {
      this.isDialogCloseable  = (this.options.isDialogCloseable === false) ? false: true;
      this.redirectURL        = this.options.redirectURL;
      this.referringUser      = this.options.referringUser;
      this.promoCode          = this.options.promoCode;
      this.user               = this.options.user;
      this.showHaveCarToShare = _.isString(this.redirectURL) ? this.redirectURL.indexOf('/listcar') !== 0 : true;

      this.templateData       = {
        'countries': this.countriesList(),
        'defaultCountry': 'US',
        'isDialogCloseable': this.isDialogCloseable,
        'sources': this.sourcesList(),
        'user': {},
        'showHaveCarToShare': this.showHaveCarToShare
      }

      var self = this;

      this.user.callFacebook('/me', function(response) {
        if (!response || response.error) {
          console.warn('Unable to fetch user data from Facebook.');
        } else {
          self.templateData.user.facebookEmail = response.email || '';
          self.templateData.user.facebookFirstName = response.first_name || '';
          self.templateData.user.facebookLastName = response.last_name || '';
        }
        self.render();
      });
    },

    render: function() {
      // Wait for FB response, then render and activate
      var $body = $('body');

      $body.append(this.template(this.templateData));
      
      // Cache jQuery objects
      this.$el          = $body.find('.signup-dialog');
      this.$form        = $body.find('.signup-form');
      this.$firstName   = this.$el.find('.first-name-input');
      this.$lastName    = this.$el.find('.last-name-input');
      this.$email       = this.$el.find('.email-input');
      this.$country     = this.$el.find('.country-select');
      this.$zip         = this.$el.find('.zip-input');
      this.$source      = this.$el.find('.source-select');
      this.$sourceOther = this.$el.find('.source-other-input');
      this.$listCar     = this.$el.find('[name=list-car]');

      this.activate();

    },

    activate: function() {
      var self = this;
      
      this.$el.on('shown.bs.modal', function() {
        self.$firstName.focus();
        
        /**
         * When the Signup Dialog is opened on any page besides '/signup', we 
         * push a 'virtualPageview' event to Google Tag Manager.
         * 
         * In Google Tag Manager we listen to this event and do two things:
         * - Create a Google Analytics virtual pageview
         * - Fire Signup View conversion events for our ad networks
         */
        if (window.location.pathname !== '/signup') {
          dataLayer.push({
            'event':'virtualPageview',
            'pageview':'/signup'
          });
        }
      });

      this.$el.on('hidden.bs.modal', function() {
        self.user.trigger('login:cancel');
        self.remove();
      });

      this.show();

      // Attaches UI events to notification. HTML is rendered in haml.
      this.notificationView = new NotificationView();

      var $zipLabel = $('#zip-label');
      function adjustZipLabelWidth() {
        self.$zip.css("padding-left", $zipLabel.width() + 20);
      }

      // Country
      this.$country
        .on('change', function(e){
          if (self.$country.val() === 'US'){
            $zipLabel.text('ZIP');
            adjustZipLabelWidth();
            self.$zip.rules('add',{
              zipcodeUS: true
            });
          } else {
            $zipLabel.text('Postal code');
            adjustZipLabelWidth();
            self.$zip.rules('remove', 'zipcodeUS');
          }
          $('#postal-code').focus();
      });

      // ====================================================
      // = How did you hear about us? =
      // ====================================================
      this.$source.on('change', function( event ){
        if (self.$source.val() === "Other") {
          self.$sourceOther.show().focus();
        } else if (self.$source.val() === "Friend") {
          self.$sourceOther.hide();
        } else{
          self.$sourceOther.hide();
        }
      });

      // ====================================================
      // = Validation =
      // ====================================================
      $.validator.setDefaults({
        debug: true,
        success: "valid"
      });

      this.$form.validate({
        rules: {
          firstName: {
            required: true
          },
          lastName: {
            required: true
          },
          email: {
            required: true,
            email: true
          },          
          country: {
            required: true
          },
          postal_code: {
            required: true,
            zipcodeUS: true
          },
          'list-car': {
            required: true
          },
          source: {
            required: true
          }
        },
        errorPlacement: function(error, element) {
          if (element.is(':radio')){
            self.$el.find('.list-car-msg').append(error);
          }
          else{
            error.insertAfter(element);
          }
        },
        submitHandler: function(form) {
          loader.setButtonSubmitting($('#signup-button'));
          $.ajax({
            type: 'POST',
            url: '/ajax/signup',
            dataType: 'json',
            data: {
              'first_name': self.$firstName.val(),
              'last_name': self.$lastName.val(),
              'email': self.$email.val(),
              'country': self.$country.val(),
              'postal_code': self.$zip.val(),
              'source': self.$source.val(),
              'source_other': self.$sourceOther.val(),
              'have_car': self.showHaveCarToShare ? self.$el.find('[name=list-car]:checked').val() : "yes",
              'referring_user': self.referringUser
            },
            context: this,
            success: function(data) {
              window.dataLayer.push({'event': 'signupSuccess'}); // Signup conversion goal
              window.setTimeout(function() {
                if (self.redirectURL) {
                  self.user.trigger('login:success', {
                    redirectURL: self.redirectURL,
                    referringUser: self.referringUser,
                    promoCode: self.promoCode
                  });
                }
                else if (self.$el.find('[name=list-car]:checked').val() === 'yes') {
                  self.user.trigger('login:success', {redirectURL: '/listcar'});
                }
                else {
                  self.user.trigger('login:success', {redirectURL: '/search'});
                }
              }, 200);
            },
            error: function(jqXHR, textStatus, errorThrown) {
              console.warn("/ajax/signup error: " + textStatus);
            }
          });
        }
      });
    },

    show: function() {
      if (this.isDialogCloseable) {
        this.$el.modal();
      } else {
        this.$el.modal({
          backdrop: 'static',
          keyboard: false
        });
      }
    },

    hide: function() {
      this.$el.modal('hide');
    },

    countriesList: function() {
      var countryPairs = _.sortBy(_.pairs(config.globals.countries), function(pair) {
        return pair[1];
      });
      return _.map(countryPairs, function(pair) {
        return {
          "short": pair[0],
          "long": pair[1]
        };
      });

    },

    sourcesList: function() {
      var sourcePairs = _.sortBy(_.pairs(config.globals.signup_sources), function(pair) {
        if (pair[0] === "Other") {
          return 1.0;
        }
        else {
          return Math.random();
        }
      });
      return _.map(sourcePairs, function(pair) {
        return {
          "source_key": pair[1],
          "source": pair[0]
        };
      });
    }

  });
});

