define('utils/formatters',[
  'libs/moment.min'
], function() {

  return {

    // formatNumber examples:
    // - formatNumber(1.20000) returns 1.2
    // - formatNumber(1.299, 2) returns 1.30 <-- price example
    formatNumber: function(num, decimalPlaces){
      // Convert string to number
      num = Number(num);

      // Set decimal places
      if(typeof decimalPlaces !== "undefined"){
        num = num.toFixed(decimalPlaces);
      }

      return num;
    },

    // Takes a Date object and returns a formatted string.
    // ex. "Sep 26, 2:00pm"
    formatDate: function(date){
      return moment(date).format("MMM D, h:mma");
    },

    formatWeekDayDate: function(date) {
      return moment(date).format("ddd M/D");
    },

    formatWeekRange: function(start, end) {
      var first;
      var second;

      if (start.getMonth() == end.getMonth()) {
        first  = moment(start).format("MMM D");
        second = moment(end).format("D YYYY");
      }
      else if (start.getFullYear() == end.getFullYear()){
        first  = moment(start).format("MMM D");
        second = moment(end).format("MMM D YYYY");
      }
      else {
        first  = moment(start).format("MMM D YYYY");
        second = moment(end).format("MMM D YYYY");
      }
      return first + " &mdash; " + second;
    }

  };

});
