define('views/car/car_edit_location',[
  'jquery',
  'backbone',
  'utils/loader',
  'views/car/set_location_map',
  'views/car/parking_inputs',
  'views/car/pickup_instructions_input',
  'libs/jquery.form',
  'libs/jquery.validate'  
], function(
  $,
  Backbone,
  loader,
  SetLocationMapView,
  ParkingInputsView,
  PickupInstructionsInputView,
  jQueryForm,  
  jQueryValidate  
){

  return CarEditLocationView = Backbone.View.extend({
  
    initialize: function() {
      this.render();
      this.activate();
    },

    render: function() {
      this.setLocationMapView = new SetLocationMapView({
        el: '.set-location-map'
      });
    
      this.parkingInputsView = new ParkingInputsView({
        el: '#car-edit-parking-inputs-view',
        model: this.model
      });

      this.pickupInstructionsInputView = new PickupInstructionsInputView({
        el: '#car-edit-pickup-instructions-input-view',
        model: this.model
      });

      this.$form       = this.$el.find('.location-form');
      this.$saveButton = this.$form.find('.save-button');

      return this;
    },
    
    activate: function() {
      var self = this;
      
      this.listenTo(this.model, 'change:parking-category', function(model) {
        self.updateParkingValidationRules();
      });

      this.$form.validate({
        errorPlacement : function(error, element) {
          if (element.is(":radio")) {
            error.appendTo(element.closest('.control-group'));
          } else {
            error.insertAfter(element);
          }
        },
        rules: {
          pickup_description: {
            required: true
          }
        },
        submitHandler: function(form) {
          loader.setButtonSubmitting(self.$saveButton);
          self.$form.ajaxSubmit().data('jqxhr')
            .done(function(data, status, xhr) {
              if (data !== 'OK') {
                loader.showErrorDialog('There was an error savings your changes. Please reload the page to try again.');
              }
            })
            .fail(function(jqXHR, textStatus, errorThrown) {
              loader.showErrorDialog('There was an error savings your changes. Please reload the page to try again.');
            })
            .always(function(xhr, status) {
              loader.clearButtonSubmitting(self.$saveButton);
            });  
        }
      });

      this.updateParkingValidationRules();
    },

    updateParkingValidationRules: function() {
      var category      = this.model.get('parking-category');
      var reserved      = this.model.get('parking-reserved');
      var specialAccess = this.model.get('parking-special-access');
      var limitedAccess = this.model.get('parking-limited-access');

      this.$form.find('[name=parking-reserved]').rules('remove');
      this.$form.find('[name=parking-special-access]').rules('remove');
      this.$form.find('[name=parking-limited-access]').rules('remove');

      if (category === 'garage' || category === 'surface_lot' || category === 'driveway') {
        this.$form.find('[name=parking-reserved]').rules('add',{
          required: true
        });
        this.$form.find('[name=parking-special-access]').rules('add',{
          required: true
        });
      }
      if (category === 'garage' || category === 'surface_lot' || category === 'valet') {
        this.$form.find('[name=parking-limited-access]').rules('add',{
          required: true
        });
      }
    },

    resizeMap: function() {
      this.setLocationMapView.triggerResize();
    }
    
  });

});

