define('views/car/car_edit',[
  'jquery',
  'underscore',
  'backbone',
  'config',
  'common/validators',
  'libs/jquery.form',
  'models/calendar',
  'models/viewmodels/car_price',
  'views/car/car_edit_calendar',
  'views/car/car_edit_connect',
  'views/car/car_edit_details',
  'views/car/car_edit_listing',
  'views/car/car_edit_location',
  'views/car/car_edit_photos',
  'views/car/car_edit_pricing',
  'views/shared/notification'
], function(
  $,
  _,
  Backbone,
  config,
  validators,
  jQueryForm,
  Calendar,
  CarPrice,
  CarEditCalendarView,
  CarEditConnectView,
  CarEditDetailsView,
  CarEditListingView,
  CarEditLocationView,  
  CarEditPhotosView,
  CarEditPricingView,
  NotificationView
) {

  var CarEditView = Backbone.View.extend({
    el: '#content',

    events: {
      'click .content-nav .nav-item': 'handleContentNavClick',
      'click .back-link': 'handleBackLinkClick'
    },

    initialize: function() {
      // Setup models and collections
      this.carID          = config.globals.car_id;
      this.weeklySchedule = config.globals.weekly_schedule;
      this.calendar       = new Calendar(config.globals.monthly_calendar_events);
      this.calendar.removeCorruptEvents();

      this.carPrice = new CarPrice({
        'hourly_rate': config.globals.hourly_rate,
        'daily_rate': config.globals.daily_rate,
        'weekly_rate': config.globals.weekly_rate
      });

      // Size based on media query checkpoints in variable.sass
      this.mobileViewMaxWidth = 767;

      // Render and turn on UI
      this.render();
      this.activate();
    },
   
    render: function() {
      // The following Backbone Views attach UI events to already existing html. They do not render
      // any content.
      this.notificationView = new NotificationView();
      
      this.$nav      = this.$el.find('.content-nav') ;
      this.$navItems = this.$nav.find('.nav-item');

      return this;
    },

    activate: function() {
      var self = this;

      this.model.set('isMobileView', this.isMobileView());

      $(document).on('appLayoutChange', function() {
        self.handleLayoutChange();
      });


      // Load a section
      if (window.location.hash) {
        this.showSection(window.location.hash.substring(1));
      } else if (!this.model.get('isMobileView')) {
        this.showSection('calendar');
      }
    },
    
    handleContentNavClick: function(event) {
      this.showSection($(event.target).data('target-section'));
    },

    handleBackLinkClick: function(event) {
      this.updateNav();
      this.$el.find('.edit-car').removeClass('hide-nav');
    },

    handleLayoutChange: function() {
      this.model.set('isMobileView', this.isMobileView());
      if (this.model.get('isMobileView')) {
        this.$el.find('.edit-car').addClass('hide-nav');
      } else {
        this.$el.find('.edit-car').removeClass('hide-nav');
      }
    },

    isMobileView: function() {
      return ($(window).width() <= this.mobileViewMaxWidth);
    },

    updateNav : function(section) {
      this.$navItems.removeClass('active');
      this.$navItems.filter('[data-target-section="' + section + '"]').addClass('active');
    },

    showSection: function(section) {
      this.updateNav(section);
      if (this.model.get('isMobileView')) {
        this.$el.find('.edit-car').addClass('hide-nav');
      }

      // Show section and initialize view
      this.$el.find('.edit-car-section').hide();
      this.$el.find('.section-' + section).show();
      window.scrollTo(0,0);

      switch(section) {
        case 'calendar':
          if (_.isUndefined(this.carEditCalendarView)) {
            this.carEditCalendarView = new CarEditCalendarView({
              el: '.section-calendar',
              carID: this.carID,
              calendar: this.calendar,
              weeklySchedule: this.weeklySchedule
            });
          }
          break;
        case 'pricing':
          if (_.isUndefined(this.carEditPricingView)) {
            this.carEditPricingView = new CarEditPricingView({
              el: '.section-pricing',
              model: this.carPrice
            });
          }
          break;
        case 'location':
          if (_.isUndefined(this.carEditLocationView)) {
            this.carEditLocationView = new CarEditLocationView({
              el: '.section-location',
              model: this.model
            });
          } else {
            // If the users loads the Location section, switches sections,
            // resizes their browser, and then switches back to the Location 
            // section it will cause a display issue with the Google Map in the
            // Location section. This is because the Google Map is unable to 
            // size itself properly during the browser resize as it doesn't have
            // width or height when it is set to display: none.
            //
            // To fix this we trigger a resize event on the map when we re-show
            // this section.
            this.carEditLocationView.resizeMap();
          }
          break;
        case 'photos':
          if (_.isUndefined(this.carEditPhotosView)) {
            this.carEditPhotosView = new CarEditPhotosView({
              el: '.section-photos'
            });
          }
          break;
        case 'connect':
          if (_.isUndefined(this.carEditConnectView)) {
            this.carEditConnectView = new CarEditConnectView({
              el: '.section-connect',
            });
          }
          break;
        case 'details':
          if (_.isUndefined(this.carEditDetailsView)) {
            this.carEditDetailsView = new CarEditDetailsView({
              el: '.section-details',
            });
          }
          break;
        case 'listing':
          if (_.isUndefined(this.carEditListingView)) {
            this.carEditListingView = new CarEditListingView({
              el: '.section-listing'
            });
          }
          break;
      }

    }
  });

  return CarEditView;
});

