define('views/shared/places_autocomplete_input',[
  'jquery',
  'backbone',
  'config',
  'utils/maps',
  'libs/bootstrap',
  'libs/handlebars'
], function(
  $,
  Backbone,
  config,
  maps,
  bootstrap
){

  var PlacesAutocompleteInputView = Backbone.View.extend({
    events: {
      'click .locate-icon': 'handleLocateIconClick'
    },

    initialize: function() {
      this.showLocateIcon = (this.options.showLocateIcon === false) ? false: true;

      // Renderd in HAML

      var self = this;
      maps.onScriptLoad(function() {
        self.activate();
      });
    },

    activate: function() {
      var self = this;
      this.$locationInput = this.$el.find('.location-input');


      if (Modernizr.geolocation && this.showLocateIcon) {
        this.$el.find('.locate-icon').tooltip({
          placement: "right"
        });
      }

      var placesAutocomplete = new google.maps.places.Autocomplete(self.$locationInput[0], {
        types: ['geocode'],
        componentRestrictions: {country: config.PLACES_AUTOCOMPLETE_COUNTRIES}
      });

      // place_changed event fires when the user presses enter in the places autocomplete input
      // or chooses an option from the autocomplete dropdown list
      google.maps.event.addListener(placesAutocomplete, 'place_changed', function() {

        self.model.set('locationText', self.$locationInput.val());

        // If location was chosen via the dropdown, we can simply use built in getPlace()
        var place = placesAutocomplete.getPlace();
        if (place.geometry) {
          self.model.set('location', place);
        } else {
          // If user pressed enter, we grab the first item from autocomplete dropdown and use
          // the Google Maps Geocoder to return a place object.
          var firstResult = $(".pac-container .pac-item:first").text();
          var geocoder    = new google.maps.Geocoder();

          geocoder.geocode({"address": firstResult }, function(results, status) {
            if (status === google.maps.GeocoderStatus.OK) {
              if (results[0].geometry){
                self.$locationInput.val(firstResult);
                self.model.set({'location': results[0]});
              }
            }
          });
        }
      });

      if (this.model.get('locationText')) {
        this.$locationInput.val(this.model.get('locationText'));
      }
    },
    handleLocateIconClick: function(event) {
      this.locateUser();
    },
    locateUser: function() {
      var self = this;

      navigator.geolocation.getCurrentPosition(
        function(position){
          var latLng   = new google.maps.LatLng(position.coords.latitude, position.coords.longitude);
          var geocoder = new google.maps.Geocoder();

          geocoder.geocode({"latLng": latLng}, function(results, status) {
            if (status === google.maps.GeocoderStatus.OK) {
              if (results[0].geometry) {
                self.$locationInput.val('Current location');
                self.model.set({
                  'location': results[0],
                  'locationText': ''
                });
              }
            }
          });

        },
        function(){
          // Error getting user location
          console.log('Error using HTML5 geolocation to get current position.');
        }
      );
    }
  });

  return PlacesAutocompleteInputView;
});
