define('trips/trips',[
  'jquery',
  'common',
  'webapp',
  'utils/maps'
], function(
  $,
  common,
  webapp,
  maps
) {

  common.pageReady("trips-.*", function(){

    // ==================================
    // Initialize Pickup Instructions Map
    // ==================================
    maps.onScriptLoad(function(){
      if(GA_ARGS['pickup_location']) {
        addMarkerLL(GA_ARGS['pickup_location'], false, true, false);
        resizeMap();
        panTo(0);
      }
    }, true);

    // ===================
    // = Submit feedback =
    // ===================
    $(".star").hover(
      function() {
        var index = $(this).attr("i");
        for (i=1; i <=index; i++) {
          if (!$(".star[i=" + i + "]").hasClass("selected")) {
            $(".star[i=" + i + "]").addClass("hover");
          }
        }
      },
      function() {
        var index = $(this).attr("i");
        for (i=1; i <=index; i++) {
          $(".star[i=" + i + "]").removeClass("hover");
        }
      }
    );

    $(".star").on('click', function(event){
      $(".star").removeClass("hover").removeClass("selected");
      var index = $(this).attr("i");
      for (i=1; i <=index; i++) {
        $(".star[i=" + i + "]").addClass("selected");
      }
      $("#rating").val(index);
      // $("#rating").valid();
    });
  });
});
