define('views/car/car_photo_uploader',[
  'jquery',
  'backbone',
  'libs/jquery_ui',
  'config'
], function(
  $,
  Backbone,
  jQueryUI,
  config
) {

  return CarPhotoUploaderView = Backbone.View.extend({
    initialize: function() {
      this.activate();
    },

    activate: function() {
      var self = this;
      // ========================
      // = Image uploader specific
      // ========================
      // Add the sorting behavior
      $("#image_upload_area .remove").hide();
      if (typeof($().sortable) !== "undefined") {
        $("#image_upload_area").sortable({
          "items": ".image",
          "scroll": false
        });
        $("#image_upload_area").bind("sortstop", function(event, ui) {
          var image_order = [];
          $("#image_upload_area .image").each(function() {
            if (typeof($(this).find(".remove").attr("uid")) !== "undefined") {
              image_order.push($(this).find(".remove").attr("uid"));
            }
          });
          $("#image_order").val(image_order.join(","));
          self.trigger('photos:reorder');
        });
      }

      $(document).on('mouseenter', '#image_upload_area .image', function() {
        if ($("#image_upload_area .image").length > 1) {
          $(this).find(".remove").show();
        }
      });
      $(document).on('mouseleave', '#image_upload_area .image', function() {
        $(this).find(".remove").hide();
      });
      $(document).on('click', '#image_upload_area .remove', function() {
        if ($("#image_upload_area .image").length > 1) {
          var image = $(this).parent(".image");

          $(image).find(".remove").remove();
          $(image).find(".image_cover").show();

          $.get("/listcar/upload", {id: $(this).attr("uid"), action: "remove"}, function(response) {
            if(response == "OK") {
              $(image).remove();
              if ($("#image_upload_area .image").length == (config.MAX_CAR_PROFILE_PHOTOS - 1)) {
                $('#image_upload').show();
              }
            }
          });
        }
      });
      $("#image_upload_button").load(function() {
        $("#image_upload_button").get(0).contentWindow.$("#file").bind("uploading_started", function() {
          $("#image_upload_cover").show();
        });
      });

      function upload(response) {
        var image_id = response["id"];
        var url = "/images_server/blob/" + image_id;
        var new_image = $('<div class="image"><div class="image_cover"></div></div>');
        $("#image_upload_area #image_upload").before(new_image);
        if ($("#image_upload_area .image").length == config.MAX_CAR_PROFILE_PHOTOS) {
          $("#image_upload").hide();
        }

        $.get("/listcar/upload", {id: image_id, action: "add"}, function(response) {

          $("#image_upload_area #image_upload_cover").hide();

          if(response == "OK") {
            $(new_image).append('<img width="175px" height="130px" src="'+url+'/175x130"><div class=remove  uid="' + image_id + '">x</div>');
            $(new_image).find(".image_cover").hide();
            if (typeof($().sortable) !== "undefined") {
              $("#image_upload_area").sortable("refresh");
            }
            $("#image_upload_area .remove").hide();
            // $("#photo_msg").valid();
          } else {
            $(new_image).remove();
            if ($("#image_upload_area .image").length < config.MAX_CAR_PROFILE_PHOTOS) {
              $("#image_upload").hide();
            }
          }
        });
      }
      window.upload = upload;
    }
  });

});

