define('views/car/lock_unlock_confirm_dialog',[
  'jquery',
  'backbone',
  'text!templates/car/lock_unlock_confirm_dialog.handlebars',
  'libs/handlebars',
  'utils/template_helpers'
], function(
  $,
  Backbone,
  lockUnlockConfirmDialogHandlebars,
  handlebars,
  templateHelpers
){

  return LockUnlockConfirmDialogView = Backbone.View.extend({
    template: Handlebars.compile(lockUnlockConfirmDialogHandlebars),

    events: {
      'click .btn-lock':   'handleLockButtonClick',
      'click .btn-unlock':   'handleUnlockButtonClick'
    },

    handleLockButtonClick: function(event) {
      this.$el.trigger('LOCK');
      this.hide();
    },

    handleUnlockButtonClick: function(event) {
      this.$el.trigger('UNLOCK');
      this.hide();
    },


    show: function(lockOrUnlock) {
      if (_.isUndefined(lockOrUnlock)) {
        console.warn('You must specify "lock" or "unlock" in the show method.')
      }
      
      this.$el.html(this.template({
        'lockOrUnlock': lockOrUnlock
      }));

      this.$modal = this.$el.find('.modal').modal();
      this.$modal.modal();
    },

    hide: function() {
      this.$modal.modal('hide');
    }
  });

});
