define('checkout',[
  'jquery',
  'underscore',
  'common',
  'config',
  'utils/formatters',
  'utils/loader',
  'libs/jquery.validate'
], function(
  $,
  _,
  common,
  config,
  formatters,
  loader,
  jQueryValidate
) {

    var animationSpeed = 300;
    var INITIAL_PENDING_DELAY = 250; // 250 milliseconds
    var MAX_PENDING_DELAY = 8000;    // 8 seconds

    common.pageReady("checkout-pending", function() {

      var pendingUrl = "/checkout/pending?rid=" + GA_ARGS['rid'];
      if (GA_ARGS['first-checkout']) {
        pendingUrl += "&first-checkout=" + GA_ARGS['first-checkout'];
      }
      var isPosting = false;
      var timeoutId;

      function setPostTimeout(delay) {
        timeoutId = setTimeout(function() {
          if (isPosting === false) {
            isPosting = true;
            $.post(pendingUrl, function(data) {
              isPosting = false;
              if (data.status == "error") {
                // reload page to get error message.
                window.location.reload();
                clearTimeout(timeoutId);
              }
              else if (data.status == "active" ||
                       data.status == "approved" ||
                       data.status == "instant") {
                window.location.replace(data.url);
                clearTimeout(timeoutId);
              }
            }, "json");
          }
          setPostTimeout(Math.min(delay*2, MAX_PENDING_DELAY));
        }, delay);
      }
      setPostTimeout(INITIAL_PENDING_DELAY);
    });

    common.pageReady(["checkout-payment", "checkout-driverinfo", "checkout-checkout"], function() {

      // =============================
      // = Utility: Map ids to names =
      // =============================
      $("form input:text[secure!=\"true\"], form select[secure!=\"true\"]").each(function() {
        $(this).attr("name", this.id);
      });

      // ====================
      // = Payment (Step 2) =
      // ====================
      $("#savePaymentMethod").click(function() {
        window.add_card_and_submit(function() {
          // since we handle advancing from payment page specially here, need to also handle the
          // checkout query params specially, including experiment impressions
          loader.setButtonSubmitting($("#savePaymentMethod"));
          var params = window.location.search.split("?")[1].split("&");
          var resultingQueryParams = "?";
          for (var i = 0; i < params.length; i++) {
            if (params[i].indexOf("impression") == -1) {
              resultingQueryParams += params[i];
              // only add "&" if there are more params left
              if (i < params.length - 1) {
                resultingQueryParams += "&";
              }
            }
          }
          // Small delay to make sure spinner is injected into DOM
          setTimeout(function() {
            if (config.globals.page_order === "pdc") {
              // pdc means checkout page order is payment -> driverinfo -> confirm
              window.location.assign("/checkout/driverinfo" + resultingQueryParams);
            } else {
              window.location.assign("/checkout/confirm" + resultingQueryParams);
            }
          }, 50);

        });
      });

      // ========================
      // = Driver Info (Step 3) =
      // ========================
      $("#checkoutform").validate({
        errorElement: "span",
        groups: {"age": "dob-year dob-month dob-day"},
        messages: {
          "license-firstname": "Required.",
          "license-lastname": "Required.",
          "dob-year": {
            "required": "These fields are required.",
            "leapDay": "February 29th only occurs in leap years."
          },
          "dob-month": "These fields are required.",
          "dob-day": "These fields are required."
        },
        errorPlacement: function(error, element) {
          if ($.inArray( $(element).attr("id"), ["dob-year", "dob-month", "dob-day"]) != -1) {
            error.insertAfter($("#dob-year"));
          } else {
            error.insertAfter(element);
          }
        },
        submitHandler: function(form) {
          $("#error-hook").removeClass("error-header").empty();

          loader.setButtonSubmitting($('#continue-button'));
          setTimeout(function() {
            form.submit();
          }, 50);
          
        },
        invalidHandler: function(form, validator) {
          window.scrollTo(0, 0);
          $("#error-hook").addClass("error-header").text("We couldn't understand your response. Please verify fields highlighted in red.");
          loader.clearButtonSubmitting($('#continue-button'));
        }
      });

      $("#dob-month").change(function() {
        $("#dob-day").find("option").remove().end().append('<option value="">Day</option>');
        var num_days = 31;
        if ($("#dob-month").val() == "February") {
          num_days = 29;
        } else if ($.inArray( $("#dob-month").val(), ["April", "June", "September", "November"]) != -1) {
          num_days = 30;
        }
        for (i=1;i<=num_days;i++) {
          var day = ("0" + i).slice(-2);
          $("#dob-day").append('<option value="'+day+'">'+day+'</option>');
        }
      });

      $("#dob-day").bind("change blur", function() {
        if ($("#dob-year").val() !== "") {
          $("#dob_year").valid();
        }
      });

      $(".remove-default").change(function() {
        $(this).children("[value='']").remove();
      });

      $("#continue-button").click(function(event) {
        $('#checkoutform').submit();
      });

      // DriverInfo page hack to deal with server-side validated phone numbers
      if (GA_ARGS["invalid-phone-no"]) {
        $("#error-hook").addClass("error-header").text(
            "The phone number you entered: " + GA_ARGS["invalid-phone-no"] +
            " doesn't appear to be a real number. Please check for typos " +
            "and correct them.");
        $("#checkoutform").validate().showErrors({"phone-no": "Please specify a valid phone number"});
        $("#phone-no").addClass("error");
      }

      // ====================
      // = Confirm (Step 4) =
      // ====================

      $('#btn-confirm-checkout').on('click', function(event) {
        event.preventDefault();

        //If the user has entered a coupon code without applying it, try to apply it
        if($("#coupon-code").val() !== "" && $("#applied-coupon").val() === "") {
          apply_coupon_code();
        } else {

          // Add request reason as a hidden input to form
          var rentalPurpose = $("#rental-purpose").val();

          $('#rental-purpose-hidden-input').remove();

          $('<input>').attr({
              id: 'rental-purpose-hidden-input',
              type:  'hidden',
              name:  'rental-purpose',
              value: rentalPurpose
            }).appendTo( $("#confirm-request") );


          // If user has not agreed to our latest Terms of Service we pop up a dialog that the
          // user must agree to before continuing through checkout.
          if(config.globals.click_wrap_terms) {
            $('#terms-agreement-dialog').modal();
          } else {
            submitConfirm();
          }
        }

      });

      var coupon_code_initial_width = $("#coupon-code").css("width");
      var coupon_code_update = (function() {
        if($(this).val().length > 0) {
          $("#coupon button").fadeIn(animationSpeed);
        } else {
          $("#coupon button").fadeOut(animationSpeed);
          $("#error-hook").hide();
        }
      });
      $("#coupon-code").keyup(coupon_code_update);


      var clear_applied_coupon = function() {
        // clear out any previously applied coupon code
        var rental_fee = parseFloat($("#rental-fee .val").attr("price"));
        var booking_fee = parseFloat($("#booking-fee .val").attr("price"));
        var discount = parseFloat($("#discount .val").attr("price"));
        var tax = 0.0;

        $("#discount .val").text("-$" + 0.0);
        $("#discount").hide();
        $("#error-hook").hide();
        $("#apply-coupon").show(animationSpeed);
        $("#clear-coupon").hide(animationSpeed);
        $("#total .val").text(
          "$" + formatters.formatNumber( rental_fee + booking_fee + tax - discount, 2 )
        );
        $("#discount .val").attr("applied", "no");
        $("#applied-coupon").val("");
      };

      window.clear_applied_coupon = clear_applied_coupon;
      // apply coupon code
      var apply_coupon_code = function() {

        if($("#discount .val").attr("applied") == "yes") {
          clear_applied_coupon();
        }

        var coupon_code = $("#coupon-code").val();
        $.get('/ajax/get-coupon-amount', {
          'coupon': coupon_code.toLowerCase(),
          'price': $("#rental-fee .dollars").attr("price")
        }, function(result) {
          if(!result['valid']) {
            $("#error-hook").show().addClass("error-header").text(
              result['reason']
            );
            window.scroll(0,0);

          } else if(!result['coupon_cleared']) {
            var rental_fee  = parseFloat($("#rental-fee .val").attr("price"));
            var booking_fee = parseFloat($("#booking-fee .val").attr("price"));
            var discount    = result.discount;
            var tax         = 0.0;

            if ($("#tax").length > 0) {
              tax = $("#tax .val");
            }

            var totalPrice = "$" + formatters.formatNumber( rental_fee + booking_fee + tax - discount, 2);

            $('.total-hold').text(totalPrice);
            $("#total .val").text(totalPrice);

            $("#discount .val").text("-$" + formatters.formatNumber( discount, 2 ));
            $("#discount").show();
            $("#error-hook").hide();
            $("#apply-coupon").hide(animationSpeed);
            $("#clear-coupon").show(animationSpeed);
            $("#discount .val").attr("applied", "yes");
            $("#applied-coupon").val(coupon_code);
          }
        }, 'json');
      };

      $("#coupon button").click(apply_coupon_code);
      $("#coupon-code").keyup(function(e) {
        if((e.keyCode ? e.keyCode : e.which)==13) {
          apply_coupon_code();
        }
      });

      var update_hidden_form_element = function() {
        $("#rental-message").attr("value", $(this).val());
      };
      $("#requestcomment").change(
        update_hidden_form_element
      ).keyup(
        update_hidden_form_element
      ).keydown(update_hidden_form_element);

      if (_.isString(GA_ARGS.promo_coupon)) {
        $("#coupon-code").val(GA_ARGS.promo_coupon.toUpperCase());
        $("#coupon button").show();
        apply_coupon_code();
      }

      // Events for Terms agreement dialog
      var $termsDialog = $('#terms-agreement-dialog');
      $termsDialog.on('click', '.cancel-btn', function(event) {
        $('#terms-agreement-dialog').modal('hide');
      });

      $termsDialog.on('click', '.confirm-btn', function(event) {
        var button  = event.target;
        loader.setButtonSubmitting(button);

        $('<input>').attr({
          id: 'agreed-to-terms-purpose-hidden-input',
          type:  'hidden',
          name:  'agreed_to_terms',
          value: 'true'
        }).appendTo( $("#confirm-request") );

        submitConfirm();
      });

      var submitConfirm = function () {
        loader.showPageLoader($('#btn-confirm-checkout'));
        setTimeout(function() {
          $("#confirm-request").submit();
        }, 50);
        
      };

    });

});

