define('views/user/user_edit',[
  'jquery',
  'backbone',
  'config',
  'views/shared/notification'
  ], function(
    $,
    Backbone,
    config,
    NotificationView
  ) {

  return UserEditView = Backbone.View.extend({
    el: '#content',

    initialize: function() {
      // HTML is rendered in HAML
     
      this.activate();
    }, 

    activate: function() {
      // Attaches UI events to notification. HTML is rendered in haml.
      this.notificationView = new NotificationView();

      $("#location #street_address").removeClass("required");

      $("#image_upload_button").load(function() {
        $("#image_upload_button").get(0).contentWindow.$("#file").bind(
          "uploading_started",
          function() {
            $("#userPhoto .image_cover").show();
          });
      });

      function upload(response) {
        //called when image upload finishes
        $("#userPhoto .image_cover").hide();
        //$("#alreadyFacebookPhoto").hide();
        var photo = $("#userPhoto .photo");
        var image_id = response["id"];
        var url = "/images_server/blob/" + image_id + "/225x225";
        //tell the server to change the photo
        $.get("/change-photo", {"photo-id": image_id}, function(response) {
          if(response == "OK") {
            photo.removeAttr("width");
            photo.removeAttr("height");
            photo.attr("src", url);
          }
        });
      }

      window.upload = upload;


      function show_sms_state(id) {
        $(".if_sms_unverified").hide();
        $(".if_sms_pending").hide();
        $(".if_sms_verified").hide();
        $(".if_sms_other").hide();
        $(".if_sms_error").hide();
        $(".if_sms_" + id).show();
      }


      // Helper function to poll for verification completion
      function poll_for_verification(phone_no){
        window.setTimeout(function () {
          check_sms_verification(phone_no);
        }, 1000);
      }

      // Called when the sms verify button is clicked.  Handles initial
      // errors, such as the phone number being verified already by someone
      // else.
      function start_sms_verification(phone_no) {
        $.post("/ajax/verify-sms", {"phone-number": phone_no}, function (response) {
          if (response === "OK") {
            show_sms_state('pending');
            poll_for_verification(phone_no);
          }
          else {
            if (response === "already_verified") {
              // page is out of date for some reason, but we're verified now
              show_sms_state("verified");
            } else if (response === "already_pending") {
              // page is out of date, but we're now in a pending state
            } else if (response === "other_verified" || response === "other_unverified" || response === "other_pending") {
              // someone else already verified (or is verifying) this phone number
              show_sms_state("other");
              poll_for_verification(phone_no);
            } else {
              show_sms_state('error');
            }
          }
        });
      }

      function check_sms_verification(phone_no) {
        $.get("/ajax/verify-sms", function(response) {
          if(phone_no != $("#phone_no").val()) {
            //ignore, because verify-sms response applies to a different phone number
          } else if(response == "unverified") {
            //the verification stage has expired
            show_sms_state("unverified");
          } else if(response == "pending") {
            //keep waiting
            window.setTimeout(function() {
              check_sms_verification(phone_no);
            }, 1000);
          } else if(response == "verified") {
            verified_phone_no = phone_no;
            $("#sms_settings option:selected").removeAttr('selected');
            $("#sms_settings option[value=on]").attr('selected', 'selected');
            show_sms_state("verified");
          } else {
            show_sms_state("error");
            throw new Error("Bad server SMS response: " + response);
          }
        }).error(function() {
          show_sms_state("error");
        });
      }

      var verified_phone_no;
      if (config.globals.isSMSVerified) {
        verified_phone_no = config.globals.phone_no;
      } else {
        check_sms_verification($("#phone_no").val());
      }

      $("#verify_sms_button").click(function() {
        if($("#phone_no").valid()) {
          show_sms_state("pending");
          var pn = $("#phone_no").val();
          //phone number will be changed so no longer verified
          verified_phone_no = undefined;
          start_sms_verification(pn);
        }
      });


      function stripPhoneNumber(pn) {
        if(typeof pn != "string") return pn;
        return pn.replace(/[^0-9]/g, '');
      }

      $("#phone_no").keyup(function() {

        var pn = $("#phone_no").val();
        if(stripPhoneNumber(pn) == stripPhoneNumber(verified_phone_no)) {
          show_sms_state("verified");
        } else {
          show_sms_state("unverified");
        }
      });
    }
  });
});

