define('views/car/rules_dialog',[
  'jquery',
  'backbone',
  'text!templates/car/rules_dialog.handlebars',
  'utils/loader',
  'libs/handlebars'
], function($, Backbone, rulesDialogHandlebars, loader){

  var RulesDialog = Backbone.View.extend({    
    template: Handlebars.compile(rulesDialogHandlebars),
    
    events: {
      'click .agree-button': 'handleAgreeButtonClick'
    },
    
    initialize: function() {
      this.car = this.options.car;      
      this.render();
      this.activate();
    },
    
    render: function() {
      var showManualTransmissionMessage = (this.car.transmission === 'manual');
      var showPremiumGasMessage         = (this.car.fuel_type === 'premium');
      var showDieselMessage             = (this.car.fuel_type === 'diesel');
      var showElectricMessage           = (this.car.fuel_type === 'electric');
      var showPetFriendlyMessage        = (this.car.pet_friendly);

      this.$el.html(this.template({
        'showManualTransmissionMessage': showManualTransmissionMessage,
        'showPremiumGasMessage': showPremiumGasMessage,
        'showDieselMessage': showDieselMessage,
        'showElectricMessage': showElectricMessage,
        'showPetFriendlyMessage': showPetFriendlyMessage
      }));

      // cache jQuery objects
      this.$modal = this.$el.find('.modal');

      return this;
    },

    activate: function() {
      var self = this;
      this.$modal.on('hidden.bs.modal', function() {
        loader.clearButtonSubmitting(self.$el.find('.agree-button'));
      });
    },

    handleAgreeButtonClick: function(event) {
      loader.setButtonSubmitting($(event.target));
      this.$el.trigger('FORWARD_USER_TO_CHECKOUT');
    },

    show: function() {
      this.$modal.modal();
    },

    hide: function(event) {
      this.$modal.modal('hide');
    }

  });

  return RulesDialog;
});
