define('views/search/results_list',[
  'jquery',
  'backbone',
  'utils/time',
  'views/search/result_list_item',
  'text!templates/search/empty_results_list.handlebars',
  'libs/handlebars'
], function(
  $,
  Backbone,
  time,
  ResultListItemView,
  emptyResultsListHandlebars
){

  var CAR_IMAGE_HEIGHT = 80;

  var ResultsListView = Backbone.View.extend({
    template: Handlebars.compile(emptyResultsListHandlebars),
    events: {
      'scroll': 'handleListScroll',
      'click .reset-filters-link': 'handleResetFiltersLinkClick'
    },
    initialize: function() {
      // If the SearchResultsCollection is empty we display an 'empty results list' template and
      // if it is not empty we display each search result with a ResultListItemView.
      //
      // When this view is initially rendered it is unlikely the SearchResultsCollection
      // fetch will have completed so the collection will be empty. This will cause the 'empty
      // results list' template to flash before the results are rendered. To prevent this unwanted
      // flash we don't display the 'empty results list' template on the initial render. The next
      // render of this view will occur when the SearchResultsCollection fetch completes.
      //
      // We check hasFirstRenderOccurred in render() before displaying the 'empty' template.
      this.hasFirstRenderOccurred = false;

      //scrollListenerEnabled holds the state if the window scroll listener is enabled
      this.isScrollListenerEnabled = false;

      this.render();
      this.activate();
    },
    render: function() {
      var self           = this;
      var pickupDate     = this.model.get('pickup_date');
      var returnDate     = this.model.get('return_date');
      var rentalDuration = time.calcDurationInHours(pickupDate, returnDate);
      var fragment       = document.createDocumentFragment();

      if (this.collection.length > 0) {
        this.collection.each(function(car) {
          var listItem = new ResultListItemView({
            model: car,
            rentalDuration: rentalDuration,
            searchUIState: self.model
          });
          fragment.appendChild(listItem.el);
        });
        this.$el.html($(fragment).clone(true));
      } else {
        if (this.hasFirstRenderOccurred) {
          var showCheckFiltersMessage    = false;
          var showNoResultsInAreaMessage = false;
         
          if (this.model.areAnyFiltersSet()) {
            showCheckFiltersMessage = true;
          } else {
            showNoResultsInAreaMessage = true;
          }

          this.$el.html(this.template({
            'isMapVisible': this.model.get('isMapVisible'),
            'showCheckFiltersMessage': showCheckFiltersMessage,
            'showNoResultsInAreaMessage': showNoResultsInAreaMessage
          }));
        }
      }

      this.hasFirstRenderOccurred = true;

      this.$el.scrollTop(0);

      // Load images for the first 10 cars. The rest of the images are loaded when they are
      // scrolled into view.
      this.$el.find('.car-image').slice(0, 10).each(function(index, carImage) {
        var $carImage = $(carImage);
        $carImage.attr('src', $carImage.data('src'));
      });

      return this;
    },
    activate: function() {
      this.listenTo(this.collection, 'beforeFetch', this.handleCarsBeforeFetch);
      this.listenTo(this.collection, 'afterFetch', this.handleCarsAfterFetch);
      this.listenTo(this.collection, 'reset', this.render);
      this.listenTo(this.collection, 'afterFetch', this.enableScrollListener);
      this.listenTo(this.model, 'change:selectedCarId', this.handleSelectedCarIdChange);

      _.bindAll(this, 'handleListScroll');
      this.enableScrollListener();

    },
    enableScrollListener: function () {
      if(!this.isScrollListenerEnabled) {
        // bind to window
        $(window).on('scroll', this.handleListScroll);
        this.isScrollListenerEnabled = true;
      }
    },
    disableScrollListener: function () {
      $(window).off('scroll', this.handleListScroll);
      this.isScrollListenerEnabled = false;
    },
    // Scrolling is slightly different on mobile and desktop
    // Both end up calling handleListScroll
    // Mobile gets the scroll event from the window object and
    // has methods to enableScrollListener and disableScrollListener.
    // Desktop uses the built in backbone scroll event for this View.
    // The reason that they are different is that on desktop the list
    // has a defined fixed height, so when the list scroll it is actually
    // the div that is scrolling. On mobile, the list has a height of auto
    // and when the list is scrolled the entire page is scrolled.
    // We could have made it a fixed height for mobile as well, but it was
    // a design decision for mobile, so that the filters would be at the top of
    // the list and scroll with the list.
    handleListScroll: function(event) {
      this.loadImagesInView();
    },
    handleResetFiltersLinkClick: function() {
      this.model.resetFilters();
    },
    handleSelectedCarIdChange: function(model, selectedCarId) {
      if (selectedCarId && selectedCarId.length > 0) {
        this.scrollCarIntoView(selectedCarId[0]);
      }
    },
    elementInViewport: function (el) {
      var rect = el.getBoundingClientRect();
      return (
        rect.top    >= 0 &&
        rect.left   >= 0 &&
        rect.top <= (window.innerHeight || document.documentElement.clientHeight)
      );
    },
    // If an image is in view and has not been loaded, load it.
    // Method is called on scroll.
    loadImagesInView: function() {
      var self          = this;
      var $carImages    = this.$el.find('.car-image[data-src]');
      if ($carImages.length > 0) {
        $carImages.each(function(index) {
          var $carImage         = $(this);
          if (self.elementInViewport(this)) {
            $carImage.attr('src', $carImage.data('src'));
            $carImage.removeAttr('data-src');
          }
        });
      } else {
        //We have no more images to load, disable the scroll handler
        this.disableScrollListener();
      }
    },
    scrollCarIntoView: function(carId) {
      var selectedCarRow    = this.$el.find('#search-results-list-car-' + carId);
      var selectedCarRowTop = selectedCarRow.position().top;
      var listHeight        = this.$el.height();
      var listScrollTop     = this.$el.scrollTop();

      // If car is out of view, scroll into view
      if (selectedCarRowTop < 0 || selectedCarRowTop > listHeight) {
        this.$el.scrollTop(listScrollTop + selectedCarRowTop);
      }
    },
    handleCarsBeforeFetch: function() {

    },
    handleCarsAfterFetch: function() {
      this.render();
    }
  });

  return ResultsListView;
});
