define('views/search/results_count',[
  'jquery',
  'backbone',
  'config',
  'text!templates/search/results_count.handlebars',
  'libs/handlebars'
], function($, Backbone, config, resultsCountHandlebars){

  var ResultsCountView = Backbone.View.extend({
    template: Handlebars.compile(resultsCountHandlebars),
    initialize: function() {
      this.render();
      this.activate();
    },
    render: function() {
      var carCount      = this.collection.length;
      var carCountLabel = (carCount >= config.MAX_CARS_TO_DISPLAY_IN_SEARCH) ? carCount + "+" : carCount;

      this.$el.html(this.template({'car_count': carCountLabel}));
      return this;
    },
    activate: function() {
      this.listenTo(this.collection, 'add remove reset', this.render);
    }
  });

  return ResultsCountView;
});

