define('views/dashboard/dashboard',[
  'jquery',
  'common',
  'config',
  'webapp',
  'common/partials/dialogs/owner_decline',
  'models/viewmodels/car_price',
  'utils/loader',
  'views/car/car_price_control',
  'views/shared/notification'
], function(
  $,
  common,
  config,
  webapp,
  ownerDeclineDialog,
  CarPrice,
  loader,
  CarPriceControlView,
  NotificationView
) {

  var DashboardView = Backbone.View.extend({
    el: '#content',

    initialize: function() {
      // Setup models and collections
      this.carPrice = new CarPrice({
        'hourly_rate': config.globals.hourly_rate,
        'daily_rate': config.globals.daily_rate,
        'weekly_rate': config.globals.weekly_rate
      });

      // Render and turn on UI
      this.render();
      this.activate();
    },

    render: function() {
      // Attaches UI events to notification. HTML is rendered in haml.
      this.notificationView = new NotificationView();

      // Old school - non Backbone view      
      ownerDeclineDialog.init();

      this.carPriceControlView = new CarPriceControlView({
        el: '#dashboard-car-price-control',
        model: this.carPrice
      });
    },

    activate: function() {

      this.listenTo(this.carPrice, 'change', function(model) {
        $.ajax({
          type: 'POST',
          url: config.ADJUST_CAR_RATES_URL,
          data: model.toJSON(),
          dataType: 'json'
        });
      });

      common.loadMore(
        '/ajax/car-history',
        {'item_count': 20},
        $('#car-history-section'),
        '.history-load-more');

      $("#pending-rentals .rental-request-box").each(function(index) {
        $this = $(this);
        var bundleId            = $this.attr("bundle_id"),
            renterFirstName      = $this.attr("renter_first_name"),
            renterPhoto          = $this.attr("renter_photo"),
            requestStartUnixTime = $this.attr("start_time"),
            requestEndUnixTime   = $this.attr("end_time");

        $(this).find(".accept-button").click(function() {
          webapp.approve(bundleId, $(this));
        });

        $(this).find(".decline-button").click(function() {

          ownerDeclineDialog.show({
            'bundleId': bundleId,
            'requestStartUnixTime': requestStartUnixTime,
            'requestEndUnixTime': requestEndUnixTime,
            'renter': renterFirstName,
            'renterPhoto': renterPhoto
          });

        });

      });

      $("#portland-survey .enroll-btn").click(function() {
        var thisButton = $(this);
        loader.setButtonSubmitting(thisButton);
        $.ajax({
          url: "/ajax/enroll_survey",
          dataType: "json",
          data: {"survey": "portland", "action": "enroll"},
          success: function(data) {
            if (data.response == "success") {
              $("#portland-survey .enroll-btn").hide();
              $("#portland-survey .message").html(data.text);
              $("#portland-survey .decline-btn")
                .unbind("click")
                .click(function(){
                  $("#portland-survey").slideUp();
                });
            }
          }
        });
      });

      $("#portland-survey .decline-btn").click(function() {
        $("#portland-survey").slideUp();
        $.ajax({
          url: "/ajax/enroll_survey",
          dataType: "json",
          data: {"survey": "portland", "action": "decline"}
        });
      });

    }
  });

  return DashboardView;

});

