// Top level view
// - loads nav
// - loads appropriate Backbone section depending on html tags id

define('views/app',[
  'jquery',
  'backbone',
  'common',
  'config',
  'raven',
  'utils/maps',
  'models/user',
  'models/viewmodels/car_edit_ui_state',
  'models/viewmodels/car_profile_ui_state',
  'models/viewmodels/list_car_ui_state',
  'models/viewmodels/search_ui_state',
  'views/car/car_edit',
  'views/car/car_profile',
  'views/car/list_car',
  'views/car/owner_key',
  'views/car/renter_key',
  'views/dashboard/dashboard',
  'views/global/login_button',
  'views/global/nav',
  'views/login/signup',
  'views/login/signup_dialog',
  'views/public/collection',
  'views/public/find_a_car',
  'views/public/list',
  'views/public/rent',
  'views/public/share',
  'views/public/promotion_landing',
  'views/public/refer',
  'views/search/search',
  'utils/loader',
  'views/user/user_edit'
], function (
  $,
  Backbone,
  common,
  config,
  raven,
  googleMaps,
  User,
  CarEditUIState,
  CarProfileUIState,
  ListCarUIState,
  SearchUIState,
  CarEditView,
  CarProfileView,
  ListCarView,
  OwnerKeyView,
  RenterKeyView,
  DashboardView,
  LoginButtonView,
  NavView,
  SignupView,
  SignupDialogView,
  CollectionView,
  FindACarView,
  ListView,
  RentView,
  ShareView,
  promotionLanding,
  ReferralView,
  SearchView,
  loader,
  UserEditView
) {

  if (config.globals.sentry_public_url) {
    window.onerror = Raven.process;
    var options = {
      logger: 'js-raven-logger',
      whitelistUrls: [/getaround\.com/],
      includePaths: [/https?:\/\/(www\.)?getaround\.com/]
    };
    Raven.config(config.globals.sentry_public_url, options).install();
    Raven.setUser({
      id: config.globals.uid
    });
  }


  return AppView = Backbone.View.extend({

    initialize: function () {

      // Setup models and collections
      this.user = new User();
      this.user.fetch();

      // Render and activate
      this.render();
      this.activate();
    },

    render: function () {
      var self = this;

      // Subviews
      this.navView = new NavView({
        el: '#nav',
        user: this.user
      });

      this.loginButtonView = new LoginButtonView({
        user: this.user
      });

      // ---------------------------------
      // Load appropriate Backbone View
      // ---------------------------------

      common.pageReady("car-car_edit", function () {
        self.carEditUIState = new CarEditUIState();
        self.carEditView = new CarEditView({
          model: self.carEditUIState
        });
      });

      common.pageReady("car-car_profile", function () {
        self.carProfileUIState = new CarProfileUIState();
        self.carProfileView = new CarProfileView({
          model: self.carProfileUIState,
          user: self.user
        });
      });

      common.pageReady("car-list_car", function () {
        self.listCarUIState = new ListCarUIState();
        self.listCarView = new ListCarView({
          model: self.listCarUIState
        });
      });

      common.pageReady("car-owner_key", function () {
        self.ownerKeyView = new OwnerKeyView();
      });

      common.pageReady("car-renter_key", function () {
        self.renterKeyView = new RenterKeyView();
      });

      common.pageReady("dashboard-dashboard", function () {
        self.dashboardView = new DashboardView();
      });

      common.pageReady("login-signup", function () {
        self.signupView = new SignupView({
          user: self.user
        });
      });

      common.pageReady('public-collection', function () {
        self.collectionView = new CollectionView();
      });

      common.pageReady('public-find', function () {
        self.findACarView = new FindACarView();
      });

      common.pageReady('public-list', function () {
        self.listView = new ListView();
      });

      common.pageReady('public-rent', function () {
        self.rentView = new RentView();
      });

      common.pageReady('public-share', function () {
        self.shareView = new ShareView();
      });

      common.beforePageReady('public-promotion-landing', function () {
        self.promotionLandingView = new promotionLanding.PromotionLandingView({
          user: self.user
        });
      });

      common.beforePageReady('public-refer', function () {
        self.referralView = new ReferralView({
          user: self.user
        });
      });

      common.pageReady("search-search", function () {
        // SearchUIState requires Google Maps
        googleMaps.onScriptLoad(function () {
          self.searchUIState = new SearchUIState();
          self.searchView = new SearchView({
            model: self.searchUIState,
            user: self.user
          });
        });
      });

      common.pageReady('user-edit', function () {
        self.userEditView = new UserEditView();
      });

    },

    activate: function () {
      var self = this;
      this.user.on('login:start', function () {
        loader.showPageLoader();
      });
      this.user.on('login:noGetaroundAccount', function (options) {
        self.signupDialogView = new SignupDialogView({
          user: self.user,
          redirectURL: options.redirectURL,
          promoCode: options.promoCode,
          referringUser: options.referringUser
        });
      });
      this.user.on('login:success', function (options) {
        if (options.redirectURL != "#") {
          // Add a small delay before forwarding the user. Without a delay, the 
          // loading spinner will not get injected into the DOM and displayed.
          setTimeout(function() {
            window.location.href = options.redirectURL;
          }, 50);
        }
      });
      this.user.on('login:fbNotAuthorized', function () {
        loader.hidePageLoader();
      });
      this.user.on('login:error login:cancel', function () {
        loader.hidePageLoader();
      });
      this.user.on('login:suspended', function () {
        window.location.href = '/login/suspended';
      });
      $(window).on('resize', this.handleWindowResize);
    },

    handleWindowResize: function () {
      // Prevent continious event triggering during resize. Trigger only after
      // user has stopped resizing for 200ms.
      clearTimeout(this.timeout);
      this.timeout = setTimeout(function() {
        $.event.trigger('appLayoutChange');
      }, 200);
    }
  });

});

