// messaging.js
// --------
//
//

define('common/partials/messaging',[
  'jquery',
  'common',
  'text!templates/messaging.handlebars',
  'libs/handlebars'
], function( $, common, messagingHandlebars ){

  var $element = $(),
    messagingTemplate,
    originalHeight;

  // =================
  // init
  // =================
  var init = function(element) {

    $element = $( element );
    originalHeight = $element.find('#message').height();

    $element.on( 'focus', '#message', function( event ) {
      $(this).height(80);
    });

    $element.on( 'keyup change', '#message', function( event ) {

      if( $(this).val() === "" ){
        $element.find('#send-message').attr( 'disabled', 'disabled' );
      }
      else {
        $element.find('#send-message').removeAttr( 'disabled' );
      }
    });

    messagingTemplate = Handlebars.compile( messagingHandlebars );

    var bundleId, messageText, toUserUid, toUserName;

    $element.find("#send-message").on('click', function() {

      var $button = $(this);
      bundleId = GA_ARGS.bundle_id;
      messageText = $element.find("#message").val();
      toUserUid = $element.find("#to-user-uid").val();

      var args = {"bundle_id": bundleId, "message": messageText};
      if (toUserUid != "All") {
        args["to_user"] = toUserUid;
        var $options = $element.find("select#to-user-uid option");
        if ($options.size() > 0) {
          toUserName = $options.filter(":selected").text()
        }
        else {
          toUserName = $element.find("#to-user-name").val()
        }
      }
      else {
        toUserName = "All";
      }

      $.post("/ajax/send-message", args, function(data) {
          $element.find("#sending-message").remove();
          if (data == "OK") {
            addPostedMessage(messageText, toUserName);
          }
          else {
            $element.find("#sending-message .action").text("Error.");
          }
        }
      );

      $element.find("#sending-message").remove();
      $element.find('#send-message').attr( 'disabled', 'disabled' );
      $element.find("#message").val("").height(originalHeight);

      $element.find(".messages").prepend(
        '<div id="sending-message" class="message system"><div class="action">Sending...</div></div>'
      );

    });
  };

  var addPostedMessage = function(messageText, toUserName) {
    var templateArgs = {
      "fromUserPhoto": common.fixImageUrl(GA_ARGS.user_photo, 75, 75),
      "allTag": "",
      "toUserName": toUserName,
      "messageText": messageText,
      "showNames": GA_ARGS.show_names
    };
    $element.find(".messages").prepend(messagingTemplate(templateArgs));
    $($element.find(".messages .message")[0])
      .hide()
      .slideDown(1250);
  }

  return {

    init: init

  };

});
