define('utils/converters',[
  'libs/moment.min'
], function(momentIsNotAModule){

  var castToBoolean = function(value) {
    if (value !== undefined) {
      return Boolean(value);
    }
    else {
      return value;
    }
  };

  var castToDate = function(value) {
    if (value) {
      return new Date(value);
    }
    else {
      return undefined;
    }
  };

  var castToNumber = function(value) {
    value = Number(value);
    if (isNaN(value)) {
      return undefined;
    }
    else {
      return value;
    }
  };

  // IANATimezoneToShortTimezone
  // ---------------------------
  // On the client side, the only timezone logic that is being done is that we are checking if the
  // user is in the same timezone as the car they are looking at on a car profile page and if the user
  // is in the same timezone of the cars in the search results they are viewing. If they are in a
  // different timezone, we show a bit of text similar to the following: "All times in EST"
  //
  // We get the user's (brower's) IANA timezone using the jstz.js lib.
  var IANATimezoneToShortTimezone = function(ianaTimezone) {
    var TIMEZONE_NAMES = {
      "America/Los_Angeles": "PST",
      "America/Denver": "MST",
      "America/Chicago": "CST",
      "America/New_York": "EST"
    };
    return TIMEZONE_NAMES[ianaTimezone];
  };

  var ISODateToJSDate = function(dateAndTime) {
    // This is different from (new Date()).setTime(Date.parse(dateAndTime));
    // because it ignores time zones.
    var dateAndTimeSplit = dateAndTime.split("T");
    var date             = dateAndTimeSplit[0];
    var dateSplit        = date.split("-");
    var year             = dateSplit[0];
    var month            = Number( dateSplit[1] ) - 1;
    var dayOfMonth       = dateSplit[2];
    var hour             = 0;
    var minute           = 0;
    var second           = 0;
    if (dateAndTimeSplit.length > 1) {
      var timePart = dateAndTimeSplit[1];
      if (timePart.indexOf("Z") > -1 || timePart.indexOf("-") > -1 ||
          timePart.indexOf("+") > -1) {
        var newDate = new Date();
        newDate.setTime(Date.parse(dateAndTime));
        return newDate;
      }
      var timePartSplit = timePart.split(":");
      hour = timePartSplit[0];
      if (timePartSplit.length > 1) {
        minute = timePartSplit[1];
      }
      if (timePartSplit.length > 2) {
        second = timePartSplit[2];
      }
    }
    return new Date( year, month, dayOfMonth, hour, minute, second );
  };

    /**
   * @param {Date} date
   * @return {string}
   */
  var JSDateToISODateTimezoneNaive = function(date) {
    return moment(date).format("YYYY-MM-DDTHH:mm:ss");
  };

  var makeISODateTimezoneNaive = function(date) {
    return moment(date).format("YYYY-MM-DDTHH:mm:ss");
  };

  /**
   * @param {Date} date
   * @return {string}
   */
  var JSDateToISODateWithoutTime = function(date) {
    function pad(n) { return (n < 10) ? '0' + n : n;  }

    var year  = date.getUTCFullYear();
    var month = pad(date.getUTCMonth() + 1);
    var day   = pad(date.getUTCDate());

    return year + '-' + month + '-' + day;
  };

  return{
    castToBoolean: castToBoolean,
    castToDate: castToDate,
    castToNumber: castToNumber,
    IANATimezoneToShortTimezone: IANATimezoneToShortTimezone,
    ISODateToJSDate: ISODateToJSDate,
    JSDateToISODateWithoutTime: JSDateToISODateWithoutTime,
    JSDateToISODateTimezoneNaive: JSDateToISODateTimezoneNaive,
    makeISODateTimezoneNaive: makeISODateTimezoneNaive
  };

});

