// Do not add anything new to this file. If you have questions about where to write your
// javascript, please ask Ish or Lokesh.
//
// This file contains shared code for user account management. If you are looking for the JS file
// for the signup page, go to js/login/signup.js
//
// Warning: This files loads and runs on every page. Dangerous. We need to slowly move this code
// out to appropriate page js files and modules.

define('signup',[
  'jquery',
  'common',
  'common/validators',
  'libs/jquery.form',
  'libs/jquery.validate',
  'utils/maps',
  'utils/loader'
], function($, common, validators, jQueryForm, jQueryValidate, maps, loader){

  var isInitialized = false;

  var signupModule = {

    initialize: function() {

      if (isInitialized) {
        return;
      }

      isInitialized = true;

      this.validator = $("#signupform").validate({
        errorElement: "span",
        focusInvalid: false,
        focusCleanup: true,
        ignore: ".ignore",
        groups: {
          "mmy" : "make model year"
        },
        messages: {
          "make" : {
            "required": "These fields are required."
          },
          "model": {
            "required": "These fields are required."
          },
          "year": {
            "required": "These fields are required."
          },
          "mileage": {
            "required": "These fields are required."
          },
          "zipcode": {
            "required": "The zip code where you will use Getaround most often."
          }
        },

        errorPlacement: function(error, element) {
          if ($.inArray( $(element).attr("id"), ["zipcode", "zipcode2"]) != -1 ) {
            if ($(element).attr("id") == "zipcode") {
              $("#zipcode_msg .one_time").empty();
            }
            $("#" + $(element).attr("id") + "_msg").append(error);
          } else if ($.inArray( $(element).attr("id"), ["make", "year", "model", "mileage"]) != -1 ) {
            // $(error).text("These fields are required.");
            if ( !$("#carOwnerOnly_msg span:contains("+error.text()+")").html() ) {
              $("#carOwnerOnly_msg").append(error);
            }
          } else if ($.inArray( $(element).attr("id"), ["geocoded_location", "geocoded_city", "geocoded_state", "geocoded_country"]) != -1 ) {
            // do not display these errors
          } else {
            error.insertAfter(element);
          }
        },

        submitHandler: function(form) {
          $("#error_hook").removeClass("error-header").empty();
          ajax_submit(form);
        },

        invalidHandler: function(form, validator) {
          window.scrollTo(0, 0);
          $("#error_hook").addClass("error-header").text(
              "We couldn't understand your response. Please verify fields highlighted in red."
          ).slideDown('slow');
        }
      });

      var ajax_submit = function(form) {
        loader.setButtonSubmitting($('.saveButton'));
        $(form).ajaxSubmit({
          success: function(responseText, statusText, xhr, form) {
            if (responseText == "OK") {
              window.location = $(form).attr("next");
            }
            else {
              if (responseText.startsWith("redirect")) {
                window.location = responseText.split(":")[1];
              }
              else {
                msg_id = $(form).attr("msg-id");
                if (msg_id) {
                  $(msg_id).text(responseText);
                }
              }
            }
          },
          error: function(xhr, statusText, errorString, form) {
            var response = JSON.parse(xhr.responseText);
            if (response.error == "phone_number_invalid") {
              $("#signupform").validate().showErrors({"phone_no": "Please specify a valid phone number."});
              $('html, body').animate({scrollTop: $('#phone_no').offset().top -50 }, 'fast');
            }
          },
          complete: function() {
            loader.clearButtonSubmitting($('.saveButton'));
          }

        });
      };
    }
  };

  common.pageReady(
    ["login-logging_in", "login-logout", "user-edit",
     "checkout-driverinfo", "dashboard-dashboard"],
    function(){

      var timeouts = {};

      // ====================
      // = Validation stuff =
      // ====================
      signupModule.initialize();

      // =============================
      // = Utility: Map ids to names =
      // =============================
      $("form input:text[secure!=\"true\"], form select[secure!=\"true\"]").each(function() {
        $(this).attr("name", this.id);
      });

      // =======================
      // = Index page specific =
      // =======================
      var index_page = false;
      
      var showOwnerOnly = function() {
        $(".carOwnerOnly select").removeAttr("disabled");
        $(".carOwnerOnly select").addClass("required");
        $(".carOwnerOnly select").addClass("enabled");
        $(".carOwnerOnly select").removeClass("disabled");
        $(".carOwnerOnly label").removeClass("disabled");
        $(".carOwnerOnly #make").focus();
      };

      var hideOwnerOnly = function() {
        $(".carOwnerOnly select").attr("disabled", "disabled");
        $(".carOwnerOnly select").removeClass("required");
        $(".carOwnerOnly select").removeClass("enabled");
        $(".carOwnerOnly select").addClass("disabled");
        $(".carOwnerOnly label").addClass("disabled");
        $(".carOwnerOnly select").removeClass("error");
        $("#carOwnerOnly_msg").empty();
      };

      $(".remove_default").change(function() {
        $(this).children("[value='']").remove();
      });

      String.prototype.startsWith = function(str) {
        return (this.match("^"+str)==str);
      };


      var geocoder;
      maps.onScriptLoad(function() {
        try {
          geocoder = new google.maps.Geocoder();
        }
        catch(geocodeErr) {
          console.log("Geocode error caught: " + geocodeErr);
        }
      });
      var delay_geocode = function() {
        return function() {
          delete timeouts["geocode"];
          $("#zipcode_msg .geocode").empty();
          $("#zipcode_msg .one_time").empty();

          var zipcode = $("#signupform #zipcode").val();
          var countryName = $('#country option:selected').text();
          var re = /^[0-9]*$/;
          if (zipcode.length == 5 && re.test(zipcode)) {
            $("#zipcode").addClass("processing");
            geocoder.geocode( { 'address': zipcode + ' ' + countryName}, function(results, status) {
              $("#zipcode").removeClass("processing");
              if (status == google.maps.GeocoderStatus.OK) {
                $("#zipcode_msg").attr("status", "OK");
                var geocode_result = $("<span class=geocode>" + results[0]["formatted_address"] + "</span>");
                $("#zipcode_msg").empty().append(geocode_result);

                var city = "";
                var state = "";
                var country = "";

                var chunks = results[0]["address_components"];
                var resultLength = chunks.length;
                // loop in reverse order to prefer locality over neighborhood for city assignment
                for (var i = resultLength-1; i >= 0; i--) {

                  if ( (chunks[i]["types"].indexOf("neighborhood") != -1 ||
                        chunks[i]["types"].indexOf("sublocality") != -1 ||
                        chunks[i]["types"].indexOf("locality") != -1) && city == "") {
                    city = chunks[i]["long_name"];
                  }
                  else if (chunks[i]["types"].indexOf("administrative_area_level_1") != -1 &&
                           chunks[i]["types"].indexOf("political") != -1 &&
                           state == "") {
                    state = chunks[i]["short_name"];
                  }
                  else if (chunks[i]["types"].indexOf("country") != -1 &&
                           chunks[i]["types"].indexOf("political") != -1 &&
                           country == "") {
                    country = (chunks[i]["short_name"]).toLowerCase();
                    // if (country == "US") { country = "United States of America"; }
                    // if (country == "CA") { country = "Canada"; }
                  }
                }
                $("#geocoded_city").val(city);
                $("#geocoded_state").val(state);
                $("#geocoded_country").val(country);
              } else if (status == google.maps.GeocoderStatus.ZERO_RESULTS) {
                $("#zipcode_msg").attr("status", "ZERO_RESULTS");
                $("#zipcode_msg").empty();
                signupModule.validator.element("#zipcode");
              } else {
                // log("Geocode failed: " + status);
              }
            });
          } else {
            $("#zipcode_msg").attr("status", "OK");
            signupModule.validator.element("#zipcode");
          }
        };
      };

      function geocode_bind() {
        if("geocode" in timeouts) {
          clearTimeout(timeouts["geocode"]);
        }
        timeouts["geocode"] = setTimeout(delay_geocode(), 500);
      }

      $("#signupform #zipcode, #country").bind("keypress change blur", geocode_bind);
      $("#signupform #zipcode").bind("keydown", function(e) {
        // catch backspace and delete
        if (e.which == 8 || e.which == 46) {
          $("#zipcode_msg .geocode").empty();
          geocode_bind();
        }
      });

      $("#signupform #country").change(function(e) {
        if ( $(this).val() === "US" ) {
          $("#zipcode").attr( 'placeholder', 'Zip code');
        } else {
          $("#zipcode").attr( 'placeholder', 'Postal code');
        }
      });



      // ====================================================
      // = Showing/Updating Map (also on Availability page and Search Settings page) =
      // ====================================================
      var geocode_callback = function(city, state, country, street_address, postal_code, loc) {
        $("#geocoded_city").val(city);
        $("#geocoded_state").val(state);
        $("#geocoded_country").val(country);
        $("#geocoded_location").val(loc.lat() + "," + loc.lng());
        userEnteredLoc = loc;
      };

      var markerLoc = null;
      var userEnteredLoc = null;

      function onMarkerMoveCallback(loc) {
        // Expects google.maps.LatLng
        $("#geocoded_location").val(loc.lat() + "," + loc.lng());
        markerLoc = loc;
        if (userEnteredLoc == null || distanceBetween(markerLoc, userEnteredLoc) > 0.1) {
          reverseGeoCode(loc, function(city, state, country, street_address, postal_code, returned_loc) {
            $("#geocoded_city").val(city);
            $("#geocoded_state").val(state);
            $("#geocoded_country").val(country);
            $("#location #street_address").val(street_address);
            $("#location #city").val(city);
            $("#location #zipcode2").val(postal_code);
            $("#location #state").val(state);
            $("#location #country").val(country);
          });
        }
      }

      function showMap(geocode){
        maps.onScriptLoad(function() {
          if ($("#location #street_address").hasClass("valid") &&
              $("#location #zipcode2").hasClass("valid"))
          {
            $("#location #map").show();
            if (window.resizeMap !== undefined) {
              window.resizeMap();
            }
            if (window.clearMap !== undefined) {
              window.clearMap(false);
            }
            if ($("#geocoded_location").val() == "0,0") {
              geocode = true;
            }
            if (geocode == true) {
              var fullAddressList = [];
              $.each(
                ["#street_address", "#city", "#state", "#country", "#zipcode2"],
                function(i, v) {
                  var value = $("#location "+v).val();
                  if (value) {
                    fullAddressList.push(value);
                  }
                }
              );
              if (fullAddressList.length > 0) {
                addMarkerGeocode(
                  fullAddressList.join(","),
                  true,
                  true,
                  onMarkerMoveCallback,
                  geocode_callback
                );
              }
            } else {
              addMarkerLL(
                $("#geocoded_location").val(),
                true,
                true,
                onMarkerMoveCallback
              );
            }
          }
        });
      }

      if ("validate_on_load" in GA_ARGS && GA_ARGS["validate_on_load"] == true){
        $("#signupform").validate().element("#street_address");
        $("#signupform").validate().element("#zipcode2");
        showMap(false);
      } else if ("show_map_on_load" in GA_ARGS && GA_ARGS["show_map_on_load"] == true) {
        /* showMapOnLoad gets used on the search settings page */

        $("#location #street_address").removeClass("required");
        $("#signupform").validate().element("#street_address");
        $("#signupform").validate().element("#zipcode2");
        var doGeoCode = $("#geocoded_location").val() == "";
        showMap(doGeoCode);
      }


      $("#location input").change(function() {
        showMap(true);
      });

      var delay_showMap = function(trigger, showMapArg) {
        return function() {
          delete timeouts[trigger];
          showMap(showMapArg);
        };
      };

      $("#location input").bind("keydown change blur", function(e) {
        if("keydown" in timeouts) {
          clearTimeout(timeouts["keydown"]);
        }
        timeouts["keydown"] = setTimeout(delay_showMap("keydown", true), 1000);
      });


      var GeoCodeCalc = {};
      GeoCodeCalc.EarthRadiusInMiles = 3956.0;
      GeoCodeCalc.EarthRadiusInKilometers = 6367.0;
      GeoCodeCalc.ToRadian = function(v) { return v * (Math.PI / 180);};
      GeoCodeCalc.DiffRadian = function(v1, v2) {
        return GeoCodeCalc.ToRadian(v2) - GeoCodeCalc.ToRadian(v1);
      };
      GeoCodeCalc.CalcDistance = function(lat1, lng1, lat2, lng2, radius) {
        return radius * 2 * Math.asin( Math.min(1, Math.sqrt( ( Math.pow(Math.sin((GeoCodeCalc.DiffRadian(lat1, lat2)) / 2.0), 2.0) + Math.cos(GeoCodeCalc.ToRadian(lat1)) * Math.cos(GeoCodeCalc.ToRadian(lat2)) * Math.pow(Math.sin((GeoCodeCalc.DiffRadian(lng1, lng2)) / 2.0), 2.0) ) ) ) );
      };
      function distanceBetween(latLng1, latLng2) {
        return Math.abs(GeoCodeCalc.CalcDistance(latLng1.lat(), latLng1.lng(),
                                                 latLng2.lat(), latLng2.lng(), GeoCodeCalc.EarthRadiusInMiles));
      }

      // =========================
      // = Payment page specific =
      // =========================
      function togglePaypal(){

        if ($("#choose_paypal:checked").val() == "paypal"){
          if (is_account_page()) {
            $("#paypal_email").addClass("required");
          }
          $("#paypal_email").show();
        } else {
          $("#payout_email").parent(".error").remove();
          $("#paypal_email").removeClass("error");
          if (is_account_page()) {
            $("#paypal_email").removeClass("required");
            // $("#payout #paypal_email").addClass("required");
          }
          $("#paypal_email").val(paypal_email_default);
          $("#paypal_email").attr("set", paypal_email_set);
          $("#paypal_email").hide();
        }
      }

      function is_account_page() {
        return window.location.pathname.split("/")[1] == "account";
      }

      function togglePayByCheck() {
        if ($("#choose_checks:checked").val() == "checks"){
          $("#payout_address").show();
        } else {
          $("#payout_address").hide();
        }
      }

      var paypal_email_default = $("#paypal_email").val();
      var paypal_email_set = $("#paypal_email").attr("set");
      togglePaypal();
      togglePayByCheck();

      $(".payment-widget input:radio").change(function(){
        togglePaypal();
        togglePayByCheck();
      });

      var tweet_text = escape("I just rented a car on @Getaround for #TCDisrupt, now I’m a #Getaround VIP! http://goo.gl/zGDRx");
      var tweet_url = "http://twitter.com/share?text="+tweet_text;
      $(".tc-disrupt-tweet").attr("href", tweet_url);
      $(".tc-disrupt-tweet").attr("target", "_tblank");
      $(".tc-disrupt-tweet").click(function() {
        window.location = "/";
      });
    });

    return signupModule;
  }
);

