// Top level Backbone view for Search.

define('views/search/search',[
  'jquery',
  'backbone',
  'config',
  'models/viewmodels/pickup_and_return',
  'models/viewmodels/places_autocomplete',
  'models/search_results',
  'utils/maps',
  'views/search/filters',
  'views/shared/pickup_and_return_inputs',
  'views/shared/places_autocomplete_input',
  'views/search/results_count',
  'views/search/results_list',
  'views/search/results_map',
  'libs/jquery.cookie',
  'common'
], function(
  $,
  Backbone,
  config,
  PickupAndReturn,
  PlacesAutocomplete,
  SearchResults,
  googleMaps,
  FiltersView,
  PickupAndReturnInputsView,
  PlacesAutocompleteInputView,
  ResultsCountView,
  ResultsListView,
  ResultsMapView,
  cookieModule,
  common
) {

  return SearchView = Backbone.View.extend({
    
    el: '#content',
    
    initialize: function() {
      // DEBUGGING
      if (common.getURLParameterByName('debug') === "clear") {
        $.cookie(config.SEARCH_COOKIE_NAME, null);
      }
      // Setup models and collections
      this.user               = this.options.user;
      this.searchResults      = new SearchResults();
      this.placesAutocomplete = new PlacesAutocomplete();
      this.pickupAndReturn    = new PickupAndReturn();

      this.model.set({
        'isMapVisible': this.$el.find('#search-results-map').is(':visible')
      });

      this.placesAutocomplete.set({
        'locationText': this.model.get('locationText')
      });

      this.pickupAndReturn.set({
        'user_iana_timezone': this.user.get('iana_timezone'),
        'pickup_date': this.model.get('pickup_date'),
        'return_date': this.model.get('return_date')
      });

      // Render and turn on UI
      this.render();
      this.activate();
    },

    render: function() {
      var self = this;
      
      // Subviews

      this.placesAutocompleteView = new PlacesAutocompleteInputView({
        el: '#search-form-places-autocomplete-input',
        model: this.placesAutocomplete
      });

      this.pickupAndReturnInputsView = new PickupAndReturnInputsView({
        el: '#search-form-pickup-and-return-inputs',
        model: this.pickupAndReturn
      });
      this.resultsCountView = new ResultsCountView({
        el: '#search-results-count',
        collection: this.searchResults
      });
      this.filtersView = new FiltersView({
        el: '#search-filters',
        model: this.model
      });
      this.resultsListView = new ResultsListView({
        el: '#search-results-list',
        collection: this.searchResults,
        model: this.model
      });

      // On narrow resolutions, we hide the map with a media query and only show the results list.
      // If the map is not displayed, we don't need to instantiate the ResultsMapView.
      if (self.model.get('isMapVisible')) {
        googleMaps.onScriptLoad(function() {
          self.renderResultsMap();
        });
      }
      

      // Cache jquery objects
      this.$resultsList = this.$el.find('#search-results-list');

      return this;
    },

    activate: function() {
      var self = this;

      this.resizeUI(); // trigger once on load
      $(document).on('appLayoutChange', function() {
        self.resizeUI();
      });

      this.listenTo(this.placesAutocomplete, 'change:location', function(model) {
        var placesLocation = model.get('location');
        this.model.set({
          'searchLat': placesLocation.geometry.location.lat(),
          'searchLng': placesLocation.geometry.location.lng(),
          'mapCenter': placesLocation.geometry.location.toUrlValue(),
          'locationText': model.get('locationText')
        });
      });

      this.listenTo(this.pickupAndReturn, 'change:pickup_date change:return_date', function(model) {
        this.model.set({
          'pickup_date': model.get('pickup_date'),
          'return_date': model.get('return_date')
        });
      });

      this.listenTo(this.model, 'change', function(model) {
        var changedAttributes = _.keys(model.changedAttributes());

        // When map visibility changes because of the user resizing the viewport we need to render
        // or remove the map view.
        if (_.contains(changedAttributes, 'isMapVisible')) {
          if (model.get('isMapVisible')) {
            self.renderResultsMap();
          } else {
            self.removeResultsMap();
            model.unset('viewport');
          }
        }

        // On changes to any of the following attributes we fetch new results.
        if (_.intersection(changedAttributes, [
          'bodyType',
          'pickup_date',
          'return_date',
          'searchLat',
          'searchLng',
          'mapZoom',
          'max_hourly_price',
          'max_daily_price',
          'max_weekly_price',
          'instant',
          'transmission']).length > 0) {
          self.loadCars();
         } else if (_.contains(changedAttributes, 'viewport')) {
          // When we query the server we pad the viewport paramaters to create a buffer. If the user 
          // pans the map a bit, we have the data already fetched. Only once they pan outside of the
          // buffer do we need to requery.
          if (model.hasMapBeenPannedOutOfBuffer()) {
            self.loadCars();
          }
        }

        model.updateCookie();
      });

      this.listenTo(this.searchResults, 'reset', function(collection) {
        if (collection.length > 0){
          self.pickupAndReturn.set('car_iana_timezone', collection.first().get('timezone'));
        }
      });

      // To-do: Refactor. This logic is hard to follow. - Lokesh
      // When there is no map we need to do the initial load of cars manually. If there is a map,
      // when it is initally rendered and its map center and zoom set, it will trigger a load.
      if (!this.model.get('isMapVisible')) {
        // In the case when a search_address is passed through the URL we don't loadcars. The eventual
        // geocoding of the URL param will trigger the first load.
        if (!((this.model.has('locationText') &&
           (!this.model.has('searchLat') && !this.model.has('searchLng') && !this.model.has('mapCenter'))))){
          this.loadCars();
        }
      }
    },

    loadCars: function() {
      this.searchResults.fetch($.param(this.model.getParams()));
    },

    renderResultsMap: function() {
      this.resultsMapView = new ResultsMapView({
        el: '#search-results-map',
        collection: this.searchResults,
        model: this.model
      });
    },

    removeResultsMap : function() {
      // Rather than using this.resultsMapView.remove() which would remove the html and unbind the
      // Backbone events from the view, we use jQuery's empty to remove the html and then
      // Backbone's unbind to unbind the Backbone events.
      //
      // The reason being that remove() will remove the root node <div id="search-results-map">.
      // jQuery's empty() will not.
      this.$el.find('#search-results-map').empty();
      this.resultsMapView.unbind();
    },

    resizeUI: function(event){
      var windowWidth  = $(window).width();
      var windowHeight = $(window).height();
      var $resultsMap  = this.$el.find('#search-results-map');
      var isMapVisible = $resultsMap.is(':visible');

      // In narrow resolutions when the map is not visible we don't set a height on the result list.
      // This lets it render and scroll normally. Meaning, when you scroll the page, everything
      // scrolls. In wider resolutions we set a height on the result list and allow users to scroll
      // it independently of the entire page.
      if (isMapVisible) {
        this.$resultsList.height(windowHeight - this.$resultsList.offset().top);
        $resultsMap.height(windowHeight - $resultsMap.offset().top);
      } else {
        this.$resultsList.css('height', 'auto');
      }
      this.model.set('isMapVisible', isMapVisible);
    }
  });

});

