define('utils/time',[
  'utils/converters'
  ], function(
    converters
  ) {


  var areDatesOnSameDay = function(date1, date2) {
    return (date1.getFullYear() === date2.getFullYear() &&
            date1.getMonth() === date2.getMonth() &&
            date1.getDate() === date2.getDate());
  };

  var calcDurationInHours = function(date1, date2) {
    if (typeof date1 !== 'undefined') {
      return ((date2 - date1) / 3600000);
    } else {
      return 0;
    }
  };

  var getMinutesBeforeMidnight =  function() {
    var now         = new Date();
    var endOfDay    = new Date();
    endOfDay.setDate(now.getDate() + 1);
    endOfDay.setHours(0, 0, 0, 0);
    return (endOfDay - now) / 60000;
  };

  // http://stackoverflow.com/a/4968292/400407
  var roundJSDateToQuarterHour = function(date) {
    var minutes = date.getMinutes();
    var hours   = date.getHours();
    date.setMinutes((((minutes + 7.5)/15 | 0) * 15) % 60);
    date.setHours((((minutes/105 + 0.5) | 0) + hours) % 24);
    date.setSeconds(0);
    return date;
  };

  var roundISODateToQuarterHour = function(ISODate) {
    var date = converters.ISODateToJSDate(ISODate);
    date = roundJSDateToQuarterHour(date);
    return converters.JSDateToISODateTimezoneNaive(date);
  };

  var ceilJSDateToQuarterHour = function(date) {
    var minutes = date.getMinutes();
    var hours   = date.getHours();
    date.setMinutes((((minutes + 7.5)/15 | 0) * 15) % 60 + 15);
    date.setHours((((minutes/105 + 0.5) | 0) + hours) % 24);
    date.setSeconds(0);
    return date;
  };

  return {
    areDatesOnSameDay: areDatesOnSameDay,
    calcDurationInHours: calcDurationInHours,
    ceilJSDateToQuarterHour: ceilJSDateToQuarterHour,
    getMinutesBeforeMidnight: getMinutesBeforeMidnight,
    roundISODateToQuarterHour: roundISODateToQuarterHour,
    roundJSDateToQuarterHour: roundJSDateToQuarterHour
  };

});
