define('views/car/key_map',[
  'jquery',
  'backbone',
  'libs/infobox',  
  'utils/maps',
  'views/car/car_location_map_card',
  'views/car/parking_map_card',
  'utils/loader'
], function(
  $,
  Backbone,
  InfoBoxModule,
  maps,
  CarLocationMapCardView,
  ParkingMapCardView,
  loader
){

  return KeyMapView = Backbone.View.extend({

    initialize: function() {
      this.isUserAllowedToLocateCar = this.options.isUserAllowedToLocateCar;
      this.rentalToken = this.options.rentalToken;

      this.render();
      this.activate();
    },

    render: function() {
      // The map div is hidden by default
      // .key-map{ display: none; }
      this.$el.show();

      this.parkingMapCardView = new ParkingMapCardView({
        model: this.model
      });
    },

    activate: function() {
      var self = this;

      // Init map
      var myOptions = {
        center: this.model.getParkingGoogleMapsLatLng(),
        mapTypeId: google.maps.MapTypeId.ROADMAP,
        disableDefaultUI: true,
        zoom: 15,
        zoomControl: true
      };
      this.map = new google.maps.Map(document.getElementById("key-map"), myOptions);  

      // Init InfoBox which is the lib used to show the map marker cards
      InfoBoxModule.init();
   
      this.parkingInfoBox = new InfoBoxModule.InfoBox({
        closeBoxURL: '',
        content: this.parkingMapCardView.el,
        enableEventPropagation: false,
        infoBoxClearance: new google.maps.Size(20, 20),
        isHidden: false,
        pane: "floatPane",
        pixelOffset: new google.maps.Size(-140, -115),
        zIndex: null
      });    

      // Show parking radius for street parked cars
      if (this.model.get('parking.location_type') === "STREET") {
        maps.addParkingRadius(this.model.getParkingGoogleMapsLatLng(), this.map);  
      }

      // Show parking marker
      this.parkingMarker = maps.addMarker('parking', this.model.getParkingGoogleMapsLatLng(), this.map);

      google.maps.event.addListener(this.parkingMarker, 'click', function(event) {
        self.showParkingCard(self.map, self.parkingMarker);
      });

      google.maps.event.addListener(this.map, 'click', function() {
        self.hideCards();
      });

      if (this.isUserAllowedToLocateCar) {
        var statusMessage = loader.showStatusMessage('Locating car');
        
        $.ajax({
          type: 'GET',
          url: '/key/locate/' + this.model.get('car_name'),
          data: {token: this.rentalToken},
          dataType: 'json',
          success: function(data) {
            self.renderCarLocationMapCardView(data);
          },
          error: function(data) {
            loader.showErrorDialog('There was an error locating the car. Reload the page to try again.');
          },
          complete: function(data, status) {
            loader.hideStatusMessage(statusMessage);
          }
        });
      }
    },

    showParkingCard: function(map, marker) {
      this.hideCarCard();
      this.parkingInfoBox.open(map, marker);
    },

    hideParkingCard: function() {
      if (!_.isUndefined(this.parkingInfoBox)) {
        this.parkingInfoBox.close();
      }
    },

    showCarCard: function(map, marker) {
      this.hideParkingCard();
      this.carInfoBox.open(map, marker);
    },

    hideCarCard: function() {
      if (!_.isUndefined(this.carInfoBox)) {
        this.carInfoBox.close();
      }
    },

    hideCards: function() {
      this.hideParkingCard();
      this.hideCarCard();
    },

    renderCarLocationMapCardView: function(recentLocation) {

      this.carLocationMapCardView = new CarLocationMapCardView({
        model: this.model,
        recentLocation: recentLocation
      });

      this.carInfoBox = new InfoBoxModule.InfoBox({
        closeBoxURL: '',
        content: this.carLocationMapCardView.el,
        enableEventPropagation: false,
        infoBoxClearance: new google.maps.Size(20, 20),
        isHidden: false,
        pane: "floatPane",
        pixelOffset: new google.maps.Size(-140, -115),
        zIndex: null
      });

      var carMarker = maps.addMarker('car', maps.createGoogleLatLng(recentLocation.lat, recentLocation.lon), this.map);

      var self = this;

      google.maps.event.addListener(carMarker, 'click', function(event) {
        self.showCarCard(self.map, carMarker);
      });

      // Zoom to fit markers in view and then zoom out once to make sure none of the markers are
      // hidden behind the lock/unlock buttons.
      maps.fitMarkers(new Array(this.parkingMarker, carMarker), this.map);

      var listener = google.maps.event.addListener(this.map, 'bounds_changed', function() {
        var zoom = self.map.getZoom() - 1;
        zoom = (zoom > 18) ? 18: zoom;
        self.map.setZoom(zoom);

        google.maps.event.removeListener(listener);

        // If location data is five or more minutes stale, show Car
        if (recentLocation.minutesSinceLocated >= 5) {
          self.showCarCard(self.map, carMarker);
        }
      });
    }
  });

});
