define('views/shared/make_model_inputs',[
  'jquery',
  'backbone'
], function(
  $,
  Backbone
) {

  return MakeModelInputsView = Backbone.View.extend({
    
    initialize: function() {
      // HTML rendered in haml.

      this.activate();
    },
    
    activate: function() {
    
      makeID  = '#make';
      yearID  = '#year';
      modelID = '#model';
      trimID  = '#trim';
      styleID = '#style';

      function loadObj(args, target, cover) {
        return (function() {
          // show cover if loading takes more than a tenth of a second
          var timeout = setTimeout("$('" + cover + "').show();", 100);

          if (target == "#year") {
            $(makeID).children("[value='']").remove();
            $(modelID).children().remove();
            $("<option></option>").val("").text("").appendTo($(modelID));
            $("#year").attr("disabled", true);
            $("#model").attr("disabled", true);
            $("#trim").attr("disabled", true);
            $("#style").attr("disabled", true);
          } else if (target == "#model") {
            $("#model").attr("disabled", true);
            $("#trim").attr("disabled", true);
            $("#style").attr("disabled", true);
          } else if (target == "#trim") {
            $("#trim").attr("disabled", true);
            $("#style").attr("disabled", true);
          } else if (target == "#style") {
            $("#style").attr("disabled", true);
          }

          data = args();

          $.get("/listcar/mmy", data, function(response) {
            // make sure cover is hidden
            clearTimeout(timeout);
            $(cover).hide();

            $(target).parent().show();

            // add a blank option every time you load in the data
            $(target).empty().attr("disabled", false);
            $("<option></option>").attr("value", "").text("").appendTo($(target));
            // add the rest of the options
            options = response.map(function(val) {
              rval = $("<option></option>");
              rval.attr("value", val).text(val);
              return rval;
            });
            for (var i = 0; i < options.length; i++) {
              ($(target)).append(options[i]);
            }

            $(target).removeClass("error").focus();
          }, "json");
        });
      }

      $(trimID).change(function() {
        loadObj(function() {
          return {"make": $(makeID).val(), "year": $(yearID).val(), "model": $(modelID).val(), "trim": $(trimID).val()};
        }, styleID, styleID + "_cover")();
      });

      $(modelID).change(function() {
        loadObj(function() {
          return {"make": $(makeID).val(), "year": $(yearID).val(), "model": $(modelID).val()};
        }, trimID, trimID + "_cover")();
      });

      /* was loadModels() */
      $(yearID).change(function() {
        loadObj(function() {
          return {"make": $(makeID).val(), "year": $(yearID).val()};
        }, modelID, modelID + "_cover" )();
      });

      /* was loadYears() */
      $(makeID).change(function() {
        loadObj(function() {
          return {"make" : $(makeID).val()};
        }, yearID, yearID + "_cover")();
      });

      // If model is already set, make sure trim dropdown is enabled.
      if ($(modelID).val() !== ""){
        $(trimID).attr("disabled", false);
      }
    }
  });

});

