define('views/car/set_location_map',[
  'jquery',
  'backbone',
  'config',
  'utils/maps'
], function(
  $,
  Backbone,
  config,
  maps
){

  return SetLocationMapView = Backbone.View.extend({

    initialize: function() {
      this.activate();
    },

    activate: function() {

      // ====================================================
      // = Showing/Updating Map =
      // ====================================================
      var self = this;
      var geocoder;

      if (!($("#zipcode").val() && $("#street_address").val())) {
        // Hide map if we do not have enough data.
        $("#map-control").hide();
      }

      maps.onScriptLoad(function() {
        geocoder = new google.maps.Geocoder();

        // Initialize map to SF
        // new google.maps.LatLng(37.7750,-122.4183)
        var lat = GA_ARGS["car_location_lat"];
        var lon = GA_ARGS["car_location_lon"];
        if (lat && lon){
          initializeMap(new google.maps.LatLng(lat, lon));
        }
        else if (GA_ARGS["car_street_address"] ||GA_ARGS["car_zipcode"]) {
          geocodeAddress(
            GA_ARGS["car_street_address"],
            GA_ARGS["car_zipcode"],
            function(result) {
              initializeMap(result.geometry.location);
            }
          );
        }
        else {
          geocodeAddress(
            "",
            GA_ARGS["user_zipcode"],
            function(result) {
              initializeMap(result.geometry.location);
            }
          );
        }
      }, false, false);

      function initializeMap(defaultLocation) {
        var myOptions = {
          zoom: 15,
          center: defaultLocation,
          mapTypeId: google.maps.MapTypeId.ROADMAP
        };
        self.map = new google.maps.Map($("#map_canvas").get(0), myOptions);

        // Initialize marker
        var markerImage  = new google.maps.MarkerImage(
          config.INSTANT_PIN,
          null,
          null,
          null,
          new google.maps.Size(28, 40)
        );

        self.marker = new google.maps.Marker({
          map: self.map,
          position: defaultLocation,
          draggable: true,
          icon: markerImage
        });

        google.maps.event.addListener(self.marker, 'dragend', function(){
          var latlng = self.marker.getPosition();
          geocoder.geocode({'latLng': latlng}, function(results, status) {
            if (status == google.maps.GeocoderStatus.OK) {
              if (results[0]) {
                var address_components = results[0].address_components;
                var addressParts = parseAddressComponents(address_components);
                if (addressParts["street_address"]){
                  $("#street_address").val(addressParts["street_address"]);
                  // addressComponentsDict["city"]
                }
                if (addressParts["postal_code"]){
                  $("#zipcode").val(addressParts["postal_code"]);
                }
                setGeocoded(results[0].geometry.location, addressParts);
              }
              else{
                clearGeocoded();
              }
            } else {
              clearGeocoded();
            }
          });
        });
      }

      function clearGeocoded() {
        $("#street_address").val("");
        $("#zipcode").val("");
        $("#geocoded_street_address").val("");
        $("#geocoded_city").val("");
        $("#geocoded_state").val("");
        $("#geocoded_zipcode").val("");
        $("#geocoded_country").val("");
        $("#geocoded_latitude").val("");
        $("#geocoded_longitude").val("");
      }

      function setGeocoded(latLng, addressParts) {
        $("#geocoded_street_address").val(addressParts["street_address"]);
        $("#geocoded_city").val(addressParts["city"]);
        $("#geocoded_state").val(addressParts["state"]);
        $("#geocoded_zipcode").val(addressParts["postal_code"]);
        $("#geocoded_country").val(addressParts["country"]);
        $("#geocoded_latitude").val(latLng.lat());
        $("#geocoded_longitude").val(latLng.lng());

      }

      function geocodeAddress(address, zipcode, callbackFunction) {
        geocoder.geocode(
          {'address': address + ", " + zipcode, "region": "us"},
          function(results, status){
            if (status == google.maps.GeocoderStatus.OK){
              callbackFunction(results[0]);
              if (results[0]) {
                var addressParts = parseAddressComponents(results[0].address_components);
                setGeocoded(results[0].geometry.location, addressParts);
              }
            }
          });
      }

      function refocusMap(evt) {
        if (evt.originalEvent) {
          var address = $("#street_address").val();
          var zipcode = $("#zipcode").val();
          var isZipcode = $(this).attr("name") == "zipcode";
          if (zipcode != "") {
            geocodeAddress(address, zipcode, function(result) {
              self.marker.setPosition(result.geometry.location);
              self.map.fitBounds(result.geometry.viewport);
              self.map.setCenter(result.geometry.location);
            });
          }
          if (zipcode && address && $("#map-control").css("display") == "none") {
            $("#map-control").slideDown(200, function() {
              google.maps.event.trigger(self.map, 'resize');
              self.map.setCenter(self.marker.getPosition());
            });
          }
        }
      }

      $("#street_address, #zipcode").change(refocusMap);

      // Taken from old maps.js
      function parseAddressComponents(address_components) {
        var city = "";
        var state = "";
        var country = "";
        var street_number = "";
        var route = "";
        var postal_code = "";
        var chunks = address_components;
        var resultLength = chunks.length;

        // loop in reverse order to prefer locality over neighborhood for city assignment
        for (var i = resultLength-1; i >= 0; i--) {
          if ( (chunks[i]["types"].indexOf("neighborhood") != -1 ||
            chunks[i]["types"].indexOf("sublocality") != -1 ||
            chunks[i]["types"].indexOf("locality") != -1) && city == "") {
            city = chunks[i]["long_name"];
          } else if ( chunks[i]["types"].indexOf("administrative_area_level_1") != -1 && state == "" ) {
            state = chunks[i]["short_name"].toUpperCase();
          } else if ( chunks[i]["types"].indexOf("country") != -1 && country == "" ) {
            country = (chunks[i]["short_name"]).toUpperCase();
          } else if ( chunks[i]["types"].indexOf("street_number") != -1 && street_number == "" ) {
            street_number = chunks[i]["long_name"];
          } else if ( chunks[i]["types"].indexOf("route") != -1 && route == "") {
            route = chunks[i]["long_name"];
          } else if ( chunks[i]["types"].indexOf("postal_code") != -1 && postal_code == "") {
            postal_code = chunks[i]["long_name"];
          }
        }
        var street_address = street_number;
        if (route) {
          street_address += (" " + route);
        }
        return {
          "street_address":street_address,
          "city":city,
          "state":state,
          "postal_code":postal_code,
          "country":country
        };
      }
    },
    
    triggerResize: function() {
      google.maps.event.trigger(this.map, 'resize');
      this.map.setCenter(this.marker.getPosition());
    }

  });
  
});

