define('views/global/nav',[
  'jquery',
  'backbone',
  'libs/bootstrap'
], function($, Backbone, bootstrapNotAModule) {

  var NavView = Backbone.View.extend({
    events: {
      'click .mobile-nav-dropdown-icon': 'handleMobileNavDropdownIconClick'
    },
    initialize: function() {
      this.user = this.options.user;
      this.updateActivityCount();
    },
    updateActivityCount: function () {
      if (this.user.isLoggedIn()) {
        $.ajax({
          type: 'GET',
          url: '/ajax/get-activity',
          dataType: 'json',
          context: this
        }).done(function (data) {
          if (data.count > 0) {
            this.$el.find('.pending-num').html(data.count);
          }
        });
      }

    },
    handleMobileNavDropdownIconClick: function(event) {
      this.toggleMobileMenu();
    },
    toggleMobileMenu: function() {
      this.$el.find('.mobile-menu-list').toggle();
    }
  });
  return NavView;
});

