define('views/car/car_edit_calendar',[
  'jquery',
  'backbone',
  'common',
  'utils/loader',
  'views/car/monthly_calendar',
  'views/car/weekly_schedule'
], function(
  $,
  Backbone,
  common,
  loader,
  MonthlyCalendarView,
  WeeklyScheduleView
){

  return CarEditCalendarView = Backbone.View.extend({
  
    events: {
      'change .minimum-rental': 'submitForm'
    },

    initialize: function() {
      this.carID          = this.options.carID;
      this.weeklySchedule = this.options.weeklySchedule;
      this.calendar       = this.options.calendar;

      this.render();
    },

    render: function() {
      this.monthlyCalendarView = new MonthlyCalendarView({
        el: '#car-edit-monthly-calendar',
        collection: this.calendar
      });

      this.weeklyScheduleView = new WeeklyScheduleView({
        el: '.weekly-schedule',
        carID: this.carID,
        unavailableRanges: this.weeklySchedule
      });

      this.$form = this.$el.find('.calendar-form');

      return this;
    },
    
    submitForm: function() {
      this.$form.ajaxSubmit().data('jqxhr')
        .done(function(data, status, xhr) {
          if (data !== 'OK') {
            loader.showErrorDialog('There was an error savings your changes. Please reload the page to try again.');
          }
        })
        .fail(function(jqXHR, textStatus, errorThrown) {
            loader.showErrorDialog('There was an error savings your changes. Please reload the page to try again.'); 
        });
    },

    
  });

});

