define('views/car/owner_key',[
  'jquery',
  'backbone',
  'config',
  'utils/maps',
  'models/car',
  'models/rental',
  'views/car/key_map',
  'views/car/lock_unlock_buttons',
  'views/car/owner_key_active_rental'
], function(
  $,
  Backbone,
  config,
  maps,
  Car,
  Rental,
  KeyMapView,
  LockUnlocksButtonView,
  OwnerKeyActiveRentalView
) {

  return OwnerKeyView = Backbone.View.extend({
    el: '#content',

    initialize: function() {
      // Setup models and collections
      this.car             = new Car().set(config.globals.car);
      this.isUserAllowedToLocateCar  = config.globals.isUserAllowedToLocateCar;
      
      this.hasActiveRental = !_.isUndefined(config.globals.rental);      
      if (this.hasActiveRental) {
        this.activeRental = new Rental().set(config.globals.rental);  
      }
      
      this.render();
      this.activate();
    },

    render: function() {
      var self = this;

      this.lockUnlockButtonsView = new LockUnlockButtonsView({
        el: '#owner-key-lock-unlock-buttons',
        model: this.car,
        hasActiveRental: this.hasActiveRental,
        isOwner: true,
        isUserAllowedToAccessCar: true
      });

      if (!this.isUserAllowedToLocateCar && this.hasActiveRental) {
        this.ownerKeyActiveRentalView = new OwnerKeyActiveRentalView({
          el: '#owner-key-active-rental',
          model: this.activeRental
        });        
      }
      else if (this.isUserAllowedToLocateCar) {
        maps.onScriptLoad(function() {
          self.keyMapView = new KeyMapView({
            el: '#key-map',
            model: self.car,
            isUserAllowedToLocateCar: self.isUserAllowedToLocateCar
          });
          self.resizeUI(); // Size map after it is loaded
        });
      }
    },

    activate: function() {
      var self = this;

      $(document).on('appLayoutChange', function() {
        self.resizeUI();
      });

    },

    resizeUI: function(event){
      var windowWidth             = $(window).width();
      var windowHeight            = $(window).height();
      var $map                    = this.$el.find('#key-map');

      if ($map.is(':visible')) {
        // Once the viewport becomes 320px (iPhone portrait) or smaller, we dock
        // the lock and unlock buttons to the bottom of the viewport. 
        //
        // If the buttons are docked, decrease the height of the map so it doesn't
        // fall underneath the buttons.
        var buttonHeightOffset = (windowWidth <= 320) ? this.$el.find('.lock-unlock-buttons').height() : 0;
        $map.height(windowHeight - $map.offset().top - buttonHeightOffset);
      }
    }

  });

});

