// availability_matrix.js
//
// As seen on the car profile page.
//
// It details the hours of the week in which a car is available for rental. It
// allows the user to scroll through the weeks from the current week through
// three months in to the future.

var MAX_WEEKS = 13; // 13 allows for an extra week and good luck.


define('common/partials/availability_matrix',[
  'jquery',
  'common',
  'utils/converters',
  'utils/formatters',
  'utils/time',
], function(
  $,
  common,
  converters,
  formatters,
  time
){

  var $element = $();
  var carData;
  var today;
  var weekOffset;
  var weekStart;
  var weekEnd;
  var $slots;

  // =================
  // init
  // =================
  var init = function(element, data) {

    $element   = $(element);
    $slots     = $element.find('.slot');
    carData    = data;
    today      = new Date();
    weekOffset = 0;

    // Convert dates from ISO to JS Dates
    for (var i = 0; i < carData.availability.length; i++) {
      // Rounding time here, we saw some issues where start and end time were not
      // rounded to the 1/4 hour and caused and issue in updateRange with
      // var $slot = $slots.eq((row * 7) + col);
      // that eq was trying to reference a float instead of an int
      // this was likely a legacy issue and only seen on Bodhi's Machine
      // https://sprint.ly/product/15733/#!/item/1639
      carData.availability[i].start_time = time.roundJSDateToQuarterHour(converters.ISODateToJSDate(carData.availability[i].start_time));
      carData.availability[i].end_time = time.roundJSDateToQuarterHour(converters.ISODateToJSDate(carData.availability[i].end_time));
    }

    renderDays();
    bindUIEvents();
  };


  var updateRange = function(startTime, endTime, calendarEventType) {
    
    var cssClass;

    switch(calendarEventType) {
      case 'UNAVAILABLE':
        cssClass = 'unavailable';
        break;
      case 'BUFFER':
        cssClass = 'unavailable';
        break;
      case 'RENTED_OUT':
        cssClass = 'unavailable';
        break;
      case 'INACCESSIBLE':
        cssClass = 'inaccessible';
        break;
    }

    var count    = 0;
    var tempTime = new Date(startTime);
    var lastRow = null;
    var lastColumn = null;

    // Loop through the time range in 15 min intervals and update the table slots
    while (tempTime < endTime && tempTime < weekEnd && count < $slots.length) {

      if (tempTime >= weekStart && tempTime < weekEnd) {
        var col   = tempTime.getDay();
        var row   = (tempTime.getHours() * 4) + (tempTime.getMinutes() / 15);
        // Taking the floor since we are dividing and it could cause a non int
        var $slot = $slots.eq(Math.floor((row * 7) + col));
        $slot
          .removeClass('available')
          .addClass(cssClass);
        if (lastRow < row - 1 && col === lastColumn) {
          for (var rowToFill = lastRow; rowToFill < row; rowToFill++) {
            var $slot = $slots.eq((rowToFill * 7) + col);
            $slot
              .removeClass('available')
              .addClass(cssClass);

              }
        }
        lastRow = row;
        lastColumn = col;
      }
      tempTime = new Date(tempTime.getTime() + (15 * 60000));
      count++;
    }
  };


  var renderDays = function() {

    var $dayLabels = $element.find('.column-headings .day-label');

    // First move the week ahead by the week offset.
    var headerDay  = new Date();
    headerDay.setDate(today.getDate() + (7 * weekOffset));
  
    // Then offset our header day to Sunday so we can loop through the days in order.
    headerDay.setDate(headerDay.getDate() - headerDay.getDay());

    weekStart = new Date(headerDay.getFullYear(), headerDay.getMonth(), headerDay.getDate());
    weekEnd   = new Date(weekStart);
    weekEnd.setDate(weekEnd.getDate() + 7);

    $element.find('.back-button, .next-button').removeClass('disabled');
    if (weekOffset === 0) {
      $element.find('.back-button').addClass('disabled');
    } else if (weekOffset == MAX_WEEKS) {
      $element.find('.next-button').addClass('disabled');
    }

    var $slot;
    for (var i = 0; i < $slots.length; i++) {
      $slot = $slots.eq(i);
      $slot
        .addClass('available')
        .removeClass('inaccessible unavailable today');

      if (weekOffset === 0 && i % 7 === today.getDay()) {
        $slot.addClass("today");
      }
    }

    for (i = 0; i < 7; i++) {
      // If column is showing the current day, add a css class of 'today'. We are using this to make
      // the column heading bold.
      if (time.areDatesOnSameDay(headerDay, new Date())) {
        $($dayLabels[i])
          .addClass('today')
          .text('Today');
      } else {
        $($dayLabels[i])
          .removeClass('today')
          .html(formatters.formatWeekDayDate(headerDay));
      }
      
      headerDay.setDate(headerDay.getDate() + 1);
    }

    for (i = 0; i < carData.availability.length; i++) {
      var period = carData.availability[i];
      if (period.start_time >= weekStart && period.start_time < weekEnd) {
        // start with start_time loop through to end_time or weekEnd.
        updateRange(period.start_time, period.end_time, period.calendar_event_type);
      }
      else if (period.end_time > weekStart && period.start_time < weekEnd) {
        // start with weekStart and loop through hours until end_time or weekEnd
        updateRange(weekStart, period.end_time, period.calendar_event_type);
      }
    }

  };

  var bindUIEvents = function () {
    var onNext = function() {
      if (weekOffset < MAX_WEEKS) {
        weekOffset++;
        renderDays();
      }
    };

    var onBack = function() {
      if (weekOffset > 0) {
        weekOffset--;
        renderDays();
      }
    };

    $element
      .on("click", ".next-button", onNext)
      .on("click", ".back-button", onBack);

  };


  return {

    init: init

  };

});
