define('views/car/car_edit_connect',[
  'jquery',
  'backbone',
  'views/car/security_toggle',
  'libs/jquery.form',
  'utils/loader'
], function(
  $,
  Backbone,
  SecurityToggleView,
  jQueryForm,
  loader
){

  return CarEditConnectView = Backbone.View.extend({
  
    events: {
      'change .security-checkbox-on': 'handleSecurityCheckboxChange'
    },

    initialize: function() {
      this.render();
    },

    render: function() {
      this.securityToggleView = new SecurityToggleView({
        el: '#car-edit-security-toggle',
      });
    },

    handleSecurityCheckboxChange: function(event) {
      this.securityToggleView.updateHiddenCheckbox();
      this.submitForm();
    },

    submitForm: function() {
      this.$el.find('.connect-form').ajaxSubmit().data('jqxhr')
        .done(function(data, status, xhr) {
          if (data !== 'OK') {
            loader.showErrorDialog('There was an error savings your changes. Please reload the page to try again.');
          }
        })
        .fail(function(jqXHR, textStatus, errorThrown) {
          loader.showErrorDialog('There was an error savings your changes. Please reload the page to try again.');
        });
    }    
  });

});

