define('trips/owner_got_rental',[
  "jquery",
  "common",
  "common/partials/messaging",
  "common/partials/rate_user"
],
function($, common, messaging, rateUser) {

  common.pageReady("trips-owner_got_rental", function(){

    messaging.init('.messaging');

    if(GA_ARGS.rental_complete && !GA_ARGS.owner_gave_feedback && GA_ARGS.rental_status === "active"){
      rateUser.init('.rate-user');
    }

  });
});
