// owner_decline.js
// ----------------
// public  init()
// public  show()
// public  hide()
// private declineRental()
// ---------------
// This modal dialog confirms that the owner wants to deny a rental request. The owner
// is required to provide a reason that will be shared with the renter.
//
// This dialog is used on three different pages:
// - dashboard/activity
// - dashbaord/dashboard
// - trips/rental
//
// Emails link directly to trips/rental with a url parameter action=decline that
// pops up the dialog on page load.

define('common/partials/dialogs/owner_decline',[
  'jquery',
  'common',
  'text!templates/owner_decline.handlebars',
  'common/partials/dialogs/manage_calendar',
  'utils/loader',
  'libs/bootstrap',
  'libs/handlebars'
], function($, common, dialogHandlebars, manageCalendarDialog, loader){

  var DECLINE_REASONS_RELATED_TO_SCHEDULE = [
    'car-serviced', // my car is in the shop at the moment.
    'car-used',     // my car is already being used so I can't accept.
    'key-handoff',   // I have a conflict and a can't arrange a key hand off,
    'other'
  ];

  var $element      = $(),
      dialogTemplate,
      requestData;

  var init = function( ){

    dialogTemplate = Handlebars.compile( dialogHandlebars );

  };


  // =================
  // show
  // =================
  var show = function( data ){

    requestData = data;
    requestData.renter = ( typeof requestData.renter === "undefined" )? "the renter": requestData.renter;
    requestData.renterPhoto = common.fixImageUrl(requestData.renterPhoto, 150, 150);

    // If modal already exists from a previous show call: remove, compile new html and add to DOM, and display modal
    $('#owner-decline-dialog').remove();
    $('body').append( dialogTemplate( requestData ));

    $element = $('#owner-decline-dialog');
    $element.modal();


    //
    // Attach UI events
    //

    // When a radio button value is chosen we need to update the UI
    $element.on( 'change', '.radio', function( event ){

      var radio                = $(this).find('input[type="radio"]'),
          declineAutoMessage   = $.trim( radio.data('message') ),
          $messageForm         = $element.find( '.message-to-renter' ),
          $declineMessageInput = $element.find('.decline-message-input'),
          isMessageFormVisible = !!$messageForm.is( ':visible' ),           // !! <-- converts to Boolean
          isOtherChosen        = !!( radio.attr( 'value' ) === "other" );


      // Enable the user to submit the form
      $element.find('.confirm-btn').removeAttr( 'disabled' );

      // Populate the message textarea with one of our canned messages and
      // fire the change event. Change event doesn't fire x-browser when you programmatically
      // change the value of a textarea.
      $declineMessageInput.val( declineAutoMessage );
      $declineMessageInput.trigger( 'change' );

      // Show/hide different UI elements depending on if the message form
      // is already visisble and which radio button has been chosen.
      if( isMessageFormVisible && isOtherChosen ){

        // Show the Other text input and give it focus
        $element.find('.other-reason-input').animate( { opacity: '1'} , 'fast', function( event ){
          $(this).focus();
        });

      } else if( !isMessageFormVisible && isOtherChosen ){

        // Show the Other text input and give it focus, then show the Message form
        $element.find('.other-reason-input').animate( { opacity: '1'} , 'fast', function( event ){
          $(this).focus();
          $messageForm.slideDown( 'slow' );
        });

      } else if( isMessageFormVisible && !isOtherChosen){

        // Give focus to the Message textarea and hide the Other text input
        $declineMessageInput.focus();
        $element.find('.other-reason-input').animate( { opacity: '0'} , 'slow');

      } else if( !isMessageFormVisible && !isOtherChosen ){

        // Show the Message form and give the textarea focus
        $messageForm.slideDown( 'slow', function( event ) {
          $declineMessageInput.focus();
        });

      }

    });

    $element.on( 'click', '.cancel-btn', hide );

    // Don't allow the user to submit the form if the decline message textarea is empty.
    $element.on( 'keyup change', '.decline-message-input', function( event ){
      if( $(this).val() === "" ){
        $element.find('.confirm-btn').attr( 'disabled', 'disabled' );
      } else {
        $element.find('.confirm-btn').removeAttr( 'disabled' );
      }


    });

    $element.on( 'click', '.confirm-btn', function( event ){

      loader.setButtonSubmitting( $element.find('.confirm-btn') );
      declineRental();

    });

  };

  // =================
  // hide
  // =================
  var hide = function(){
    $element.modal( 'hide' );
  };


  // =================
  // declineRental
  // =================
  var declineRental = function( ){

    var declineReason  = $element.find('input[name=decline-reason]:checked').val(),
        otherReason    = $element.find('.other-reason-input').val(),
        declineMessage = $element.find('.decline-message-input').val(),
        isDeclineReasonRelatedToSchedule = false;


    for( var i = 0, l = DECLINE_REASONS_RELATED_TO_SCHEDULE.length; i < l; i++ ){
      if ( DECLINE_REASONS_RELATED_TO_SCHEDULE[ i ] ===  declineReason ){
        isDeclineReasonRelatedToSchedule = true;
      }
    }

    $.post(
      "/ajax/decline",
      {
        "bundle_id": requestData.bundleId,
        "decline_reason": declineReason,
        "other_reason": otherReason,
        "decline_message": declineMessage
      },
      function( data ){

        // If decline reason is related to schedule, show dialog that let's the car owner stop
        // receiving requests for a range of days.
        if ( isDeclineReasonRelatedToSchedule ){
          manageCalendarDialog.init();
          hide();
          manageCalendarDialog.show({
            startUnixTime: requestData.requestStartUnixTime,
            endUnixTime: requestData.requestEndUnixTime,
            carId: GA_ARGS["car_id"]
          });
        } else {

          // Reload current page WITHOUT url parameters
          // The reason we strip url parameters is that in our emails we link to the trip page with
          // the parameter action=decline which pops up the modal dialog on page load. If the user
          // declines the request and we reload the page, the decline confirm dialog would pop up
          // again unless we removed the action=decline parameter.
          window.location = window.location.href.split("?")[0];
        }
      }
    );
  };


  return {

    init: init,

    show: show,

    hide: hide

  };
});
