define('views/car/monthly_calendar_day_popup_event',[
  'jquery',
  'backbone',
  'utils/converters',
  'utils/time',
  'utils/loader',  
  'text!templates/car/monthly_calendar_day_popup_event.handlebars',
  'libs/handlebars'
], function(
  $,
  Backbone,
  converters,
  time,
  loader,
  eventHandlebars
){

  var MonthlyCalendarDayPopupEventView = Backbone.View.extend({
    template: Handlebars.compile(eventHandlebars),
    events: {
      'click .unblock-button': 'handleUnblockButtonClick'
    },
    
    initialize: function() {    
      this.carID = this.options.carID;
      this.day   = this.options.day;

      this.render();
    },

    render: function() {
      var startISO   = time.roundISODateToQuarterHour(this.model.get('start'));
      var endISO     = time.roundISODateToQuarterHour(this.model.get('end'));
      var dayStartMS = this.day.getTime();
      var dayEndMS   = dayStartMS + (24 * 60 * 60 * 1000);

      var startDate  = converters.ISODateToJSDate(startISO);
      var endDate    = converters.ISODateToJSDate(endISO);
      var startMS    = startDate.getTime();
      var endMS      = endDate.getTime();
      var startLabel;
      var endLabel;
      
      // If start or end time is the same as the selected day then we drop the day in label
      // Ex: "9/26 9:00pm" becomes "9:00 PM"
      if (time.areDatesOnSameDay(startDate, this.day)){
        startLabel = moment(startISO).format("h:mm A");
      } else {
        startLabel = moment(startISO).format("MMM D, h:mm A");
      }

      if (time.areDatesOnSameDay(endDate, this.day)){
        endLabel = moment(endISO).format("h:mm A");
      } else {
        endLabel = moment(endISO).format("MMM D, h:mm A");
      }

      var statusLabel = '';
      var isBlocked = false;
      switch(this.model.get('type')) {
        case 'RENTED_OUT':
          statusLabel = 'Rental';
          break;
        case 'REQUEST':
          statusLabel = 'Request';
          break;
        case 'UNAVAILABLE':
          statusLabel = 'Blocked';
          isBlocked   = true;
          break;
      }

      this.$el.html(this.template({
        'cssClass': statusLabel.toLowerCase(),
        'statusLabel': statusLabel,
        'startLabel': startLabel,
        'endLabel': endLabel,
        'isBlocked': isBlocked
      }));
      return this;
    },
    
    handleUnblockButtonClick: function(event) {
      var self   = this;

      var startISO = converters.makeISODateTimezoneNaive(this.model.get('start'));
      var endISO   = converters.makeISODateTimezoneNaive(this.model.get('end'));

      this.showUnblockProcessing();

      this.collection.unblock(this.carID, startISO, endISO, 'monthly-calendar')
        .done(function(data, status, xhr) {
          if (data.result === 'success') {
            self.model.destroy();
          } else {
            loader.showErrorDialog('There was an error unblocking the time. Please reload the page to try again.');
            this.hideUnblockProcessing();
          }
        })
        .fail(function(jqXHR, textStatus, errorThrown) {
            loader.showErrorDialog('There was an error unblocking the time. Please reload the page to try again.');
            this.hideUnblockProcessing();
        });
    },

    showUnblockProcessing: function() {
      this.$el.find('.unblock-link').text('');
      this.$el.find('.spinner').css('display', 'block');
    },

    hideUnblockProcessing: function() {
      this.$el.find('.unblock-link').text('Unblock');
      this.$el.find('.spinner').css('display', 'none');
    }
    
  });

  return MonthlyCalendarDayPopupEventView;
});

