define('models/search_results',[
  'underscore',
  'backbone',
  'config',
  'models/search_car',
  'libs/jquery.xdomainrequest'
], function(_, Backbone, config, SearchCar, xDomainRequest){

  var QUERY_PROPERTIES = [
    "car_id",
    "car_name",
    "car_photo",
    "carkit_enabled",
    "distance",
    "instant_rental",
    "latitude",
    "longitude",
    "make",
    "model",
    "price_daily",
    "price_hourly",
    "price_weekly",
    "total_price",
    "timezone",
    "year"];

  var SORT_TYPE      = "best"; // Maybe "magic_sans_distance" is more descriptive.
  var PAGE_SORT_TYPE = "magic";

  var SearchResults = Backbone.Collection.extend({
    model: SearchCar,
    initialize: function() {
      // copy array so we can remove failing backends with out changing the original list.
      this.backendsList = config.globals.index_server_backends.slice(0);
    },
    // Overriding the fetch method
    fetch: function(params) {
      // Fetch cars from the index server.
      var paramsString = "?" + params +
          "&properties=" + QUERY_PROPERTIES.join(",") +
          "&sort=" + SORT_TYPE +
          "&page_sort=" + PAGE_SORT_TYPE +
          "&page_size=" + config.MAX_CARS_TO_DISPLAY_IN_SEARCH;
      this.trigger('beforeFetch');
      this._recursiveFetchHelper(paramsString);
    },
    _recursiveFetchHelper: function(paramsString) {
      // Keep fetching on backend errors until we've exhausted our backend list.

      var backendIndex = Math.floor(Math.random() * this.backendsList.length);
      var url = this.backendsList[backendIndex] + paramsString;
      var self = this;

      $.ajax({
        type: 'GET',
        url: url,
        dataType: 'json',
        success: function(data) {
          self.set(data.cars);
          self.trigger('afterFetch');
        },
        error: function(jqXHR, textStatus, errorThrown) {
          if (textStatus === 'timeout' || textStatus === 'error' || textStatus === 'abort') {
            if (self.backendsList.length > 0) {
              self.backendsList.splice(backendIndex, 1); // Remove the bad index server.
              self._recursiveFetchHelper(paramsString);
            }
            else {
              console.log("ERROR: all backends timed out or errored.");
              // restore the backends list so we can try again.
              self.backendsList = config.globals.index_server_backends.slice(0);
              self.trigger('afterFetch');
            }
          }
          else {
            console.log("ERROR: Unknown error on fetch. " + textStatus + " - " + errorThrown);
            self.trigger('afterFetch');
          }
        }
      });
    }
  });

  return SearchResults;
});

