// countdown.js
// --------
// Creates a countdown timer.
// Used on the request page.

define('common/partials/countdown',[
  'jquery',
  'text!templates/countdown.handlebars',
  'libs/handlebars'
], function( $, countdownHandlebars ){

  var $element      = $(),
      countdownTemplate,
      countdownInterval;


  // =================
  // init
  // =================
  var init = function(element, timeRemainingInSeconds) {

    $element = $(element);
    countdownTemplate = Handlebars.compile(countdownHandlebars);
    $element.html(countdownTemplate);

    var count = timeRemainingInSeconds,
        hours,
        minutes,
        seconds;

    var countdown = function(){

      hours   = Math.floor((count / 60) / 60).toString();
      minutes = Math.floor((count / 60) % 60).toString();
      seconds = Math.floor(count % 60).toString();

      // Prepend zeroes to single digit numbers.
      // ex. "9" becomes "09"
      hours   = (hours.length === 1)? "0" + hours: hours;
      minutes = (minutes.length === 1)? "0" + minutes: minutes;
      seconds = (seconds.length === 1)? "0" + seconds: seconds;

      $element
        .find('.hours .pos-1').html(hours[0])
        .end()
        .find('.hours .pos-2').html(hours[1])
        .end()
        .find('.mins .pos-1').html(minutes[0])
        .end()
        .find('.mins .pos-2').html(minutes[1])
        .end()
        .find('.secs .pos-1').html(seconds[0])
        .end()
        .find('.secs .pos-2').html(seconds[1]);

      // Hide action buttons when countdown expires
      if (count <= 0) {
        clearInterval(countdownInterval);
        $('.request-actions .action-buttons').slideUp();
        $('.request-stamp').html('<div class="rental-status"><div class="stamp expired"><div class="stamp-border"><div class="status-header">Rental</div><div class="rental-status">Expired</div></div></div></div>');
      }
      count--;
    };

    countdown();
    countdownInterval = setInterval(countdown, 1000);
  };

  return {
    init: init
  };

});
