define('models/car',[
  'backbone',
  'libs/deep-model',
  'utils/maps'
], function(
  Backbone,
  DeepModel,
  maps
){

  return Car = Backbone.DeepModel.extend({
    // This model is used to store small, medium, and large car objects as defined in 
    // util/model_translation.py
    
    getParkingLabel: function() {
      if (this.get('parking.mode') === "GETAROUND_RESERVED") {
        return "Dedicated parking";
      } else {
        switch (this.get('parking.location_type')) {
          case "STREET": return "Street parking";
          case "GARAGE": return "Garage";
          case "SURFACE_LOT": return "Parking lot";
          case "DRIVEWAY": return "Driveway parking";
          case "VALET": return "Valet parking";
        }
      }
      return "Parking";
    },

    getParkingGoogleMapsLatLng: function() {
      return maps.createGoogleLatLng(this.get('location.lat'), this.get('location.lon'));
    }

  });

});
