define('views/car/car_edit_listing',[
  'jquery',
  'backbone',
  'common/validators',
  'libs/jquery.form',
  'libs/jquery.validate',
  'utils/loader',
  'views/car/listing_toggle',
  'views/shared/make_model_inputs'
], function(
  $,
  Backbone,
  validators,
  jQueryForm,  
  jQueryValidate,
  loader,
  ListingToggleView,
  MakeModelInputsView
){

  return CarEditListingView = Backbone.View.extend({
  
    initialize: function() {
      this.render();
      this.activate();
    },

    render: function() {
      this.makeModelInputsView = new MakeModelInputsView();
      this.listingToggleView   = new ListingToggleView();

      this.$form       = this.$el.find('.listing-form');
      this.$saveButton = this.$form.find('.save-button');
      return this;
    },

    activate: function() {
      var self = this;

      this.$form.validate({
        errorPlacement : function(error, element) {
          if (element.is(":radio")) {
            error.appendTo(element.closest('.control-group'));
          } else {
            error.insertAfter(element);
          }
        },
        rules: {
          make: {
            required: true
          },
          year: {
            required: true,
            carYear: true
          },
          model: {
            required: true
          },
          mileage: {
            required: true
          },
          transmission: {
            required: true
          },
          color: {
            required: true,
            asciiOnly: true
          },
          license_state: {
            required: true
          },
          license_plate: {
            asciiOnly: true,
            minlength: 2,
            noSpaces: true
          },
          vin: {
            vinUS: true
          },
          street_address: {
            required: true,
            asciiOnly: true
          },
          zipcode: {
            required: true,
            zipcodeUS: true
          }
        },
        submitHandler: function(form) {
          loader.setButtonSubmitting(self.$saveButton);
          self.$form.ajaxSubmit().data('jqxhr')
            .done(function(data, status, xhr) {
              if (data !== 'OK') {
                loader.showErrorDialog('There was an error savings your changes. Please reload the page to try again.');
              }
            })
            .fail(function(jqXHR, textStatus, errorThrown) {
              loader.showErrorDialog('There was an error savings your changes. Please reload the page to try again.');
            })
            .always(function(xhr, status) {
              loader.clearButtonSubmitting(self.$saveButton);
            });  
        }
      });
    }
    
  });

});

