define('views/search/filters',[
  'jquery',
  'backbone',
  'config',
  'libs/jquery_ui',
  'libs/jquery.ui.touch-punch',
  'text!templates/search/filters.handlebars',
  'libs/handlebars'
], function(
  $,
  Backbone,
  config,
  jQueryUI,
  jqueryUITouchPunch,
  filtersHandlebars
){

  var FiltersView = Backbone.View.extend({
    template: Handlebars.compile(filtersHandlebars),
    events: {
      'click .toggle-filters-panel-button': 'handleFiltersButtonClick',
      'change input[name=filters-price]': 'handlePriceTimingChange',
      'change .filters-instant': 'handleInstantChange',
      'change .filters-transmission': 'handleTransmissionChange',
      'change .filters-body-type': 'handleBodyTypeChange'
    },
    
    initialize: function() {
      this.veryLargeNumber        = Math.pow(10, 9);
      this.MAX_PRICE_SLIDER_VALUE = 100;

      this.render();
      this.activate();
    },

    render: function() {
      this.$el.html(this.template());

      // Cache jQuery objects
      this.$filtersPanel           = this.$el.find('.filters-panel');
      this.$automaticCheckbox      = this.$el.find('.filters-transmission-automatic');
      this.$manualCheckbox         = this.$el.find('.filters-transmission-manual');
      this.$bodyTypeCheckboxes     = this.$el.find('.filters-body-type');
      this.$carCheckbox            = this.$el.find('.filters-body-type-car');
      this.$convertibleCheckbox    = this.$el.find('.filters-body-type-convertible');
      this.$suvCheckbox            = this.$el.find('.filters-body-type-suv');
      this.$truckCheckbox          = this.$el.find('.filters-body-type-truck');
      this.$vanCheckbox            = this.$el.find('.filters-body-type-van');

      return this;
    },

    activate: function() {
      var self = this;
      var showFiltersOnLoad = false;

      this._enablePriceSlider();

      this._updateUI();

      if (this.model.areAnyFiltersSet()) {
        this.showPanel();
      }

      this.listenTo(this.model, 'change:priceValue', this.handlePriceValueChange);
    
      this.listenTo(this.model, 'change', function(model) {
        var changedAttributes = _.keys(model.changedAttributes());
        
        // On changes to any of the following attributes we update the filters UI controls
        if (_.intersection(changedAttributes, [
          'bodyType',
          'max_hourly_price',
          'instant',
          'transmission']).length > 0) {
          self._updateUI();
         }
      });
    },

    _updateUI: function() {
      // ------------
      // Price slider 
      // ------------
      var priceValue; // If this gets set we need to set the value of the slider.

      if (this.model.has('max_hourly_price')) {
        priceValue = this.model.get('max_hourly_price');
      }

      if (priceValue > 0) {
        this.model.set('priceValue', priceValue);

        this.$el.find('.filters-price-slider').slider(
          'value',
          this._inverseComputeSliderValue(priceValue)
        );
        this.updatePriceValueDisplay(priceValue);
      } else {
        this.$el.find('.filters-price-slider').slider('value', this.MAX_PRICE_SLIDER_VALUE);
        this.updatePriceValueDisplay(this.MAX_PRICE_SLIDER_VALUE);
      }


      // -------
      // Instant
      // -------
      if (this.model.has('instant')) {
        this.$el.find('.filters-instant').prop('checked', true);
      } else {
        this.$el.find('.filters-instant').prop('checked', false);
      }

      // ------------
      // Transmission
      // ------------
      if (this.model.has('transmission')) {
        var transmission = this.model.get('transmission');
        
        if (transmission === 'automatic') {
          this.$automaticCheckbox.prop('checked', true);
          this.$manualCheckbox.prop('checked', false);
        } else if (transmission === 'manual') {
          this.$automaticCheckbox.prop('checked', false);
          this.$manualCheckbox.prop('checked', true);
        }

      } else {
        var automaticChecked = this.$automaticCheckbox.is(':checked');
        var manualChecked    = this.$manualCheckbox.is(':checked');
        // When 'transmission' is not set in the model...
        // If both Automatic and Manual are checked, don't change anything. If both trasnmission
        // types are checked or both are unchecked, they retun the same search results. What we want
        // to avoid is when the user checks Automatic and then checks Manual and we uncheck both
        // controls jarringly.
        if (!(automaticChecked && manualChecked)) {
          this.$automaticCheckbox.prop('checked', false);
          this.$manualCheckbox.prop('checked', false);
        }
      }

      // ---------
      // Body type
      // ---------
      if (this.model.has('bodyType')) {
        var bodyTypes = this.model.get('bodyType').split(',');
        this.$carCheckbox.prop('checked', (bodyTypes.indexOf('car') > -1));
        this.$convertibleCheckbox.prop('checked', (bodyTypes.indexOf('convertible') > -1));
        this.$suvCheckbox.prop('checked', (bodyTypes.indexOf('suv') > -1));
        this.$truckCheckbox.prop('checked', (bodyTypes.indexOf('truck') > -1));
        this.$vanCheckbox.prop('checked', (bodyTypes.indexOf('van') > -1));
      } else {
        // Similar to how we handle transmission, if the user has selected all the body type
        // checkboxes then bodyType on the model is unset. But we don't want to clear all
        // the checkboxes if the user has just checked them all. So leave them be in this case.
        var numBodyTypes        = this.$bodyTypeCheckboxes.length;
        var numBodyTypesChecked = this.$bodyTypeCheckboxes.filter(':checked').length;
        if (numBodyTypes !== numBodyTypesChecked) {
          this.$bodyTypeCheckboxes.prop('checked', false);
        }
      }
    },

    handleFiltersButtonClick: function(event) {
      if (this.$filtersPanel.is(':visible')){
        this.hidePanel();
      } else {
        this.showPanel();
      }
    },

    handlePriceValueChange: function(event) {
      this.setPriceFilterValue();
    },

    handlePriceTimingChange: function(event) {
      this.setPriceFilterValue();
    },

    handleInstantChange: function(event){
      if (this.$el.find('.filters-instant').is(':checked')) {
        this.model.set("instant", true);
      }
      else {
        this.model.unset("instant");
      }
    },

    handleTransmissionChange: function(event) {
      var automaticChecked = this.$automaticCheckbox.is(':checked');
      var manualChecked    = this.$manualCheckbox.is(':checked');
      
      if ((automaticChecked && manualChecked) || (!automaticChecked && !manualChecked)) {
        this.model.unset("transmission");
      } else if (automaticChecked) {
        this.model.set('transmission', 'automatic');
      } else if (manualChecked) {
        this.model.set('transmission', 'manual');
      }
    },

    handleBodyTypeChange: function(event) {
      var convertibleChecked = this.$convertibleCheckbox.is(':checked');
      var carChecked         = this.$carCheckbox.is(':checked');
      var suvChecked         = this.$suvCheckbox.is(':checked');
      var vanChecked         = this.$vanCheckbox.is(':checked');
      var truckChecked       = this.$truckCheckbox.is(':checked');
      
      var numBodyTypes        = this.$bodyTypeCheckboxes.length;
      var numBodyTypesChecked = this.$bodyTypeCheckboxes.filter(':checked').length;
      
      if (numBodyTypesChecked === 0 || numBodyTypes === numBodyTypesChecked) {
        this.model.unset('bodyType');
      } else {
        var bodyTypeValue;
        bodyTypeValue = (carChecked ? 'car,' : '');
        bodyTypeValue = bodyTypeValue + (convertibleChecked ? 'convertible,' : '');
        bodyTypeValue = bodyTypeValue + (suvChecked ? 'suv,' : '');
        bodyTypeValue = bodyTypeValue + (vanChecked ? 'van,' : '');
        bodyTypeValue = bodyTypeValue + (truckChecked ? 'truck,' : '');
        bodyTypeValue = bodyTypeValue.slice(0, - 1); // Remove trailing comma

        this.model.set('bodyType', bodyTypeValue);
      }
    },
    
    // When the filters panel is opened we begun listening to all mouse click events. If a click
    // happens outside of the filters panel, we hide the panel.
    handleDocumentClick: function(event) {
      // Reference to FiltersView is passed in via event.data
      var self = event.data;

      if ($(event.target).closest('.filters-panel').length === 0){
        self.hidePanel();
      }
    },

    _enablePriceSlider: function() {
      var self = this;
      this.$el.find('.filters-price-slider').slider({
        range: 'min',
        min: 0,
        max: this.MAX_PRICE_SLIDER_VALUE,
        value: this.MAX_PRICE_SLIDER_VALUE,
        slide: function(event, ui) {
          var computedValue = self._computeSliderValue(ui.value);
          self.updatePriceValueDisplay(computedValue);
        },
        stop: function(event, ui) {
          self.model.set("priceValue", self._computeSliderValue(ui.value));
        }
      });
    },
    
    showPanel: function() {
      var self = this;
      this.$filtersPanel.show();
      setTimeout( function(){
        $(document).on('click', self, self.handleDocumentClick);
      }, 50);
    },
    
    hidePanel: function() {
      this.$filtersPanel.hide();
      $(document).off('click', this.handleDocumentClick);
    },

    updatePriceValueDisplay: function(priceValue) {
      var displayPrice;
      if (priceValue >= this.MAX_PRICE_SLIDER_VALUE) {
        this.$el.find('.price-control-box').addClass('any-price');
        hourlyPrice = 'Any price';
        dailyPrice  = '&nbsp;';
      } else {
        this.$el.find('.price-control-box').removeClass('any-price');
        hourlyPrice = '$' + priceValue;
        dailyPrice  = '$' + priceValue * config.globals.hourly_daily_ratio;
      }
      this.$el.find('.hourly-price-value').text(hourlyPrice);
      this.$el.find('.daily-price-value').text(dailyPrice);
    },

    _computeSliderValue: function (value) {
      // Exponential scaling for easier selection of smaller values.
      // No decimals on the dollars for now.
      // All values over 500 are set to 1000000000, and display 'Any price'.
      var scaledResult = (Math.exp(0.05 * value) + 4).toFixed(0);
      if (scaledResult >= this.MAX_PRICE_SLIDER_VALUE) {
        scaledResult = this.veryLargeNumber; // selecting exactly 500 is still possible
      }
      return scaledResult;
    },

    _inverseComputeSliderValue: function(scaledResult) {
      return Math.min(Math.round(Math.log(scaledResult - 4)/0.05), this.MAX_PRICE_SLIDER_VALUE);
    },

    setPriceFilterValue: function() {
      var self = this;

      // Kill all pricing values before setting the one that we are actually using.
      var pricingTimingOptions = ['max_hourly_price', 'max_daily_price', 'max_weekly_price'];
      $.each(pricingTimingOptions, function(index, value) {
        self.model.unset(value, {silent: true});
      });

      // If the user has moved the slider all the way to the right, they have essentially 'unset'
      // the price filter. In this case we don't need to do anything else and should keep all the
      // price attributes on the model unset.
      //
      // If they have moved the slider anywhere but the right edge then we need to update the model.
      var hourlyPrice = this.model.get('priceValue');
      if (hourlyPrice < this.MAX_PRICE_SLIDER_VALUE) {
        var dailyPrice  = hourlyPrice * config.globals.hourly_daily_ratio;
        var weeklyPrice = dailyPrice * config.globals.daily_weekly_ratio;
        this.model.set('max_hourly_price', hourlyPrice);
        this.model.set('max_daily_price', dailyPrice);
        this.model.set('max_weekly_price', weeklyPrice);
      }
    }

  });

  return FiltersView;
});

