define('public/tour',[
  'jquery',
  'common',
  'libs/bootstrap',
  'libs/froogaloop.patched'
], function(
  $,
  common,
  bootstrap,
  $f
) {

  common.pageReady( ["public-tour-*"], function() {

    // -------------
    // Vimeo Players
    // -------------
    $('.modal').on('hidden.bs.modal', pauseVideos);

    // Auto-play vimeo videos when opening them up in modal
    $('.video-modal-link').on( 'click', function(event){
      var videoId = $(this).data('target');
      player = $f($(videoId).find('.vimeo-player')[0]);
      player.api('play');
    });

    // Pauses all Vimeo videos on page.
    function pauseVideos(){
      var player;

      $('.vimeo-player').each( function(i, val){
        player = $f($(val)[0]);
        player.api('pause');
      });

    }

  });
});
