define('views/car/weekly_schedule',[
  'jquery',
  'backbone',
  'libs/fullcalendar.patched'
], function(
  $,
  Backbone,
  fullCalendar
) {

  return WeeklyScheduleView = Backbone.View.extend({
    initialize: function() {
      this.blockedList      = [];
      this.ajaxQueue        = [];
      this.carID            = this.options.carID;
      this.unavailableRanges = this.options.unavailableRanges;
      this.isTimeToQueue    = false;

      this.activate();
    },

    activate: function() {
      var self = this;
      this.buildBlockedList(this.unavailableRanges || []);

      var isMouseDown = false,
          $html = $('html'),
          isBlocked;

      var slotSelected = function( $slot ) {
        if ($slot.hasClass("blocked") != isBlocked) {
          $slot.addClass("dragging");
          $slot.toggleClass("blocked", isBlocked);
          // console.log(this.blockedList)
          self.blockedList[$slot.data("blockedListIndex")] = isBlocked;
        }
      };

      var onSlotMouseOver = function( event ) {
        if ( isMouseDown ) {
          slotSelected($(this));
        }
      };

      var onSlotMouseDown = function( event ){
        var $this = $(this);
        if (!$this.hasClass("loading-stripes")) {
          isMouseDown = true;
          $html.one('mouseup', onSlotMouseUp);
          isBlocked = ! $this.hasClass( "blocked" );
          slotSelected($this);
        }
        return false; // prevent text selection
      };

      var onSlotMouseUp = function( event ) {
        isMouseDown = false;
        self.saveWeeklySchedule();
        self.$el.find(".dragging").removeClass("dragging");
      };

      this.$el
        .on( 'mousedown', '.slot', onSlotMouseDown)
        .on( 'mouseover', '.slot', onSlotMouseOver);

    },

    buildBlockedList: function(unavailableRanges) {
      var $rows = this.$el.find(".slots tr");
      for (var i=0; i < 24*7; i++) {
        this.blockedList[i] = false;
      }

      for (var i=0; i < unavailableRanges.length; i++) {
        var blockedRange = unavailableRanges[i];
        for (var j=blockedRange.start; j < blockedRange.end; j++) {
          this.blockedList[j] = true;
        }
      }

      for (var i=0; i < $rows.length; i++) {
        var $row = $($rows[i]).find(".slot");
        for (var j=0; j < $row.length; j++) {
          var blockedListIndex = j*24+i;
          var $slot = $($row[j]);
          $slot.data("blockedListIndex", blockedListIndex);
          $slot.toggleClass("blocked", this.blockedList[blockedListIndex]);
        }
      }
    },

    saveWeeklySchedule: function(savingList) {
      var currentRange = null;
      var rangeList = [];
      for (var i=0; i < this.blockedList.length; i++) {
        if (this.blockedList[i] === true && currentRange === null) {
          currentRange = {"start":i};
        }
        else if (this.blockedList[i] === false && currentRange !== null) {
          currentRange.end = i;
          rangeList.push(currentRange.start);
          rangeList.push(currentRange.end);
          currentRange = null;
        }
      }
      if (currentRange !== null) {
        var lastIndex = this.blockedList.length - 1;
        if (this.blockedList[lastIndex] === true) {
          currentRange.end = lastIndex + 1;
          rangeList.push(currentRange.start);
          rangeList.push(currentRange.end);
        }
        else {
          console.log("Error something went wrong with saveWeeklySchedule.");
          return;
        }
      }
      this.saveToServer(rangeList);
    },
    
    saveToServer: function(rangeList) {
      var self = this;
      this.$el.find(".slot.dragging").addClass("loading-stripes");
      if (this.isTimeToQueue) {
        // Since we store the whole state we only save the latest one.
        // Can't really call it a queue then can you?
        this.ajaxQueue[0] = rangeList;
      }
      else {
        this.isTimeToQueue = true;
        $.ajax({
          url: "/ajax/weekly_availability/save",
          type: "POST",
          dataType: "json",
          data: {
            "carid"     : this.carID,
            "hours"     : rangeList.join(",")
          },
          success: function(data, textStatus, jqXHR) {
            if (data.result !== "success") {
              // Here we revert the state of the box.
              console.log("failure server returned " + data.message);
            }
          },
          error: function(jqXHR, textStatus, errorThrown) {
            console.log("HTTP call failed "+textStatus+" ERROR: "+errorThrown);
          }
        }).done(function() {
            self.isTimeToQueue = false;
            var nextRangeList = self.ajaxQueue.shift();
            if (nextRangeList) {
              self.saveToServer(nextRangeList);
            }
            else {
              self.$el.find(".loading-stripes").removeClass("loading-stripes");
            }
        });
      }
    }
  });

});

