define('activity',[
  'jquery',
  'models/user',
  'common',
  'webapp',
  'common/partials/dialogs/owner_decline',
  'views/shared/notification'
], function(
  $,
  User,
  common,
  webapp,
  ownerDeclineDialog,
  NotificationView
) {

  common.pageReady(["dashboard-activity"], function(){

    // Attaches UI events to notification. HTML is rendered in haml.
    this.notificationView = new NotificationView();

    ownerDeclineDialog.init();

    $(".rental-box .decline-button").click(function(event) {

      var $rentalBox           = $(this).closest(".rental-box"),
          bundleId            = $rentalBox.attr("rid"),
          renterFirstName      = $rentalBox.attr("renter_first_name"),
          renterPhoto          = $rentalBox.attr("renter_photo"),
          requestStartUnixTime = $rentalBox.attr("startunixtime"),
          requestEndUnixTime   = $rentalBox.attr("endunixtime");

      ownerDeclineDialog.show({
        'bundleId': bundleId,
        'requestStartUnixTime': requestStartUnixTime,
        'requestEndUnixTime': requestEndUnixTime,
        'renter': renterFirstName,
        'renterPhoto': renterPhoto
      });

    });

    $(".rental-box .accept-button").click(function() {
        webapp.approve($(this).closest(".rental-box").attr("rid"), $(this));
    });

  var userTimezone = (new User()).get("iana_timezone");

  // We only want to make the car-history ajax call if the user is an owner
  // The presence of the id in the view is sufficient indicator this is an owner
  if ($('#car-history-section').length) {
    common.loadMore("/ajax/car-history",
                    {"item_count": 20},
                    $('#car-history-section'),
                    '.history-load-more');
  }
  common.loadMore("/ajax/user-history",
                  {"item_count": 20, "user_timezone": userTimezone},
                  $('#user-history-section'),
                  '.history-load-more');
  });
});

