define('views/car/listing_toggle',[
  'jquery',
  'backbone',
  'libs/dragdealer'
], function(
  $,
  Backbone,
  Dragdealer
) {

  return ListingToggleView = Backbone.View.extend({

    initialize: function() {
      this.activate();
    },

    activate: function() {
      var nub_clicked, toggle_box;

      function toggle() {
        setTimeout(function() {
          var loc = toggle_box.value.current;
          toggle_box.setValue(1-loc[0], loc[1]);
        }, 0);
      }

      var value = ($('#switch').attr("value") == "true");

      toggle_box = new Dragdealer($('#switch')[0], {
        steps: 2,
        x: $('#switch').attr("value") == "true" ? 1 : 0,
        animationCallback: function(x, y) {
          if(x > 0.5) {
            if (!value) $(document).trigger('switch.value_changed',[false]);
            value = true;

            $("#listing-box").removeClass( 'active');

            $(".switch_mask")
              .removeClass("switch_mask_green")
              .addClass("switch_mask_red");
          } else {
            $("#listing-box").addClass( 'active');

            $(".switch_mask")
              .removeClass("switch_mask_red")
              .addClass("switch_mask_green");

            if (value) $(document).trigger('switch.value_changed',[true]);
            value = false;
          }
        }
      });

      $(document).bind("switch.value_changed", function(e, status) {
        $.post("/ajax/toggle_listing", { "visible": status });
      });

      $("#nub")
        .mousedown(function(evt) { nub_clicked = true; })
        .mousemove(function(evt) { nub_clicked = false; })
        .mouseup(function(evt) { if(nub_clicked === true) { toggle(); }})
        .on('touchstart', function(evt) { nub_clicked = true; })
        .on('touchmove', function(evt) { nub_clicked = false; })
        .on('touchend', function(evt) { if(nub_clicked === true) { toggle(); }});
    }

  });

});
