define('utils/template_helpers',[
  'jquery',
  'underscore',
  'config',
  'utils/formatters',
  'libs/handlebars',
  'libs/moment.min'
], function($, _, config, formatters, momentIsNotAModule) {

  Handlebars.registerHelper('capitalize', function(txt) {
    return txt.charAt(0).toUpperCase() + txt.slice(1);
  });

  Handlebars.registerHelper('formatCurrency', function(currency) {
    var digits = (currency % 1 === 0)? 0: 2;
    return Number(currency).toFixed(digits);
  });

  Handlebars.registerHelper('formatDate', function(date) {
    return moment(date).format("MMM Do h:mma");
  });

  Handlebars.registerHelper('formatDistance', function(distance) {
    // Because of an index server bug the distance attribute will be undefined on the car object if
    // the car's latitude and longitude are the same as the user's search latitude and longitude.
    // For this reason, we need an additional conditional statement till the bug is fixed.
    // Lighthouse Bug #1238 
    if (_.isUndefined(distance)) {
      return '0.0';
    } else {
      return formatters.formatNumber(distance, 1);
    }
  });

  // If value is less than 10, always show 2 decimal places.
  // This is used to display the hourly rate in the Car price slider component.
  Handlebars.registerHelper('formatHourlySliderRate', function(currency) {
    var digits = (currency >= 10)? 0: 2;
    return Number(currency).toFixed(digits);
  });

  Handlebars.registerHelper('pluralS', function(num) {
    return (num > 1)? 's': '';
  });

  Handlebars.registerHelper('select', function(value, options){
    var $el = $('<select />').html(options.fn(this));
    $el.find('[value=' + value + ']').attr({'selected':'selected'});
    return $el.html();
  });

  Handlebars.registerHelper('validateCarPhoto', function(photo) {
    return (typeof photo !== "undefined") ? photo : config.globals.default_car_image;
  });

});
