define('public/iphone',[
  'jquery',
  'common',
  'utils/loader'
], function(
  $,
  common,
  loader
){

  common.pageReady( "public-iphone-index", function() {

    $form = $("#iphone-sms-form");

    $form.validate({
      rules: {
        'phone-number': {
          required: true,
          phone: true
        }
      },
      submitHandler: function(form) {
        loader.setButtonSubmitting($form.find('.send-sms-button'));

        $.ajax({
          url: "/ajax/sendSMS",
          type: "POST",
          data: {
            "message-kind": "iphone",
            "phone-number": $form.find('.phone-number').val()
          },
          success: function(response) {
            loader.clearButtonSubmitting( $form.find('.send-sms-button') );
            $form.find('.sms-sent-message').hide().fadeIn('slow');
          },
          error: function(response) {
            console.log("Error: " + response);
          }
        });

      }
    });


    $(document.documentElement).keyup(function(event) {
      var phoneNumberHasFocus = $('.phone-number').is(':focus');
      if ( !phoneNumberHasFocus && (event.keyCode == 38 || event.keyCode == 40 || event.keyCode == 32)) {
        event.preventDefault();
        return false;
      }
      return true;
    });

    $(document.documentElement).keypress(function(event) {
      var phoneNumberHasFocus = $('.phone-number').is(':focus');
      if ( !phoneNumberHasFocus && (event.keyCode == 38 || event.keyCode == 40 || event.keyCode == 32)) {
        event.preventDefault();
        return false;
      }
      return true;
    });

  });
});
