define('models/viewmodels/search_ui_state',[
  'jquery',
  'backbone',
  'common',
  'config',
  'models/viewmodels/search_cookie'
], function(
  $,
  Backbone,
  common,
  config,
  SearchCookie
){

  var VIEWPORT_PADDING_PERCENTAGE = 0.2;
  var DEFAULT_SEARCH_DISTANCE_MI  = 1; // This is in miles. And it's only used when the map
                                       // is not present.

  var SearchUIState = Backbone.Model.extend({
    _lastSearchParams: null,
    _lastPaddedViewport: null,

    // Expected data:
    // - isMapVisible: In narrow resolutions we hide the map. (Not saved in cookie.)
    // - locationText: The text entered into the Google Places autocomplete location input.
    // - mapCenter
    // - mapZoom 
    // - pickup_date
    // - return_date
    // - selectedCar: Keeps track of what car the user clicked either from the list or the map. 
    //                This determine which map card to show. (Not saved in cookie.)
    //
    // - searchLat: The latitude used in the search query. This can come from a server setting, the
    //              cookie, url, or the location input.
    //
    // - searchLng: The longitude... see above ^ 
    //
    // - viewport: Generated from a Google maps viewport toUrlValue(). This is used to generate a
    //             padded viewport for the search query. This is not used to recreate the map state
    //             in the UI. We use mapCenter and mapZoom for that.
    //
    // Filters:
    // - bodyType
    // - instant
    // - max_hourly_price
    // - max_daily_price
    // - max_weekly_price
    // - priceValue (does not get saved to cookie)
    // - transmission
    
    defaults: {
      mapZoom: config.SEARCH_DEFAULT_ZOOM
    },
    
    initialize: function() {
      this._readCookie();
      this._readURL();
      this._initLatLng();
    },

    _readCookie: function() {
      this.searchCookie = new SearchCookie();
      this.set(this.searchCookie.toJSON());
    },

    _readURL: function() {
       this.urlSearchAddress = common.getURLParameterByName('location');
       if (this.urlSearchAddress) {
         this.set('locationText', this.urlSearchAddress);
         this.unset('searchLat');
         this.unset('searchLng');
         this.unset('mapCenter');
         this.unset('mapZoom');
       }
       
       // WARNING: Reading Pickup and Return dates from the URL is untested
       var pickupDate = common.getURLParameterByName('pickup_date');
       var returnDate = common.getURLParameterByName('return_date');
       if (pickupDate) {
         this.set("pickup_date", converters.ISODateToJSDate(pickupDate));
       }
       if (returnDate) {
         this.set("return_date", converters.ISODateToJSDate(returnDate));
       }
    },

    _initLatLng: function() {
      var self     = this;
      var geocoder = new google.maps.Geocoder();

      if (this.urlSearchAddress) {
        // If search_address param is set on URL, it overrides other values.
        geocoder.geocode(
          {'address': this.urlSearchAddress},
          function(results, status) {
            if (status == google.maps.GeocoderStatus.OK) {
              self.set({
                'searchLat': results[0].geometry.location.lat(),
                'searchLng': results[0].geometry.location.lng()
              });
            } else {
              console.log("ERROR GeocoderStatus is not OK, " + status);
            }
        });
      } else if (!this.has("searchLat") || !this.has("searchLng")) {
        if (config.globals.latitude && config.globals.longitude) {
          this.set({
            'searchLat': Number(config.globals.latitude),
            'searchLng': Number(config.globals.longitude)
          });
        }
        else if (config.globals.search_address) {
          // If we do not have any lat lon coordinates for the user we need to look them up
          // via the Google Geocoder.
          geocoder.geocode(
            {'address': config.globals.search_address},
            function(results, status) {
              if (status == google.maps.GeocoderStatus.OK) {
                self.set({
                  'searchLat': results[0].geometry.location.lat(),
                  'searchLng': results[0].geometry.location.lng()
                });
              } else {
                console.log("ERROR GeocoderStatus is not OK, " + status);
              }
          });
        }
      }
    },

    updateCookie: function() {
      this.searchCookie.updateCookieValues(this.toJSON());
    },

    getParams: function() {
      var params = {};
      params.product = 'web';
      params.uid = config.globals.uid;
      if (config.globals.is_become_user === true) {
        params.admin = config.globals.admin;
      }
      if (this.has('searchLat') && this.has('searchLng')) {
        params.user_lat = this.get('searchLat');
        params.user_lng = this.get('searchLng');
      }
      var mapViewport  = this.get('viewport');
      if (mapViewport) {
        // pad map viewport to make it a larger viewport
        var paddedViewport = this._getPaddedViewport(mapViewport);
        params.viewport = paddedViewport.toUrlValue();
      }
      else if (this.has('searchLat') && this.has('searchLng')) {
        params.lat = params.user_lat;
        params.lng = params.user_lng;
        params.distance = DEFAULT_SEARCH_DISTANCE_MI;
      }
      else {
        console.log("ERROR No user lat lng nor viewport. Can not do a valid search.");
      }
      if (this.has('pickup_date') && this.has('return_date')) {
        params.start_time = this.get('pickup_date').toISOString();
        params.end_time = this.get('return_date').toISOString();
      }

      var pricingTimingOptions = {"max_hourly_price":0, "max_daily_price":1, "max_weekly_price":2};

      // NOTE: this allows the use of multiple price filters on a
      // single query, though we don't intend to use it with multiple price filters
      for (var option in pricingTimingOptions) {
        if (this.has(option)) {
          params[option] = this.get(option);
        }
      }

      if (this.has('instant')) {
        params.instant = this.get('instant');
      }

      if (this.has('transmission')) {
        params.transmission = this.get('transmission');
      }

      if (this.has('bodyType')) {
        var bodyTypes = this.get('bodyType').split(',');
        params.body_style = (bodyTypes.indexOf('car') > -1) ? 'coupe,sedan,hatchback,' : '';
        params.body_style = params.body_style + ((bodyTypes.indexOf('convertible') > -1) ? 'convertible,' : '');
        params.body_style = params.body_style + ((bodyTypes.indexOf('suv') > -1) ? 'suv,' : '');
        params.body_style = params.body_style + ((bodyTypes.indexOf('van') > -1) ? 'van,wagon,' : '');
        params.body_style = params.body_style + ((bodyTypes.indexOf('truck') > -1) ? 'truck,' : '');
        params.body_style = params.body_style.slice(0, - 1); // Remove trailing comma
      }

      this._lastSearchParams   = params;
      this._lastPaddedViewport = paddedViewport;
      this._lastMapZoom        = this.get("mapZoom");

      return params;
    },

    hasMapBeenPannedOutOfBuffer: function() {
      if (!this._lastSearchParams) {
        return true;
      }

      if (this._lastPaddedViewport && this.get('viewport')) {
        var viewportSWCorner = this.get('viewport').getSouthWest();
        var viewportNECorner = this.get('viewport').getNorthEast();
        if (!(this._lastPaddedViewport.contains(viewportSWCorner) &&
              this._lastPaddedViewport.contains(viewportNECorner))) {
          return true;
        }
      }
      return false;
    },

    resetFilters: function() {
      this.unset('bodyType');
      this.unset('max_hourly_price');
      this.unset('max_daily_price');
      this.unset('max_weekly_price');
      this.unset('priceValue');
      this.unset('instant');
      this.unset('transmission');
    },

    areAnyFiltersSet: function() {
      if (this.has('bodyType') ||
          this.has('max_hourly_price') ||
          this.has('max_daily_price') ||
          this.has('max_weekly_price') ||
          this.has('instant') ||
          this.has('transmission')) {
        return true;
      } else {
        return false;
      }
    },

    _getPaddedViewport: function(viewport) {
      var mapLatLngSW = viewport.getSouthWest();
      var mapLatLngNE = viewport.getNorthEast();

      var extraPadding = this._getCartesianDistance(
          mapLatLngSW,
          mapLatLngNE
      ) * VIEWPORT_PADDING_PERCENTAGE;

      var newSouthwest = new google.maps.LatLng(
          mapLatLngSW.lat() - extraPadding,
          mapLatLngSW.lng() - extraPadding
      );

      var newNorthEast = new google.maps.LatLng(
          mapLatLngNE.lat() + extraPadding,
          mapLatLngNE.lng() + extraPadding
      );
      return new google.maps.LatLngBounds(newSouthwest, newNorthEast);
    },

    _getCartesianDistance: function(southwest, northeast) {
      return Math.sqrt(
          Math.pow(northeast.lat() - southwest.lat(), 2) +
          Math.pow(northeast.lng() - southwest.lng(), 2)
      );
    }
  });

  return SearchUIState;
});

