define('utils/polyfills',[
], function() {

  // console.log()
  // If browser doesn't have console support, create the object and empty methods to prevent errors.
  if (!window.console) {
    window.console         = {};
    window.console.error   = function() {};
    window.console.info    = function() {};
    window.console.log     = function() {};
    window.console.time    = function() {};
    window.console.timeEnd = function() {};
    window.console.warn    = function() {};
  }

  // Date.toISOString()
  // Using this shim for IE8.
  // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Date/toISOString
  if (!Date.prototype.toISOString) {
    (function() {

      function pad(number) {
        var r = String(number);
        if (r.length === 1) {
          r = '0' + r;
        }
        return r;
      }

      Date.prototype.toISOString = function() {
        return this.getUTCFullYear()
          + '-' + pad(this.getUTCMonth() + 1)
          + '-' + pad(this.getUTCDate())
          + 'T' + pad(this.getUTCHours())
          + ':' + pad(this.getUTCMinutes())
          + ':' + pad(this.getUTCSeconds())
          + '.' + String((this.getUTCMilliseconds()/1000).toFixed(3)).slice(2, 5)
          + 'Z';
      };

    }());
  }

  // window.btoa() and window.atob()
  // Polyfill for browsers which don't provide window.btoa and window.atob
  // https://github.com/davidchambers/Base64.js
  var
    object = typeof window != 'undefined' ? window : exports,
    chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=',
    INVALID_CHARACTER_ERR = (function () {
      // fabricate a suitable error object
      try { document.createElement('$'); }
      catch (error) { return error; }}());

  // encoder
  // [https://gist.github.com/999166] by [https://github.com/nignag]
  object.btoa || (
  object.btoa = function (input) {
    for (
      // initialize result and counter
      var block, charCode, idx = 0, map = chars, output = '';
      // if the next input index does not exist:
      //   change the mapping table to "="
      //   check if d has no fractional digits
      input.charAt(idx | 0) || (map = '=', idx % 1);
      // "8 - idx % 1 * 8" generates the sequence 2, 4, 6, 8
      output += map.charAt(63 & block >> 8 - idx % 1 * 8)
    ) {
      charCode = input.charCodeAt(idx += 3/4);
      if (charCode > 0xFF) throw INVALID_CHARACTER_ERR;
      block = block << 8 | charCode;
    }
    return output;
  });

  // decoder
  // [https://gist.github.com/1020396] by [https://github.com/atk]
  object.atob || (
  object.atob = function (input) {
    input = input.replace(/=+$/, '')
    if (input.length % 4 == 1) throw INVALID_CHARACTER_ERR;
    for (
      // initialize result and counters
      var bc = 0, bs, buffer, idx = 0, output = '';
      // get next character
      buffer = input.charAt(idx++);
      // character found in table? initialize bit storage and add its ascii value;
      ~buffer && (bs = bc % 4 ? bs * 64 + buffer : buffer,
        // and if not first of each 4 characters,
        // convert the first 8 bits to one ascii character
        bc++ % 4) ? output += String.fromCharCode(255 & bs >> (-2 * bc & 6)) : 0
    ) {
      // try to find character in table (0-63, not found => -1)
      buffer = chars.indexOf(buffer);
    }
    return output;
  });

});

