define('views/car/monthly_calendar_day_popup',[
  'jquery',
  'backbone',
  'models/viewmodels/pickup_and_return',
  'text!templates/car/monthly_calendar_day_popup.handlebars',
  'utils/converters',
  'utils/loader',
  'utils/time',
  'views/car/monthly_calendar_day_popup_event',
  'views/shared/pickup_and_return_inputs',
  'libs/handlebars',
  'libs/moment.min'
], function(
  $,
  Backbone,
  PickupAndReturn,
  dayPopupHandlebars,
  converters,
  loader,
  time,
  MonthlyCalendarDayPopupEventView,
  PickupAndReturnInputsView
){

  return MonthlyCalendarDayPopupView = Backbone.View.extend({
    template: Handlebars.compile(dayPopupHandlebars),

    initialize: function() {
      this.carID                = this.options.carID;
      this.day                  = this.options.day;
      this.selectedDayTableCell = this.options.selectedDayTableCell;
      this.pickupAndReturn      = new PickupAndReturn();

      this.activate();
    },

    activate: function() {
      var self = this;
      
      this.listenTo(this.collection, 'add remove change', this.renderEvents);
      
      this.listenTo(this.pickupAndReturn, 'change:isValid', function(model, isValid) {
        if (isValid) {
          this.$blockButton.removeAttr('disabled');
        } else {
          this.$blockButton.attr('disabled', 'disabled');
        }
      });
    },

    handleBlockButtonClick: function(event) {
      var self = this;

      if (this.pickupAndReturn.get('isValid')) {

        loader.setButtonSubmitting(this.$blockButton);
        this.$blockButton.html('<div class="spinner spinner-light-bg-small"></div>');

        var start    = this.pickupAndReturn.get('pickup_date');
        var end      = this.pickupAndReturn.get('return_date');
        var startISO = converters.JSDateToISODateTimezoneNaive(start);
        var endISO   = converters.JSDateToISODateTimezoneNaive(end);
        
        this.collection.block(this.carID, startISO, endISO, 'monthly-calendar')
          .done(function(data, status, xhr) {
            if (data.result === 'success') {
              self.hide();
            } else {
              loader.showErrorDialog('There was an error blocking off the time. Please reload the page to try again.');
            }
          })
          .fail(function(jqXHR, textStatus, errorThrown) {
            loader.showErrorDialog('There was an error blocking off the time. Please reload the page to try again.');
          })
          .always(function(xhr, status) {
            loader.clearButtonSubmitting(this.$blockButton);
            self.$blockButton.html('Block');
          });  
      }
    },

    show: function() {
      var start = new Date(this.day.getTime());
      var now   = new Date();
      if (time.areDatesOnSameDay(now, this.day)) {
        start = time.ceilJSDateToQuarterHour(new Date(now.getTime()));
      }

      var self     = this;
      var dayLabel = moment(this.day).format('dddd - MMM D');

      var selectedDayStartMS = this.day.getTime();
      var selectedDayEndMS   = selectedDayStartMS + (24 * 60 * 60 * 1000);
    
      this.$el = $(this.template({
        dayLabel: dayLabel
      }));

      // Append to dom so we can measure width and height
      this.$el.appendTo($('body'));
      this.$el          = $('body').find('.event-popup:last');
      this.$blockButton = this.$el.find('.block-button');

      this.renderEvents();

      this.pickupAndReturn.set('pickup_date', start);

      this.pickupAndReturnInputsView = new PickupAndReturnInputsView({
        el: this.$el.find('.date-and-time-range-inputs')[0],
        model: this.pickupAndReturn,
        startLabel: 'From',
        endLabel: 'To',
        isStartTimeInputSetOnInit: false
      });


      // Position popup
      var offsetLeft      = this.selectedDayTableCell.offset().left;
      var offsetTop       = this.selectedDayTableCell.offset().top;
      var width           = this.selectedDayTableCell.width();
      var height          = this.selectedDayTableCell.height();
      var centerPointLeft = offsetLeft + Math.floor(width/2);
      var centerPointTop  = offsetTop + Math.floor(height/2);
      var popupWidth      = this.$el.width();
      var popupHeight     = this.$el.height();
      var popupLeft       = centerPointLeft - Math.floor(popupWidth/2);
      var popupTop        = centerPointTop - Math.floor(popupHeight/2);
      var docScrollTop    = $(document).scrollTop();
      var windowWidth     = $(window).width();
      var windowHeight    = $(window).height();

      // Don't allow width to be greater than viewport
      popupWidth = (popupWidth > windowWidth - 40 )? windowWidth - 40: popupWidth;

      // Don't position so left is cut off
      popupLeft = (popupLeft < 20)? 20: popupLeft;
      // Don't position so right is cut off
      popupLeft = (popupLeft + popupWidth > windowWidth - 20)? windowWidth - popupWidth - 20: popupLeft;
      // Don't position so the bottom is cut off (unless content makes it taller than the viewport)
      popupTop = (popupTop + popupHeight > docScrollTop + windowHeight - 20)? docScrollTop + windowHeight - popupHeight - 20: popupTop;
      // Don't position so the top is cut off
      popupTop = (popupTop < docScrollTop + 20)? docScrollTop + 20: popupTop;

      this.$el.css({
        'left' : popupLeft + 'px',
        'top' :  popupTop + 'px',
        'width': popupWidth + 'px'
      });

      this.$el.addClass('open');

      // When the user closes the popup, we animate the calendar date div with a subtle 'pressed in'
      // animation.

      this.$el.find('.fc-content td').removeClass('press-in');

      // Bind UI events for the day popup
      this.$el.find('.popup-close-button').on('click', function(event){
        self.selectedDayTableCell.addClass('press-in');
        self.hide();
      });

      this.$blockButton.on('click', function(event){
        self.handleBlockButtonClick();
      });
    },

    hide: function() {
      var self = this;

      if (Modernizr.csstransitions){
        this.$el
          .removeClass('open')
          .bind('transitionend webkitTransitionEnd oTransitionEnd MSTransitionEnd', function(event){
            self.remove();
        });
      } else {
        $('.event-popup').remove();
      }
    },

    renderEvents: function() {
      var self = this;
      
      this.$el.find('.popup-content').empty();
      
      $.each(self.collection.getEventsByDay(this.day), function(i, calendarEvent) {
        var eventView = new MonthlyCalendarDayPopupEventView({
          carID: self.carID,
          day: self.day,
          model: calendarEvent,
          collection: self.collection
        });
        self.$el.find('.popup-content').append(eventView.el);
      });
    },

    remove: function() {
      // Custom cleanup code
      this.pickupAndReturnInputsView.remove();

      // Call the base class remove method 
      Backbone.View.prototype.remove.apply(this, arguments);
    }
  });

});

