define('trips/renter_made_rental',[
  "jquery",
  "common",
  "common/partials/messaging",
  "common/partials/rate_user"
], function($, common, messaging, rateUser ) {

  common.pageReady("trips-renter_made_rental", function(){

    messaging.init('.messaging');

    if(GA_ARGS.rental_complete && !GA_ARGS.renter_gave_feedback && GA_ARGS.rental_status === "active" ){
      rateUser.init('.rate-user');
    }

  });
});
