define('views/shared/notification',[
  'jquery',
  'backbone'
], function(
  $,
  Backbone
) {

  return NotificationView = Backbone.View.extend({
    events: {
      'click .close-btn': 'handleCloseButtonClick'
    },
    
    initialize: function() {
      // HTML rendered in haml.

      this.activate();
    },
    
    activate: function() {
      var self = this;
      $('.notification-box').on('click', '.close-btn', function(event) {
        self.handleCloseButtonClick();
      });
    },
    
    handleCloseButtonClick: function() {
      var notificationText = escape($('.notification-box .message').text());

      this.close();
      this.clearNotification(notificationText);
    },
    
    close: function(){
      $('.notification-box').slideUp();
    },
    
    clearNotification: function(notificationText) {
      $.ajax({
        type: 'POST',
        url: '/ajax/clear-notification',
        data: 'notification=' + notificationText,
        success: function(data, textStatus, request) {
          if (data === "FAIL") {
            console.log('FAILED /ajax/clear-notification');
          }
        }
      });
    }
  });

});
